import { autobind } from "core-decorators";
import * as React from "react";
import * as ReactDOM from "react-dom";
import { connect } from "react-redux";
import { Link, RouteComponentProps } from "react-router-dom";
import { Grid, Row, Col, Button, Alert } from "react-bootstrap";
import { IApplicationState } from "../stores";
import DeliveryPickupOptions from "../components/DeliveryOptions/DeliveryPickupOptions";
import KzLoader from "../components/Loader/KzLoader";
import { UserDeliveryDetailFormDataViewModel } from "../models/DeliveryOptions/UserDeliveryDetailFormDataViewModel";
import { DeliveryOptionsFormDataViewModel, DeliveryDetail } from "../models/DeliveryOptions/DeliveryDetailFormDataViewModel";
import Payment from "../components/DeliveryOptions/Payment";
import PickupDetails from "../components/DeliveryOptions/PickupDetails";
import { UpdateTransactionResponseViewModel } from "../models/UpdateTransactionResponseViewModel";
import * as Checkoutstore from "../stores/Checkout";
import * as numeral from "numeral";
import Loader from 'react-loader-advanced';
import "../scss/_custom.scss";
import "../scss/_payment.scss";
import { Helmet } from "react-helmet";
import Metatag from "../components/Metatags/Metatag";

type DeliveryOptionsComponentProps = Checkoutstore.ICheckoutLoginState & typeof Checkoutstore.actionCreators & RouteComponentProps<{}>;

export class DeliveryOptions extends React.Component<DeliveryOptionsComponentProps, Checkoutstore.ICheckoutLoginState> {
    public componentWillMount() {
        this.setState({ currentPage: "deliveryOptions" });
    }

    public componentDidMount() {
        if (window) {
            window.scrollTo(0, 0)
        }

        this.setState({ currentPage: "deliveryOptions" });
        this.props.requestCountryData();
    }

    public switchPage(page, e) {
        this.setState({
            currentPage: page,
        });
    }

    public render() {
        if (this.props.fetchCountriesSuccess) {
            return (<div> <Metatag url={this.props.location.pathname} title="Delivery Options" />
                <div className="container checkout-pages pt-3 pb-5">
                    <div className="card checkout-card">
                        <h5> Delivery &amp; Pickup Options
                                <small className="float-right text-muted"><i className="fa fa-lock text-success"></i> Secure</small>
                        </h5>
                        {(this.state.currentPage == "deliveryOptions" || this.state.currentPage == "pickupDetails" || this.state.currentPage == "payment") && <DeliveryPickupOptions switchPage={this.switchPage.bind(this)} showButtons={this.state.currentPage == "deliveryOptions"} />}
                        <hr />
                        <h5>Pickup Details</h5>
                        {(this.state.currentPage == "pickupDetails" || this.state.currentPage == "payment") && <PickupDetails onSubmit={this.onSubmitPickupDetails} countryData={this.props.countryList} />}
                        <h5>Payment</h5>
                        <div className="card-footer gradient-bg">
                            <small>By clicking continue, you accept and agree to all terms of feel india policy. A receipt will be provided to you electronically that contains these terms and an itemized list of your purchase including taxes and shipping charges, if any.</small>
                        </div>
                    </div>
                </div>
            </div>
            );
        } else {
            return <div><KzLoader /></div>;
        }
    }

    @autobind
    private onSubmitPickupDetails(values: UserDeliveryDetailFormDataViewModel) {
        if (localStorage.getItem('cartItems') != null && localStorage.getItem('cartItems') != '0') {
            var data = JSON.parse(localStorage.getItem("cartItems"));
            var detailData = [];
            data.forEach(function (val) {
                let eventDetail: DeliveryDetail = {
                    eventTicketAttributeId: val.eventTicketAttributeId,
                    deliveryTypeId: 2,
                    firstName: values.firstName,
                    lastName: values.lastName,
                    email: values.email,
                    phoneCode: values.phoneCode,
                    phoneNumber: values.phoneNumber,
                };
                detailData.push(eventDetail);
            });
            var pickupDetail: DeliveryOptionsFormDataViewModel = {
                transactionId: +(localStorage.getItem("transactionId")),
                deliveryDetail: detailData
            }
            this.props.saveDeliveryOptions(pickupDetail, (response: UpdateTransactionResponseViewModel) => {
                if (response.success) {
                    //this.setState({ currentPage: "payment" });
                    this.props.history.push("/payment");
                }
            });
        } else {
            this.props.history.replace("/");
        }
    }
}

export default connect(
    (state: IApplicationState) => state.DeliveryOptions,
    Checkoutstore.actionCreators
)(DeliveryOptions);



