import { Form, Formik } from "formik";
import * as React from "react";
import { Button } from "react-bootstrap";
import { Link, RouteComponentProps } from "react-router-dom";

interface IPickupDetailFormProps {
    onSubmit: (values: any) => void;
    initialValues: any;
    validationSchema: any; // TODO: figure out where Yup SCHEMA went
}

export default class PickupDetailForm extends React.Component<IPickupDetailFormProps, any> {
    public render() {
        return <Formik
            onSubmit={this.props.onSubmit}
            initialValues={this.props.initialValues || {}}
            validationSchema={this.props.validationSchema}
        >
            {(props) => (<Form>
                {this.props.children}
                <div className="gradient-bg border p-2 row">
                    <div className="text-right w-100">
                        <Link to="/itinerary" className="btn btn-primary float-left text-uppercase">Back to Bag</Link>
                        <Button type="submit" className="btn site-primery-btn text-uppercase">Continue</Button>
                    </div>
                </div>
            </Form>)}
        </Formik>;
    }
}



