import { Field } from "formik";
import * as React from "react";
import AccountForm from "./AccountForm";
import { ChangePasswordFormDataViewModel } from "../../../models/Account/ChangePasswordFormDataViewModel";
import Yup from "yup";

interface IChangePasswordFormProps {
    onSubmit: (values: ChangePasswordFormDataViewModel) => void;
    onCancel: () => void;
}

export class ChangePasswordForm extends React.Component<IChangePasswordFormProps, {}> {
    public render() {
        const schema = this.getSchema();
        return (
            <AccountForm {...this.props} validationSchema={schema} initialValues={{}}>
                <div className="col-auto">
                    <Field type="password" name="oldPassword" placeholder="Old Password" className="form-control mb-2" required />
                </div>
                <div className="col-auto">
                    <div className="input-group mb-2">
                        <Field type="password" ref="password" name="newPassword" placeholder="New Password" className="form-control m-b-10" required />
                    </div>
                </div>
                <div className="col-auto">
                    <div className="input-group mb-2">
                        <div className="myaccount-error">
                            <Field type="password" name="confirmPassword" placeholder="Confirm New Password" className="form-control m-b-10" required />
                        </div>
                    </div>
                </div>
            </AccountForm>
        );
    }

    private getSchema() {
        Yup.addMethod(Yup.mixed, 'sameAs', function (ref, message) {
            return this.test('sameAs', message, function (value) {
                let other = this.resolve(ref);
                return !other || !value || value === other;
            })
        });

        return Yup.object().shape({
            oldPassword: Yup.string().required(),
            newPassword: Yup.string().required(),
            confirmPassword: Yup.string().sameAs(Yup.ref('password'), "Password mismatch")
                .required("Password mismatch"),
        });
    }
}



