import * as React from "react";
import { Link, RouteComponentProps } from "react-router-dom";

export default class EventImage extends React.Component<any, any> {

    public getCurrentItem(that, item) {
        localStorage.setItem("currentPlace", JSON.stringify(that))
    }

    public render() {
        return <div className="carousel-inner">
            <div className="carousel-item active">
                <Link to={"/place/" + this.props.altId.toUpperCase()}><img onClick={this.getCurrentItem.bind(this, this.props.currentEvent)} className="card-img-top" src={"https://static1.feelaplace.com/images/event/HotTickets/" + this.props.altId + "-ht-c1.jpg"} alt="Card image cap" />
                </Link>
            </div>
            <div className="carousel-item">
                <Link to={"/place/" + this.props.altId.toUpperCase()}><img onClick={this.getCurrentItem.bind(this, this.props.currentEvent)} className="card-img-top" src={"https://static1.feelaplace.com/images/event/HotTickets/" + this.props.altId + "-ht-c2.jpg"} alt="Card image cap" />
                </Link>
            </div>
            <div className="carousel-item">
                <Link to={"/place/" + this.props.altId.toUpperCase()}><img onClick={this.getCurrentItem.bind(this, this.props.currentEvent)} className="card-img-top" src={"https://static1.feelaplace.com/images/event/HotTickets/" + this.props.altId + "-ht-c3.jpg"} alt="Card image cap" />
                </Link>
            </div>
        </div>;
    }
}



