import * as React from "react";
import { Link, RouteComponentProps } from "react-router-dom";
import KzLoader from "../Loader/KzLoader";
import * as numeral from "numeral";

export class UserOrders extends React.Component<any, any> {
    public componentDidMount() {
        if (localStorage.getItem('userToken') != null && localStorage.getItem('userToken') != '0') {
            this.props.userOrders(localStorage.getItem('userToken'));
        }
    }

    public orderConfirmationView(e, id) {
        var transactionId = { "transactionId": e };
        localStorage.setItem('transactionState', JSON.stringify(transactionId));
    }

   public render() {
        if (this.props.fetchSuccess) {
            var paymentMode = this.props.orderList.transactionPaymentDetail;
            var userOrders = this.props.orderList;
            var that = this;
            var i = -1;
            var cardList = this.props.orderList.transaction.map(function(val) {
                i = i + 1;
                var modeOfPayment = paymentMode.filter(function(payment) {
                    return val.id == payment.transactionId;
                });
                return <div className="card">
                    <div className="card-body">
                        <div className="user-address">
                            <ul className="list-group">
                                <li className="list-group-item list-group-item-secondary">
                                    <div className="float-left">
                                        <Link to={"/order-confirmation"} className="btn btn-primary pull-right text-white">
                                            <span onClick={that.orderConfirmationView.bind(this, val.id)}>View Details</span>
                                        </Link>
                                        <span className="font-weight-bold">Transaction Id-{val.id}</span> <br />
                                        Your Date and Time of Booking: {new Date(val.createdUtc).toDateString().split(' ').join(' ')} {numeral(new Date(val.createdUtc).getHours()).format('00') + ":" + numeral(new Date(val.createdUtc).getMinutes()).format('00')}
                                        <br />Payment Mode - {modeOfPayment[0].paymentOptionId}
                                    </div>
                                    <div className="float-right">
                                        <span className="h5 mr-5">Total Amount- ${val.netTicketAmount}</span>                                        
                                    </div>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>;

            });
            return <div className="col-sm-9 right-act-pnl">
                <h4 className="mb-3 mt-3">My Orders</h4>
                {cardList}
            </div>;
        } else {
            return <div><KzLoader /></div>;
        }
    }
}


