import { autobind } from "core-decorators";
import * as React from "react";
import { connect } from "react-redux";
import { Link, RouteComponentProps } from "react-router-dom";
//import * as FacebookLogin from "react-facebook-login";
import FacebookProvider, { Login } from 'react-facebook';
import { FacebookSignInResponseViewModel } from "../../../models/SocialSignIn/FacebookSignInResponseViewModel";
import FacebookSignInFormDataViewModel from "../../../models/SocialSignIn/FacebookSignInFormDataViewModel";
import { IApplicationState } from "../../../stores";
import * as LoginFacebookStore from "../../../stores/LoginFacebook";
//383625982044825 - Local
//315761145630069 - Production
type LoginProps = LoginFacebookStore.ILoginState & typeof LoginFacebookStore.actionCreators & RouteComponentProps<{}>

class SignInFacebook extends React.Component<any, any> {
    render() {
        return (
            <div>
                <FacebookProvider appId="315761145630069">
                    <Login
                        scope="email,public_profile"
                        onResponse={this.responseFacebook}
                    >
                        <button type="button" className="btn btn-primary btn-block fb-btn"><i className="fa fa-facebook-f mr-2"></i>Sign in with Facebook</button>
                    </Login>
                </FacebookProvider>
            </div>
        );
    }

    @autobind
    responseFacebook(responseJson) {
        var userData = new FacebookSignInFormDataViewModel();
        userData.email = responseJson.profile.email;
        userData.firstName = responseJson.profile.first_name;
        userData.lastName = responseJson.profile.last_name;
        userData.SocialLoginId = responseJson.profile.id;
        this.props.facebookLogin(userData, (response: FacebookSignInResponseViewModel) => {
            if (response.success) {
                localStorage.setItem('userToken', response.session.user.altId);
                (window as any).location.replace("/");
            }
        });
    }
}

export default connect(
    (state: IApplicationState) => state.LoginFacebook,
    LoginFacebookStore.actionCreators
)(SignInFacebook);



