import * as React from "react";
import { Link, RouteComponentProps } from "react-router-dom";
import EventImage from "./EventImage";
import EventName from "./EventName";
import EventAddress from "./EventAddress";
import EventPrice from "./EventPrice";
import EventRatings from "./EventRatings";
import BespokeWishListViewModel from "../../models/BespokeWishList/BespokeWishListViewModel";
import * as PubSub from 'pubsub-js';
import {
    Grid, Row, ListGroup,
    ListGroupItem, Collapse, Glyphicon,
    Button, InputGroup, FormControl
} from "react-bootstrap";

export default class MonumentsTickets extends React.Component<any, any> {
    public componentWillMount() {
        this.setState({ loaded: false });
    }

    public checkVenue(item) {
        var venues = item.venue.map((item) => item.name)
        if (venues.length === 1) {
            return <p className="card-text m-0 cart-address"><small><Link className="text-dark" to={"https://www.google.com/maps/search/" + venues[0]} target="_blank">{item.city[0].name + ", " + item.country[0].name}</Link></small></p>;
        } else {
            var uniqueVenues = venues.filter(function (value, index, self) {
                return self.indexOf(value) === index;
            });
            if (uniqueVenues.length == 1) {
                return <p className="card-text m-0"><small><Link to={"https://www.google.com/maps/search/" + uniqueVenues[0]} target="_blank">{item.city[0].name + ", " + item.country[0].name}</Link></small></p>;
            } else {
                var cities = item.city.map((item) => item.name)
                return <p className="card-text m-0"><small><Link to={"https://www.google.com/maps/search/" + item[0]} target="_blank">{cities.toString()}</Link></small></p>;
            }
        }
    }

    public componentDidMount() {
        var that = this;
        var itemsData = this.props.categoryData;
        itemsData.map(function (val) {
            that.setState({
                [val.event.altId]: false
            })
        });
        var bespokeData = (localStorage.getItem('bespokeItems') != null && localStorage.getItem('bespokeItems') != '0') ? JSON.parse(localStorage.getItem("bespokeItems")) : [];
        if (bespokeData.length > 0) {
            bespokeData = bespokeData.filter(function (val) { return val.userAltId == localStorage.getItem('userToken') });
        }
        if (bespokeData.length > 0) {
            bespokeData.map(function (val) {
                if (that.props.isAuthenticated) {
                    that.setState({
                        [val.cartItems.categoryEvent.altId]: true
                    })
                } else {
                    that.setState({
                        [val.cartItems.categoryEvent.altId]: false
                    })
                }

            });
        }
        that.setState({ loaded: true });
        PubSub.subscribe('UPDATE_WISHLIST_DATA_EVENT', this.subscriberData.bind(this));   
    }


    public subscriberData(msg, data) {
        this.componentDidMount();
    }

    public getMinPrice(item) {
        var prices = item.map((val) => val.price)
        if (prices.length == 1) {
            return prices[0];
        } else {
            return Math.min(...prices);
        }
    }

    public getMaxPrice(item) {
        var prices = item.map((val) => val.price)
        if (prices.length == 1) {
            return prices[0];
        } else {
            return Math.max(...prices);
        }
    }

    public addToBespokeItinerary(item, e) {
        if (!this.props.isAuthenticated) {
            this.props.push();
        } else {
            
            var that = this;
            var bespokeData = (localStorage.getItem('bespokeItems') != null && localStorage.getItem('bespokeItems') != '0') ? JSON.parse(localStorage.getItem("bespokeItems")) : [];

            if (bespokeData.length > 0) {
                bespokeData = bespokeData.filter(function (val) { return val.userAltId == localStorage.getItem('userToken') });
            }
            var isPlaceExists = bespokeData.filter(function (val) { return val.cartItems.categoryEvent.altId == item.categoryEvent.altId });

            if (isPlaceExists.length == 0) {
                var userCheckoutData: BespokeWishListViewModel = {
                    cartItems: item,
                    userAltId: localStorage.getItem('userToken')
                }
                bespokeData.push(userCheckoutData);
                var allWishlistexceptCurrent;
                if (localStorage.getItem("bespokeItems") !== null && localStorage.getItem('bespokeItems') != '0') {
                    allWishlistexceptCurrent = JSON.parse(localStorage.getItem("bespokeItems")).filter(function (val) { return val.userAltId !== localStorage.getItem('userToken') });
                    allWishlistexceptCurrent.forEach(function (val) {
                        bespokeData.push(val);
                    });
                }
                localStorage.setItem('bespokeItems', JSON.stringify(bespokeData));
                that.setState({
                    [item.categoryEvent.altId]: true
                })
            } else {
                var i = -1;
                if (bespokeData.length == 1) {
                    var allWishlistexceptCurrent;
                    if (localStorage.getItem("bespokeItems") !== null && localStorage.getItem('bespokeItems') != '0') {
                        allWishlistexceptCurrent = JSON.parse(localStorage.getItem("bespokeItems")).filter(function (val) { return val.userAltId !== localStorage.getItem('userToken') });
                    }
                    var data = []
                    if (allWishlistexceptCurrent.length > 0) {
                        allWishlistexceptCurrent.forEach(function (val) {
                            data.push(val);
                        });
                        localStorage.setItem('bespokeItems', JSON.stringify(data));
                    } else {
                        localStorage.removeItem('bespokeItems');
                    }
                    that.setState({
                        [item.categoryEvent.altId]: false
                    });
                } else {
                    var bespokeItems = bespokeData.forEach(function (val) {
                        i = i + 1;
                        if (val.cartItems.categoryEvent.altId == item.categoryEvent.altId) {
                            bespokeData.splice(i, 1);
                        }
                    });
                    if (localStorage.getItem("bespokeItems") !== null && localStorage.getItem('bespokeItems') != '0') {
                        allWishlistexceptCurrent = JSON.parse(localStorage.getItem("bespokeItems")).filter(function (val) { return val.userAltId !== localStorage.getItem('userToken') });
                        allWishlistexceptCurrent.forEach(function (val) {
                            bespokeData.push(val);
                        });
                    }
                    localStorage.setItem('bespokeItems', JSON.stringify(bespokeData));
                    that.setState({
                        [item.categoryEvent.altId]: false
                    });
                }
            }
            PubSub.publish('UPDATE_NAV_WISHLIST_DATA_EVENT', 1);
        }
    }
    public getCurrentItem(that, item) {
        localStorage.setItem("currentPlace", JSON.stringify(that))
    }

    public render() {
        var catData;
        var categoryPlaceData = this.props.categoryData;
        var that = this;
        if (this.props.display) {
            catData = this.props.categoryData.slice(0, 12);
        } else {
            catData = this.props.categoryData
        }

        var filteredCategoriesData = catData;
        if (this.props.cityFilterKeyword == "" && this.props.stateFilterKeyword == "" && this.props.countryFilterKeyword == "") {
            filteredCategoriesData = catData;
        } else if (this.props.cityFilterKeyword == "" && this.props.stateFilterKeyword != "" && this.props.countryFilterKeyword == "") {
            filteredCategoriesData = categoryPlaceData.filter(function (val) {
                return val.state[0].name == that.props.stateFilterKeyword;
            });
        } else if (this.props.cityFilterKeyword != "" && this.props.stateFilterKeyword == "" && this.props.countryFilterKeyword == "") {
            filteredCategoriesData = categoryPlaceData.filter(function (val) {
                return val.city[0].name == that.props.cityFilterKeyword;
            });
        } else if (this.props.cityFilterKeyword == "" && this.props.stateFilterKeyword == "" && this.props.countryFilterKeyword != "") {
            filteredCategoriesData = categoryPlaceData.filter(function (val) {
                return val.country[0].name == that.props.countryFilterKeyword;
            });
        }
        const categoryEvents = filteredCategoriesData.map((item) => {
            return <div className="card">
                <div className="tinerary-box" onClick={this.addToBespokeItinerary.bind(this, item)} data-toggle="tooltip" data-placement="top" title="Add to your feelList">
                    {that.state[item.event.altId] ? <img src="https://s3-us-west-2.amazonaws.com/feel-cdn/images/icons/feelList-hear-filled.png" alt="Add to your feelList" /> : <img src="https://s3-us-west-2.amazonaws.com/feel-cdn/images/icons/feelList-hear.png" alt="Add to your feelList" />}
                </div>
                <div id={"feelMonuments" + item.event.altId.toUpperCase()} className="carousel slide" data-ride="carousel">
                    <ol className="carousel-indicators">
                        <li data-target={"#feelMonuments" + item.event.altId.toUpperCase()} data-slide-to="0" className="active"></li>
                        <li data-target={"#feelMonuments" + item.event.altId.toUpperCase()} data-slide-to="1"></li>
                        <li data-target={"#feelMonuments" + item.event.altId.toUpperCase()} data-slide-to="2"></li>
                    </ol>
                    <EventImage currentEvent={item} altId={item.event.altId.toUpperCase()} img={"https://static1.feelaplace.com/images/event/HotTickets/" + item.event.altId.toUpperCase() + "-ht.jpg"} />
                    <a className="carousel-control-prev" href={"#feelMonuments" + item.event.altId.toUpperCase()} role="button" data-slide="prev">
                        <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                        <span className="sr-only">Previous</span>
                    </a>
                    <a className="carousel-control-next" href={"#feelMonuments" + item.event.altId.toUpperCase()} role="button" data-slide="next">
                        <span className="carousel-control-next-icon" aria-hidden="true"></span>
                        <span className="sr-only">Next</span>
                    </a>
                </div>
                <div className="card-body p-2">
                    <EventName Name={item.event.name} altId={item.event.altId} />
                    {this.checkVenue(item)}
                    <EventPrice currency={item.currencyType && item.currencyType.code} minPrice={this.getMinPrice(item.eventTicketAttribute)} maxPrice={this.getMaxPrice(item.eventTicketAttribute)} />
                    <EventRatings rating={item.rating} eventAltId={item.event.altId} />
                </div>
                <div className="card-footer text-center text-uppercase p-1">
                    <Link to={"/place/" + item.event.altId.toUpperCase()} ><span className="text-white btn-block" onClick={that.getCurrentItem.bind(that, item)} >Book this Feel+</span></Link>
                </div>
            </div>
        });
        if (this.state.loaded) {
            return <div className="card-columns">
                {categoryEvents}
            </div>;
        } else {
            return <div className="card-columns">
            </div>;
        }
    }
}



