import { Form, Formik } from "formik";
import * as React from "react";
import { Button } from "react-bootstrap";

interface IGuestCheckoutFormProps {
    onSubmit: (values: any) => void;
    initialValues: any;
    validationSchema: any; // TODO: figure out where Yup SCHEMA went
}

export default class GuestCheckout extends React.Component<IGuestCheckoutFormProps, any> {
    public render() {
        return <Formik
            onSubmit={this.props.onSubmit}
            initialValues={this.props.initialValues || {}}
            validationSchema={this.props.validationSchema}
        >
            {(props) => (<Form>
                {this.props.children}
                <Button type="submit" className="btn site-primery-btn">Continue As Guest</Button>
            </Form>)}
        </Formik>
    }
}



