import * as React from "React";
import { ReactHTMLConverter } from "react-html-converter/node";

export default class InDepth extends React.Component<any, any> {
    public render() {
        const converter = ReactHTMLConverter();
        var timeline = this.props.timelineData.map(function (val) {
            return <img src={val.image} className="img-fluid" alt="" />
        });
        var architectureSlider = this.props.architectureData.map(function (val, index) {
            return <li data-target="#Architecturaldetail" data-slide-to="0" className={index == 0 ? "active" : ""}></li>;
        })
        var architectureDataDetail = this.props.architectureData.map(function (val, index) {
            return <div className={index == 0 ? "carousel-item active" : "carousel-item"}>
                <div className="row">
                    <div className="popup-text col-md-6">
                        {converter.convert(val.description)}
                    </div>
                    <div className="col-md-6">
                        <img className="card-img-top" src={val.image} alt="Card image cap" />
                    </div>
                </div>
            </div>;
        });
        var highlightsSlider = this.props.highlightsData.map(function (val, index) {
            return <li data-target="#carouselHighlightNuggets" data-slide-to="0" className={index == 0 ? "active" : ""}></li>;
        })
        var highlightsDataDetail = this.props.highlightsData.map(function (val, index) {
            return <div className={index == 0 ? "carousel-item active" : "carousel-item"}>
                <div className="row">
                    <div className="popup-text col-md-6">
                        {converter.convert(val.description)}
                    </div>
                    <div className="col-md-6">
                        <img className="card-img-top" src={val.image} alt="Card image cap" />
                    </div>
                </div>
            </div>;
        });
        var placeMapPlan = this.props.mapPlanData.map(function (val) {
            return <img className="card-img-top img-fluid" src={val.image} alt="" />
        });
        return <div className="col-sm-8 in-depth-list">
            <div>
                <h5 className="mb-4"><img src="https://static1.feelaplace.com/images/footer/feel-heart-logo.png" className="mr-2" /> In-depth</h5>
                {this.props.historyData.length > 0 && <p className="float-left w-50"><img src={'https://static1.feelaplace.com/images/event/AboutEvent/icons/history-icon.png'} className="mr-2" /> <a href="#" data-toggle="modal" data-target="#popup-history">History</a> </p>}
                {this.props.highlightsData.length > 0 && <p className="float-left w-50"><img src={'https://static1.feelaplace.com/images/event/AboutEvent/icons/highlights-icon.png'} className="mr-2" /> <a href="#" data-toggle="modal" data-target="#HighlightsNuggets">Highlights/Nuggets</a> </p>}
                {this.props.mapPlanData.length > 0 && <p className="float-left w-50"><img src={'https://static1.feelaplace.com/images/event/AboutEvent/icons/map-icon.png'} className="mr-2" /> <a href="#" data-toggle="modal" data-target="#popup-mapplan">Place map/plan</a> </p>}
                {this.props.timelineData.length > 0 && <p className="float-left w-50"><img src={'https://static1.feelaplace.com/images/event/AboutEvent/icons/timeline-icon.png'} className="mr-2" /> <a href="#" data-toggle="modal" data-target="#popup-timeline">Timeline</a> </p>}
                {this.props.immersiveData.length > 0 && <p className="float-left w-50"><img src={'https://static1.feelaplace.com/images/event/AboutEvent/icons/compass-icon.png'} className="mr-2" /> <a href="javascript:void(0)">Immersive experience</a> </p>}
                {this.props.architectureData.length > 0 && <p className="float-left w-50"><img src={'https://static1.feelaplace.com/images/event/AboutEvent/icons/architectural-icon.png'} className="mr-2" /> <a href="#" data-toggle="modal" data-target="#popup-Architecturaldetail">Architectural detail</a> </p>}
                
            </div>
            <div className="modal fade bd-example-modal-lg popup-history" id="popup-history" role="dialog" aria-labelledby="popup-history" aria-hidden="true">
                <div className="modal-dialog modal-lg modal-dialog-centered">
                    <div className="modal-content">
                        <div className="modal-header">
                            <h5 className="modal-title" id="exampleModalCenterTitle"><img src="https://static1.feelaplace.com/images/footer/feel-heart-logo.png" width="20" className="mr-2" /> History</h5>
                            <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div className="modal-body">
                            {converter.convert(this.props.historyData[0].description)}                            
                        </div>
                    </div>
                </div>
            </div>
            <div className="modal fade bd-example-modal-lg popup-timeline p-0" id="popup-timeline" role="dialog" aria-labelledby="popup-timeline" aria-hidden="true">
                <div className="modal-dialog modal-dialog-full modal-dialog-centered">
                    <button type="button" className="close text-white position-absolute" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true" className="h1">&times;</span>
                    </button>
                    {timeline}
                </div>
            </div>
            <div className="modal fade bd-example-modal-lg popup-with-slide" id="popup-Architecturaldetail" role="dialog" aria-labelledby="popup-Architecturaldetail" aria-hidden="true">
                <div className="modal-dialog modal-lg modal-dialog-centered">
                    <div className="modal-content ">
                        <div className="modal-header">
                            <h5 className="modal-title" id="exampleModalCenterTitle"><img src="https://static1.feelaplace.com/images/footer/feel-heart-logo.png" width="20" className="mr-2" /> Architectural detail</h5>
                            <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div className="modal-body">
                            <div className="custome-slider">
                                <div id="Architecturaldetail" className="carousel slide" data-ride="carousel">
                                    <ol className="carousel-indicators">
                                        {architectureSlider}
                                    </ol>
                                    <div className="carousel-inner">
                                        {architectureDataDetail}
                                    </div>
                                    <a className="carousel-control-prev" href="#Architecturaldetail" role="button" data-slide="prev">
                                        <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                                        <span className="sr-only">Previous</span>
                                    </a>
                                    <a className="carousel-control-next" href="#Architecturaldetail" role="button" data-slide="next">
                                        <span className="carousel-control-next-icon" aria-hidden="true"></span>
                                        <span className="sr-only">Next</span>
                                    </a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="modal fade bd-example-modal-lg popup-with-slide" id="HighlightsNuggets" role="dialog" aria-labelledby="HighlightsNuggets" aria-hidden="true">
                <div className="modal-dialog modal-lg modal-dialog-centered">
                    <div className="modal-content ">
                        <div className="modal-header">
                            <h5 className="modal-title" id="exampleModalCenterTitle"><img src="https://static1.feelaplace.com/images/footer/feel-heart-logo.png" width="20" className="mr-2" /> Highlights/Nuggets</h5>
                            <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div className="modal-body">
                            <div className="custome-slider">
                                <div id="carouselHighlightNuggets" className="carousel slide" data-ride="carousel">
                                    <ol className="carousel-indicators">
                                        {highlightsSlider}
                                    </ol>
                                    <div className="carousel-inner">
                                        {highlightsDataDetail}
                                    </div>
                                    <a className="carousel-control-prev" href="#carouselHighlightNuggets" role="button" data-slide="prev">
                                        <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                                        <span className="sr-only">Previous</span>
                                    </a>
                                    <a className="carousel-control-next" href="#carouselHighlightNuggets" role="button" data-slide="next">
                                        <span className="carousel-control-next-icon" aria-hidden="true"></span>
                                        <span className="sr-only">Next</span>
                                    </a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="modal fade bd-example-modal-lg popup-mapplan p-0" id="popup-mapplan" role="dialog" aria-labelledby="popup-mapplan" aria-hidden="true">
                <div className="modal-dialog modal-dialog-full modal-dialog-centered">
                    <button type="button" className="close text-white position-absolute" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true" className="h1">&times;</span>
                    </button>
                    {placeMapPlan}
                </div>
            </div>
        </div>
    }
}



