import * as React from "react";
import { RegisterFeelForm } from "../form/RegisterFeelForm";

export class RegisterComponent extends React.Component<any, any> {
    public render() {
        return <div>
            <h6 className="mb-3">New User</h6>
            <RegisterFeelForm onSubmit={this.props.submitRegister} countryData={this.props.countryList} redirectLink={this.props.redirectLink} />
            {this.props.passwordCheck && !this.props.error && <span className="validate-txt"><small style={{ color: "#FF0000" }}>Password do not match</small></span>}
            {this.props.error && <span className="validate-txt"><small style={{ color: "#FF0000" }}>{this.props.registrationMessage}</small></span>}
            {this.props.success && !this.props.passwordCheck && <div className="alert alert-success" role="alert">Thank you for registering with us, we have mailed you the account activation link. Please activate your account to login</div>}
        </div>
    }
}



