import * as React from "React";
import { Link, RouteComponentProps } from "react-router-dom";
import ReactStars from 'react-stars';
import * as numeral from "numeral";
import { autobind } from "core-decorators";

export default class EventReviews extends React.Component<any, any> {
    constructor(props) {
        super(props);
        this.state = {
            minVal: this.props.minReview,
            maxVal: this.props.maxReview,
            newRating: 0,
            newReview: "",
            reviewRatingError: "",
            searchTearm: "",
            val: 1,
            isPopupShow: false,
            PopupMessage: "",
            isChecked: false,
            isSubmitButtonShow: true
        }
    }

    @autobind
    public changeNext() {
        if (this.state.maxVal + this.props.maxReview <= this.props.reviews.length) {
            this.setState({ maxVal: this.state.maxVal + this.props.maxReview });
        }
        if (this.state.maxVal + this.props.maxReview > this.props.reviews.length) {
            this.setState({ maxVal: this.props.reviews.length });
        }
        if (this.state.minVal + this.props.maxReview <= this.state.maxVal) {
            this.setState({ minVal: this.state.minVal + this.props.maxReview });
        }
        this.setState({ val: this.state.val + 1 });
    }

    @autobind
    public changeReviewPage(e) {
        this.setState({ minVal: ((parseInt(e.target.text) * this.props.maxReview) - this.props.maxReview) });
        this.setState({ maxVal: (parseInt(e.target.text) * this.props.maxReview) });
    }

    @autobind
    public changePrevious() {
        this.setState({ maxVal: this.state.minVal });
        this.setState({ minVal: this.state.minVal - this.props.maxReview });
        if (this.state.val <= 1) {
            this.setState({ val: 1 });
        }
        if (this.state.val > 1) {
            this.setState({ val: this.state.val - 1 });
        }
    }

    @autobind
    public ratingChanged(newRating) {
        var rating = newRating;
        this.setState({ newRating: rating, reviewRatingError: "" });
    }

    @autobind
    public reviewChanged(e) {
        this.setState({ newReview: e.target.value, reviewRatingError: "" });
    }

    @autobind
    public searchReview(e) {
        this.setState({ searchTearm: e.target.value, reviewRatingError: "" });
    }

    @autobind
    public onReviewSubmit(e) {
        if (!this.props.isPurase && this.props.isAthenticated) {
            this.props.goToBookNowPage();
        } else {
            this.props.onSubmit(this.state.newReview, this.state.newRating);
        }
    }

    @autobind
    public showPopup(e) {
        this.setState({ isPopupShow: true, isSubmitButtonShow : true });
    }
    @autobind
    public hidePopup(e) {
        this.setState({ isPopupShow: false });
    }

    @autobind
    public popupMessage(e) {
        this.setState({ PopupMessage: "These reports help make feelaplace better for everyone, so we take them seriously. Well reach out if we have questions about your report.", isSubmitButtonShow : false });
    }

    @autobind
    public popupMessageOnClose(e) {
        this.setState({ PopupMessage: "" });
    }

    @autobind
    public isChecked(e) {
        this.setState({ isChecked: true });
    }

    @autobind
    public handleKeyPress(target) {
        if (target.charCode == 13) {
            if (!this.props.isPurase && this.props.isAthenticated) {
                this.props.goToBookNowPage();
            } else {
                this.props.onSubmit(this.state.newReview, this.state.newRating);
            }
        }
    }

    public render() {
        var review;
        if (this.state.searchTearm === "") {
            review = this.props.reviews;
        } else {
            var that = this;
            review = this.props.reviews.filter(function (item) { return item.comment.toLowerCase().indexOf(that.state.searchTearm.toLowerCase()) >= 0 });

        }
        var isEditable = false;
        var placeholder = "Please login to add public comment...";
        if (this.props.isAthenticated) {
            if (this.props.isPurase) {
                isEditable = true;
            }
            if (this.props.isAthenticated && !this.props.isPurase) {
                placeholder = "Please purchase ticket of a place to add public comment...";
            } else {
                placeholder = "Add public comment...";
            }
        }
        var months = ["January", "Feburary", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
        var user = this.props.user;
        var userReview;
        if (review.length >= this.state.minVal) {
            review = review.slice(this.state.minVal, this.state.maxVal);
            var i = -1;
            userReview = review.map((item) => {
                i = i + 1;
                user = this.props.user.filter(function (user) { return user.id === item.userId });
                var userImage = this.props.UserImageMap.filter(function (user) { return user.id === item.userId });
                return <div className="col-sm-12 pt-3 mt-3 border-top user-reviews">
                    <a href="#" className="review-flag" data-toggle="modal" data-target="#exampleModalCenter" ><i onClick={this.showPopup.bind(this)} className="fa fa-flag-o" aria-hidden="true"></i></a>
                    <img src={userImage[0].imagePath} className="rounded-circle" alt="" />
                    <h6 className="text-capitalize">{user[0].firstName[0].toUpperCase() + "" + user[0].firstName.substring(1, user[0].firstName.length)}</h6>
                    <p>
                        <div className="rating">
                            Rating: <ReactStars className="rating d-inline-block" color2={"#572483"} edit={false} value={item.points} count={5} /> <br />
                            <span className="review-time"> {months[new Date(item.createdUtc).getMonth()]} {new Date(item.createdUtc).getFullYear()} </span> <br />
                            <div className="feel-comment">{item.comment}</div>
                        </div>
                    </p>
                </div>
            })
        }
        return <div className="pt-3 row review-sec">
            <div className="col-sm-8 review-sec-head">
                <h4 className="m-0">{this.props.eventname}</h4>
                <div className="rating">
                    <span>Ratings: <ReactStars className="d-inline-block" color2={"#572483"} style={{ lineHeight: '10px' }} half={false} value={this.state.newRating} count={5} onChange={this.ratingChanged} /> </span>
                    <span className="review-text">{this.props.reviews.length}+ Reviews</span>
                </div>
            </div>
            <div className="col-sm-4">
                <input className="form-control " type="search" onChange={this.searchReview.bind(this)} placeholder="Look for..." aria-label="Search" />
            </div>
            <div className="col-sm-12 pt-3 mt-3 border-top user-reviews">
                <img src="https://static1.feelaplace.com/images/user-profile/fee-review-user-icon.png" className="rounded-circle" alt="" />
                <input type="textarea" className="form-control comment-box " onChange={this.reviewChanged.bind(this)} onKeyPress={this.handleKeyPress.bind(this)} placeholder={placeholder} aria-label="Search" />
                {(this.state.reviewRatingError !== "") && <p className="validate-txt">{this.state.reviewRatingError}</p>}
                <button value="submit" className="float-right btn btn-outline-primary mt-2" onClick={this.onReviewSubmit.bind(this)} type="button">Submit</button>
            </div>
            {userReview}
            <div className="col-sm-12 pt-3 mt-3 border-top user-reviews">
                <nav aria-label="Page navigation example pull-right">
                    <ul className="pagination justify-content-end">
                        <li className="page-item">
                            {(this.state.minVal > 0) && <Link to="#" className="page-link" onClick={this.changePrevious}>Previous</Link>}
                        </li>
                        <li className="page-item">
                            {((this.state.val) * this.props.maxReview < this.props.reviews.length) && <Link to="#" className="page-link" onClick={this.changeReviewPage} >{this.state.val}</Link>}
                        </li>
                        <li className="page-item">
                            {((this.state.val + 1) * this.props.maxReview < this.props.reviews.length) && <Link to="#" className="page-link" onClick={this.changeReviewPage}>{this.state.val + 1}</Link>}
                        </li>
                        <li className="page-item">
                            {((this.state.val + 2) * this.props.maxReview < this.props.reviews.length) && <Link to="#" className="page-link" onClick={this.changeReviewPage}>{this.state.val + 2}</Link>}
                        </li>

                        <li className="page-item">
                            {(this.state.maxVal < this.props.reviews.length) && <Link to="#" className="page-link" onClick={this.changeNext}>Next</Link>}

                        </li>
                    </ul>
                </nav>
            </div>

            {(this.state.isPopupShow) && <div className="modal fade" id="exampleModalCenter" role="dialog" aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
                <div className="modal-dialog modal-dialog-centered" role="document">
                    <div className="modal-content">
                        <div className="modal-header">
                            <h5 className="modal-title" id="exampleModalLongTitle">Do you want to anonymously report this review?</h5>
                            <button type="button" onClick={this.popupMessageOnClose.bind(this)} className="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div className="modal-body">
                            <div className="form-check mb-2">
                                <label className="form-check-label">
                                    <input type="radio" onClick={this.isChecked.bind(this)} className="form-check-input" id="materialUnchecked" name="materialExampleRadios" />
                                    Inappropriate content</label>
                            </div>

                            <div className="form-check mb-2">
                                <label className="form-check-label">
                                    <input type="radio" onClick={this.isChecked.bind(this)} className="form-check-input" id="materialChecked" name="materialExampleRadios" />
                                    Dishonest or hateful content</label>
                            </div>

                            <div className="form-check mb-2">
                                <label className="form-check-label">
                                    <input type="radio" onClick={this.isChecked.bind(this)} className="form-check-input" id="materialChecked" name="materialExampleRadios" />
                                    Fake content</label>
                            </div>
                            {(this.state.isChecked && this.state.PopupMessage !== "") && <div><h4 className="text-success mt-4 mb-0">Thank You</h4> <p className="mt-20 text-success mt-1">{this.state.PopupMessage}</p></div>}
                        </div>

                        <div className="modal-footer">
                            <button type="button" onClick={this.popupMessageOnClose.bind(this)} className="btn btn-link" data-dismiss="modal">Close</button>
                            {(this.state.isSubmitButtonShow) && < button type="button" onClick={this.popupMessage.bind(this)} className="btn btn-primary site-primery-btn">Submit</button>}
                        </div>
                    </div>
                </div>
            </div>}
        </div>
    }
}


