import * as React from "react";
import { RouteComponentProps, Link } from "react-router-dom";

export default class EventName extends React.Component<any, any> {
    public render() {
        return <div>
            <h6 className="card-title m-0"><Link to={"/place/" + this.props.altId.toUpperCase()} className="text-dark">{this.props.Name}</Link></h6>
        </div>;
    }
}



