import { autobind } from "core-decorators";
import * as map from "lodash/map";
import * as React from "react";
import { connect } from "react-redux";
import { Route, Switch, withRouter } from "react-router-dom";
import { RouteComponentProps } from "react-router-dom";
import { bindActionCreators } from "redux";
import { actionCreators as sessionActionCreators, ISessionProps, ISessionState } from "shared/stores/Session";
import "shared/styles/globalStyles/main.scss";
import {
    userIsAdminRedirect,
    userIsAuthenticated,
    userIsAuthenticatedRedirect,
    userIsNotAuthenticated,
    userIsNotAuthenticatedRedirect
} from "shared/utils/auth";
import Layout from "./components/Layout";
import { IApplicationState } from "./stores";
import { actionCreators as categoryActionCreators, ICategoryProps, ICategoryState } from "./stores/Category";
//import CategoryView from "./views/Category";
import DeliveryOptionsView from "./views/DeliveryOptions";
import PaymentView from "./views/Payment";
import PaymentForm from "./views/PaymentForm";
import PaymentResponse from "./views/PaymentResponse";
import PaymentError from "./views/PaymentError";
import HomeView from "./views/Home";
import Registerview from "./views/Register";
import CheckoutView from "./views/Checkout";
import AccountView from "./views/Account";
import SignInView from "./views/SignIn";
import SignUpView from "./views/SignUp";
import TicketPurchaseSelectionView from "./views/TicketPurchaseSelection";
import ItineraryView from "./views/Itinerary";
import EventLearnPage from "./views/EventLearnPage";
import ActiveReview from "./views/ActiveReview";
import OrderConfirmation from "./views/OrderConfirmation";
import ActivateUser from "./views/Activate";
import ResetPassword from "./views/ResetPassword";
import ComingSoon from "./views/ComingSoon";

interface IAppProps {
    gtmId: string;
}

type AppProps = IAppProps
    & ISessionProps
    & ICategoryProps
    & typeof sessionActionCreators
    & typeof categoryActionCreators
    & RouteComponentProps<IAppProps>;

class App extends React.Component<AppProps, {}> {
    public componentDidMount() {
        if (!this.props.session.isLoading) {
            this.props.getSession();
        }
        if (!this.props.category.isLoading) {
            this.props.requestCategoryData();
        }
    }

    public render() {
        return <Layout
            gtmId={this.props.gtmId}
            categories={this.props.category.categories}
            url={this.props.location.pathname}>
            <Switch>
                <Route exact path="/" component={HomeView} />
                <Route path="/delivery-options" component={DeliveryOptionsView} />
                <Route path="/payment" component={PaymentView} />
                <Route exact path="/paymentform" component={PaymentForm} />
                <Route path="/response" component={PaymentResponse} />
                <Route path="/pgerror" component={PaymentError} />
                <Route exact path="/ticket-purchase-selection/:eventId" component={TicketPurchaseSelectionView} />
                <Route exact path="/itinerary" component={ItineraryView} />
                <Route exact path="/place/:eventId" component={EventLearnPage} />
                <Route exact path="/review/:ratingAltId" component={ActiveReview} />
                <Route path="/order-confirmation" component={OrderConfirmation} />
                <Route path="/checkout" component={CheckoutView} />
                <Route path="/register" component={Registerview} />
                <Route path="/reset-password" component={ResetPassword} />
                <Route path="/login" component={SignInView} />
                <Route path="/signup" component={SignUpView} />
                <Route exact path="/account" component={AccountView} />                
                <Route exact path="/activate/:altId" component={ActivateUser} />
                <Route exact path="/coming-soon" component={ComingSoon} />
                {/* Dynamic routes for categories */}
                {/*map(this.props.category.categories, (cat, index) => {
                    return <Route path={`/${cat.slug}`} render={(props) => {
                                return <CategoryView {...props} key={`${index}`} />;
                        }} />;
                    })
                */}
            </Switch>
        </Layout>;
    }
}

export default connect(
    (state: IApplicationState, ownProps) => ({ session: state.session, category: state.category, ...ownProps }),
    (dispatch) => bindActionCreators({ ...sessionActionCreators, ...categoryActionCreators }, dispatch)
)(App);



