import { autobind } from "core-decorators";
import * as React from "react";
import { connect } from "react-redux";
import { IApplicationState } from "../stores";
import { RouteComponentProps } from "react-router-dom";
import * as OrderConfirmationStore from "../stores/OrderConfirmation";
import EventItems from "../../ClientApp/components/OrderConfirmation/EventItems";
import KzLoader from "../components/Loader/KzLoader";
import "./OrderConfirmation.scss";
import * as numeral from "numeral";

type OrderConfirmationComponentProps =
    OrderConfirmationStore.IOrderConfirmationState
    & typeof OrderConfirmationStore.actionCreators

class OrderConfirmation extends React.Component<OrderConfirmationComponentProps, OrderConfirmationStore.IOrderConfirmationState> {
    public constructor() {
        super();
    }

    public componentDidMount() {
        if (window) {
            window.scrollTo(0, 0)
        }

        if (localStorage.getItem('transactionState') != null && localStorage.getItem('transactionState') != '0') {
        	var data = JSON.parse(localStorage.getItem("transactionState"));
        	var transactionId = data.transactionId;
        	this.props.requestConfirmationData(transactionId);
        }
    }
    public render() {
        if (this.props.fetchOrderConfirmationSuccess) {
            var orderConfirmationData = this.props.orderconfirmations;
            return <div className="container confirmation-page pt-3 pb-5 inner-banner">
                <div className="card">
                    <div className="card-header h5 bg-white">
                        Your Order Confirmation <small className="float-right text-muted"><i className="fa fa-lock text-success"></i> Secure</small>
                    </div>
                    <div className="card-body">
                        <div className="cart-feader text-center">
                            <h2>Thank You</h2>
                            <p className="text-success">Your order has been processed</p>
                            <p className="spread-word">Spread the word:
					        <span className="float-right">
                                    <a href="https://www.instagram.com/feelaplace/" target="_blank"><i className="fa fa-instagram-square text-danger"></i></a>
                                    <a href="https://www.facebook.com/feel.aplace.1" target="_blank"><i className="fa fa-facebook-square text-primary"></i></a>
                                    <a href="https://twitter.com/feelaplace" target="_blank"><i className="fa fa-twitter-square text-primary"></i></a>                                    
                                </span>
                            </p>
                            <p> <img src={orderConfirmationData.transactionQrcode} width="120" height="120" alt="" /> </p>
                            <p className="text-muted clearfix">
                                <span className="float-sm-left">Your Confirmation ID is:{orderConfirmationData.transaction.id * 5 + "0032-" + "KZ" + orderConfirmationData.transaction.id}</span>
                                <span className="float-sm-right">Your Date and Time of Booking: {new Date(orderConfirmationData.transaction.createdUtc).toDateString().split(' ').join(', ').toUpperCase()} {numeral(new Date(orderConfirmationData.transaction.createdUtc).getHours()).format('00') + ":" + numeral(new Date(orderConfirmationData.transaction.createdUtc).getMinutes()).format('00')}</span>
                            </p>
                        </div>
                        <hr />
                        <div className="row">
                            <div className="col-sm-6">
                                <h4 className="border-bottom pb-2">User Details</h4>
                                <p>
                                    <span className="h5 d-block">Buyer Name:</span>
                                    <i className="fa fa-user" aria-hidden="true"></i> {orderConfirmationData.transaction.firstName} {orderConfirmationData.transaction.lastName}
                                </p>
                                <p>
                                    <span className="h5 d-block">Email:</span>
                                    <i className="fa fa-envelope" aria-hidden="true"></i> {orderConfirmationData.transaction.emailId}
                                </p>
                                <p>
                                    <span className="h5 d-block">Mobile No:</span>
                                    <i className="fa fa-mobile-alt"></i> +{orderConfirmationData.transaction.phoneCode + " - " + orderConfirmationData.transaction.phoneNumber}
                                </p>
                            </div>

                            <div className="col-sm-6">
                                <h4 className="border-bottom pb-2">Payment Details</h4>
                                <p>
                                    <span className="h5 d-block">Mode of Payment</span>
                                    {orderConfirmationData.paymentOption} ({orderConfirmationData.userCardDetail.cardTypeId})
					            </p>
                                <p>
                                    <span className="h5 d-block">Name on Card</span>
                                    {orderConfirmationData.userCardDetail.nameOnCard}
                                </p>
                                <p>
                                    <span className="h5 d-block">Bag Sub Total</span>
                                    {orderConfirmationData.currencyType.code} {numeral(orderConfirmationData.transaction.grossTicketAmount).format('00.00')}
                                </p>
                                <p>
                                    <span className="h5 d-block">Delivery Fee</span>
                                    {orderConfirmationData.currencyType.code} {numeral(orderConfirmationData.transaction.deliveryCharges).format('00.00')}
                                </p>
                                <p>
                                    <span className="h5 d-block">Total Ammount Paid</span>
                                    {orderConfirmationData.currencyType.code} {numeral(orderConfirmationData.transaction.netTicketAmount).format('00.00')}
                                </p>
                            </div>
                        </div>
                        <EventItems eventContainer={orderConfirmationData} currency={orderConfirmationData.currencyType} />
                    </div>
                    <div className="gradient-bg p-3 pr-4 pl-4 border-top">
                        Should you have any questions regarding your transaction, please email us at <a href="mailto:customerservice@feelaplace.com">customerservice@feelaplace.com</a> or call us at +912233489000 (M-F) 9:30am to 6:00pm IST
		            </div>
                </div>
            </div>
        } else {
            return <div><KzLoader /></div>
        }
    }
}
export default connect(
    (state: IApplicationState) => state.OrderConfirmation,
    OrderConfirmationStore.actionCreators
)(OrderConfirmation);


