import * as React from "React";
import * as numeral from "numeral";
import { RouteComponentProps, Link } from "react-router-dom";

export default class Checklist extends React.Component<any, any> {
    public render() {
        return <div className="col-sm-4 more-checklist">
            <h5 className="mb-4"><img src={'https://static1.feelaplace.com/images/footer/feel-heart-logo.png'} className="mr-2" /> Checklist</h5>
            <p className="mb-2"><span><i className="fa fa-map-marker" aria-hidden="true" style={{ fontSize: "19px"}}></i></span><span className="w-75 text-left align-text-top">{this.props.venueName}, {this.props.cityName}, {this.props.stateName}, {this.props.countryName}</span></p>
            <p className="mb-2"><span><i className="fa fa-clock-o" aria-hidden="true"></i></span>{numeral(new Date(this.props.eventDetails.startDateTime).getHours()).format('00') + ":" + numeral(new Date(this.props.eventDetails.startDateTime).getMinutes()).format('00')}-{numeral(new Date(this.props.eventDetails.endDateTime).getHours()).format('00') + ":" + numeral(new Date(this.props.eventDetails.endDateTime).getMinutes()).format('00')} </p>
            <p className="mb-2"><span><img src={'https://static1.feelaplace.com/images/event/AboutEvent/icons/map-icon.png'} style={{ width: "14" }} /></span> <a href="#">How to get there</a></p>
            <p className="mb-2"><span><img src={'https://static1.feelaplace.com/images/event/AboutEvent/icons/wether-icon.png'} style={{ width: "18" }} /></span> <a href="#">Weather</a> </p>
            <p><span><img src={'https://static1.feelaplace.com/images/event/AboutEvent/icons/briefcase-icon.png'} style={{ width: "14" }} /></span> <a href="#">What to pack</a> </p>
        </div>
    }
}


