import { autobind } from "core-decorators";
import * as React from "react";
import { connect } from "react-redux";
import { Link, RouteComponentProps } from "react-router-dom";
import KzAlert from "../components/Alert/KzAlert";
import { ResetPasswordForm } from "../components/form/ResetPasswordForm";
import { ResetPasswordFormDataViewModel } from "shared/models/ResetPasswordFormDataViewModel";
import { ResetPasswordResponseViewModel } from "shared/models/ResetPasswordResponseViewModel";
import { IApplicationState } from "../stores";
import * as ResetPasswordStore from "../stores/ResetPassword";

type ResetPasswordProps = ResetPasswordStore.IResetPasswordState & typeof ResetPasswordStore.actionCreators & RouteComponentProps<{}>;

class ResetPassword extends React.Component<ResetPasswordProps, ResetPasswordStore.IResetPasswordState> {
    public componentDidMount() {
        if (window) {
            window.scrollTo(0, 0)
        }
    }

    public render() {
        return <div className="container sign-form forgot-password pt-3 pb-5">
            <div className="card">
                <div className="card-header h5 bg-white">
                    Reset Password <small className="float-right text-muted"><i className="fas fa-lock text-success"></i> Secure</small>
                </div>
                <div className="card-body">
                    <div className="row">
                        <div className="col-lg-6 col-sm-12 sign-form-left">
                            <p>It's a good idea to use a strong password that you're not using elsewhere</p>
                            <ResetPasswordForm onSubmit={this.onSubmitResetPassword} />
                            <br />
                            {this.props.error && <span className="validate-txt"><br /><p>{this.props.errorMessage}</p></span>}
                            {this.props.passwordMismatch && <span className="validate-txt"><br /><p>Passwords do not match</p></span>}
                            {this.props.resetSuccessful && <span className="validate-txt"><p style={{ color: "#5bac30" }}>{this.props.alertMessage.body}</p></span>}
                        </div>
                        <div className="col-lg-6 col-sm-12 sign-form-right">
                            <Link to="/checkout" href="#" className="btn btn-link"><i className="fa fa-angle-left mr-2" aria-hidden="true"></i> Back to Sign In</Link>
                        </div>
                    </div>
                </div>
            </div>
        </div>;
    }

    @autobind
    private onSubmitResetPassword(values: ResetPasswordFormDataViewModel) {
        if (values.password != values.confirmPassword) {
            this.props.passwordMismatchAction();
        } else {
            values.altId = this.props.location.search.split('?')[1];
            this.props.resetPassword(values, (response: ResetPasswordResponseViewModel) => {
                if (response.success) {
                    setTimeout(() => {
                        this.props.history.push({ pathname: "/" });
                    }, 3000);
                } else {
                }
            });
        }
    }
}

export default connect(
    (state: IApplicationState) => state.ResetPassword,
    ResetPasswordStore.actionCreators
)(ResetPassword);



