import * as React from "react";
import * as numeral from "numeral";

export default class EventPrice extends React.Component<any, any> {
    public render() {
        var price;
        var symbol;
        if (this.props.currency == "INR") {
            symbol = "";
        }
        else if (this.props.currency == "GBP") {
            symbol = "";
        }
        else if (this.props.currency == "USD") {
            symbol = "$";
        }
        else if (this.props.currency == "EURO") {
            symbol = "";
        }
        else {
            symbol = "";
        }

        if (this.props.minPrice == this.props.maxPrice) {
            //price = this.props.minPrice
            price = <p className="card-text m-0"><small><i aria-hidden="true"></i>{symbol + this.props.minPrice}</small></p>
        }
        else {
            price = <p className="card-text m-0"><small><i aria-hidden="true"></i>{symbol + this.props.minPrice} - <i aria-hidden="true"></i>{symbol + this.props.maxPrice}</small></p>
            //price = '' + this.props.minPrice + ' - ' + this.props.maxPrice
        }

        return <div>
            {price}
        </div>;
    }
}


