import * as numeral from "numeral";
import * as React from "react";
import { Link } from "react-router-dom";
import * as PubSub from 'pubsub-js';

export default class NavCart extends React.Component<any, any> {
    public constructor() {
        super();
        this.state = { cartData: "", isRedirect: true, isSort: 0, isPaymentPage: false, isOrderConfirmation: false };
    }

    public componentWillMount() {
        this.setState({ cartData: "" });
        PubSub.subscribe('UPDATE_NAV_CART_DATA_EVENT', this.subscriberData.bind(this));
        PubSub.subscribe('UPDATE_NAV_CART_DATA_ON_CART_REMOVE_EVENT', this.subscribeNavCartData.bind(this));
    }

    //public componentWillUnmount() {
    //    PubSub.unsubscribe('UPDATE_NAV_CART_DATA_ON_CART_REMOVE_EVENT');
    //}

    public subscriberData(msg, data) {
        var that = this;
        if (localStorage.getItem('cartItems') != null && localStorage.getItem('cartItems') != '0') {
            var data = JSON.parse(localStorage.getItem('cartItems'));
            this.setState({ cartData: data });
        }
        this.setState({ cartError: false });
        if ((window as any).location.href.indexOf("payment") > -1) {
            this.setState({ isPaymentPage: true });
        }
    }

    public subscribeNavCartData(msg, data) {
        var that = this;
        if (localStorage.getItem('cartItems') != null && localStorage.getItem('cartItems') != '0') {
            var data = JSON.parse(localStorage.getItem('cartItems'));
            this.setState({ cartData: data });
        } else {
            localStorage.removeItem('cartItems');
            this.setState({ cartData: null });
        }
        this.setState({ cartError: false });
        if ((window as any).location.href.indexOf("payment") > -1) {
            this.setState({ isPaymentPage: true });
        }
    }

    public componentDidMount() {
        if ((window as any).location.href.indexOf("payment") > -1) {
            this.setState({ isPaymentPage: true });
        }
        if ((window as any).location.href.indexOf("order-confirmation") > -1) {
            this.setState({ isOrderConfirmation: true });
        }
        if (localStorage.getItem('cartItems') != null && localStorage.getItem('cartItems') != '0' && localStorage.getItem('cartItems') !== "undefined") {
            var data = JSON.parse(localStorage.getItem('cartItems'));
            this.loadCartItems(data);
            this.setState({ cartData: data, cartError: false });
        }
    }

    public loadCartItems(data) {
        var that = this;
        if (data != "" && (this.state.isOrderConfirmation == false)) {
            var cartData;
            if (localStorage.getItem('cartItems') === "") {
                cartData = null;
                localStorage.removeItem('cartItems');
            } else {
                cartData = JSON.parse(localStorage.getItem('cartItems'));
            }
            data.map(function (val) {
                var eventTicketAttributeId = val.eventTicketAttributeId;
                var quantity = val.quantity;
                that.setState({
                    [eventTicketAttributeId]: quantity,
                    isSort: that.state.isSort + 1,
                    cartData: cartData
                });
            });
        } else {
            this.setState({ cartData: "" });
        }
    }





    public removeItemFromCart(item, e) {
        if (localStorage.getItem('cartItems') != null && localStorage.getItem('cartItems') != '0') {
            var cartData = JSON.parse(localStorage.getItem('cartItems'));
            var cartItems;
            for (var k = 0; k < cartData.length; k++) {
                if (cartData[k].eventDetailId == item.eventDetailId) {
                    cartData[k].quantity = 0;
                    localStorage.setItem('cartItems', JSON.stringify(cartData));
                }
            }
            localStorage.setItem('cartItems', JSON.stringify(JSON.parse(localStorage.getItem('cartItems')).filter(function (item) { return item.quantity > 0 })));
            this.loadCartItems(cartData);

            PubSub.publish('UPDATE_CART_COUNT_EVENT', 1);
            PubSub.publish('UPDATE_CART_DATA_EVENT', 1);
            //PubSub.publish('UPDATE_CART_COUNT_EVENT', 1);
            //PubSub.publish('UPDATE_TICKET_CATEGORY_EVENT', 1);
            //PubSub.publish('UPDATE_EMPTY_CART_EVENT', 1);
        }
    }


    public getUnique(data) {
        return data.filter(function (x, i) {
            return data.indexOf(x) === i
        })
    }

    public render() {
        if (this.state.cartData !== "" && this.state.cartData !== null && (this.state.isOrderConfirmation === false)) {
            var data = this.state.cartData.filter(function (item) { return item.quantity > 0 });
            var quantity = 0;
            var totalquantity = this.state.cartData.map(function (item) { return quantity += item.quantity });
            var that = this;
            var unique = Array.from(new Set(data.map((item) => item.eventDetailId)));
            var eventDetailsIds = data.map((item) => item.eventDetailId);
            unique = that.getUnique(eventDetailsIds);
            var cartItems = unique.map(function (eventdetailId) {
                var categories = data.filter(function (cat) {
                    return cat.eventDetailId == eventdetailId;
                })
                var symbol = "";
                if (categories[0].currencyName == "INR") {
                    symbol = "";
                }
                else if (categories[0].currencyName == "GBP") {
                    symbol = "";
                }
                else if (categories[0].currencyName == "USD") {
                    symbol = "$";
                }
                else if (categories[0].currencyName == "EURO") {
                    symbol = "";
                }
                else {
                    symbol = "";
                }
                var categoryData = categories.map(function (val) {
                    return <small>
                        <div>{val.ticketCategoryName + " (" + val.quantity + " x " + symbol + numeral(val.pricePerTicket).format('00.00') + ")"}
                            <span className="pull-right">{symbol + numeral((val.quantity * val.pricePerTicket)).format('00.00')}</span></div>
                    </small>
                });
                var imageSrc = "https://static1.feelaplace.com/images/event/HotTickets/" + categories[0].altId + "-ht-c1.jpg";
                return <li className="clearfix">
                    {(!that.state.isPaymentPage) && <a href="javascript:void(0)" onClick={that.removeItemFromCart.bind(that, categories[0])} className="close">x</a>}
                    <div className="float-left">
                        <img className="rounded" src={imageSrc} />
                    </div>
                    <div className="float-right">
                        <span>
                            {categories[0].name}<br />
                            {categoryData}
                        </span>
                    </div>
                </li>
            })
            return <li className="nav-item dropdown">
                <Link to="/itinerary" data-toggle="dropdown" className="nav-link" role="button" aria-expanded="false">
                    {(quantity > 0) && <img src="https://static1.feelaplace.com/images/header/cart-icon-fill.png" alt="Feel Cart Icon" />}
                    {(quantity === 0) && <img src="https://static1.feelaplace.com/images/header/cart-icon-blank.png" alt="Feel Cart Icon" />}
                </Link>
                <ul className="dropdown-menu dropdown-cart p-2">
                    <div>
                        {cartItems}
                        <li>
                            {(quantity > 0) && <Link to="/itinerary" className="btn btn-block btn-primary" aria-label="checkout">
                                Checkout
                    </Link>}
                            {(quantity == 0) && <div>No items in your cart</div>}
                        </li>
                        <li className="header-list"><i className="fa fa-shopping-bag" aria-hidden="true"></i><Link to="/itinerary">Bag ({quantity})</Link></li>
                        {(this.props.isAuth === false) && <li className="header-list"><i className="fa fa-inbox" aria-hidden="true"></i><Link to="/login">Orders</Link></li>}
                        {(this.props.isAuth === true) && <li className="header-list"><i className="fa fa-inbox" aria-hidden="true"></i><Link to="/account">Orders</Link></li>}
                        {(this.props.isAuth === false) && <li className="header-list"><i className="fa fa-inbox" aria-hidden="true"></i><Link to="/login">Account</Link></li>}
                        {(this.props.isAuth === true) && <li className="header-list"><i className="fa fa-inbox" aria-hidden="true"></i><Link to="/account">Account</Link></li>}
                    </div>
                </ul>
            </li>

        } else {

            return <li className="nav-item dropdown">
                <Link to="/itinerary" data-toggle="dropdown" className="nav-link" role="button" aria-expanded="false">
                    {(quantity > 0) && <img src="https://static1.feelaplace.com/images/header/cart-icon-fill.png" alt="Feel Cart Icon" />}
                    {(quantity === 0) && <img src="https://static1.feelaplace.com/images/header/cart-icon-blank.png" alt="Feel Cart Icon" />}
                </Link>
                <ul className="dropdown-menu dropdown-cart p-2">
                    <div>
                        {cartItems}
                        <li>
                            {(quantity > 0) && <Link to="/itinerary" className="btn btn-block btn-primary" aria-label="checkout">
                                Checkout
                    </Link>}
                            {(quantity == 0) && <div>No items in your cart</div>}
                        </li>
                        <li className="header-list"><i className="fa fa-shopping-bag" aria-hidden="true"></i><Link to="/itinerary">Bag ({quantity})</Link></li>
                        {(this.props.isAuth === false) && <li className="header-list"><i className="fa fa-inbox" aria-hidden="true"></i><Link to="/login">Orders</Link></li>}
                        {(this.props.isAuth === true) && <li className="header-list"><i className="fa fa-inbox" aria-hidden="true"></i><Link to="/account">Orders</Link></li>}
                        {(this.props.isAuth === false) && <li className="header-list"><i className="fa fa-inbox" aria-hidden="true"></i><Link to="/login">Account</Link></li>}
                        {(this.props.isAuth === true) && <li className="header-list"><i className="fa fa-inbox" aria-hidden="true"></i><Link to="/account">Account</Link></li>}
                    </div>
                </ul>
            </li>
        }
    }
}



