import * as React from "react";
import { Link, RouteComponentProps } from "react-router-dom";
import { IApplicationState } from "../../stores";
import * as CustomerUpdateStore from "../../stores/CustomerUpdate";
import { Modal, Button } from "react-bootstrap";
import { connect } from "react-redux";

type CustomerUpdateProps = CustomerUpdateStore.ICustomerUpdateComponentState & typeof CustomerUpdateStore.actionCreators & RouteComponentProps<{}>;

export class CustomerUpdate extends React.Component<CustomerUpdateProps, any> {
	constructor(props, context) {
		super(props, context);

		this.handleShow = this.handleShow.bind(this);
		this.handleClose = this.handleClose.bind(this);

		this.state = {
			show: false			
		};
	}

	public componentDidMount() {
		this.props.requestCustomerUpdateData();
	}

	handleClose() {
		this.setState({ show: false });
	}

	handleShow() {
		this.setState({ show: true });
	}

	public render() {
		if (this.props.fetchCustomerUpdateSuccess) {
			const customerUpdateData = this.props.customerUpdate;
			var custData;
			if (customerUpdateData.customerUpdates.length > 0) {
				custData = customerUpdateData.customerUpdates.map(function (val) {
					return <li className="border-bottom">{ val.description }</li>;
				});
			}
			else {
				custData = <li className="border-bottom">No updates yet, Stay tuned for updates</li>;
			}

            return (<div><a className="nav-link" href="#" id="navbarDropdownUpdate" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                    <img alt="Feel Notification Icon" src="https://static1.feelaplace.com/images/header/bell-icon.png" />
                </a>
				<div className="dropdown-menu" aria-labelledby="navbarDropdownUpdate">
					<ol className="m-0 cus-update">{custData}</ol>
				</div>
			</div>);
		}
		else {
            return <div><a className="nav-link"><img alt="Feel Notification Icon" src="https://static1.feelaplace.com/images/header/bell-icon.png" /></a></div>
		}
	}
}

export default connect(
	(state: IApplicationState) => state.CustomerUpdate,
	CustomerUpdateStore.actionCreators
)(CustomerUpdate);



