import * as React from "react";
import { Link, RouteComponentProps } from "react-router-dom";
import KzLoader from "../Loader/KzLoader";
import { DeleteAddressDataViewModel } from "../../models/Account/DeleteAddressDataViewModel";
import { SetDefaultAddressFormDataViewModel } from "../../models/Account/SetDefaultAddressFormDataViewModel";
import { SaveAddressForm } from "../Account/form/SaveAddressForm";
import { GetAddressesDataViewModel } from "../../models/Account/GetAddressesDataViewModel";

export class UserAddresses extends React.Component<any, any> {
    public constructor() {
        super();
        this.state = {
            addressType: 1
        };
    }

    public componentDidMount() {
        if (localStorage.getItem('userToken') != null && localStorage.getItem('userToken') != '0') {
            var address: GetAddressesDataViewModel = {
                altId: localStorage.getItem('userToken'),
                addressType: 1,
            };
            this.props.getAddressList(address);
        }
    }

    public deleteAddress(addressAltID, e) {
        e.preventDefault();
        const address: DeleteAddressDataViewModel = {
            altId: addressAltID,
        };
        this.props.submitDeleteAddress(address);
    }

    public changeAddressType(addressTypeId, e) {
        this.setState({ addressType: addressTypeId });
        var address: GetAddressesDataViewModel = {
            altId: localStorage.getItem('userToken'),
            addressType: addressTypeId,
        };
        this.props.getAddressList(address);
    }

    public setDefaultAddress(addressAltId, makeDefault, e) {
        const defaultAddress: SetDefaultAddressFormDataViewModel = {
            altId: addressAltId,
            makeDefault: makeDefault,
        };
        this.props.submitSetDefaultAddress(defaultAddress);
    }

    public render() {
        var that = this;
        var countries = this.props.countryData;
        if (this.props.fetchSuccess) {
            var addressList = this.props.addressList.userAddresses.map(function (val) {
                return <div className="col-lg-4 col-sm-6">
                    <ul key={val.altId} className="list-group">
                        <li className="list-group-item list-group-item-secondary">{val.firstName + " " + val.lastName}</li>
                        <li className="list-group-item list-group-item-secondary">{"+" + val.phoneCode + " " + val.phoneNumber}</li>
                        <li className="list-group-item list-group-item-secondary">{val.addressLine1}
                            <br />{val.zipcode}
                        </li>
                        <li className="list-group-item list-group-item-secondary">
                            <div className="custom-control custom-checkbox float-left">
                                <label className="custom-control-label">
                                    <input type="checkbox" value="" checked={val.isDefault} onClick={that.setDefaultAddress.bind(that, val.altId, !val.isDefault)} className="custom-control-input" />Default Address
                                        </label>
                            </div>
                            <div className="float-right">
                                <Link to="#" onClick={that.deleteAddress.bind(that, val.altId)}>
                                    <i className="far fa-trash-alt" aria-hidden="true"></i> Remove
                                        </Link>
                            </div>
                        </li>
                    </ul>
                </div>
            });

            return <div className="col-sm-9 right-act-pnl">
                <h4 className="mb-3 mt-3">Addresses</h4>
                <div className="card">
                    <div className=" card-body">
                        <div className="row m-t-30">
                            <p><button type="button" className="btn btn-outline-secondary" data-toggle="collapse" data-target="#collapseExample" aria-expanded="false" aria-controls="collapseExample">+ Add New Address</button></p>
                            <div className="col-sm-4" style={{ marginTop: "8" }}>
                                <input type="radio" checked={this.state.addressType == 1} onClick={this.changeAddressType.bind(this, 1)} name="rdbAddressType" value="1" /> Shipping Address
                            </div>
                            <div className="col-sm-4" style={{ marginTop: "8" }}>
                                <input type="radio" checked={this.state.addressType == 2} onClick={this.changeAddressType.bind(this, 2)} name="rdbAddressType" value="2" /> Billing Address
                            </div>
                        </div>
                        <div id="collapseExample" className="new-address-fields collapse">
                            <SaveAddressForm countryData={countries} addressType={this.state.addressType} onSubmit={this.props.submitSaveAddress} onCancel={this.props.onCancel} />
                        </div>
                        <div className="row user-address">
                            {addressList}
                        </div>
                    </div>
                </div>
            </div>;
        } else if (this.props.updateSuccess) {
            return <div>
            </div>
        } else {
            return <div><KzLoader /></div>;
        }
    }
}



