import * as React from "react";
import * as ReactDOM from "react-dom";
import { connect } from "react-redux";
import { Link, RouteComponentProps } from "react-router-dom";
import { CartDataViewModel, CartItem, GuestData } from "../../models/Cart/CartDataViewModel";
import Calendar from 'react-calendar';
import * as numeral from "numeral";
import * as PubSub from 'pubsub-js';
import NavCart from "../NavCart/NavCart";
import "./TicketCategoryDateSelection.scss";

export default class TicketCategorySelection extends React.Component<any, any> {
    public componentWillMount() {
        var that = this;
        var ticketCategoryDataModel: CartDataViewModel = {
            cartItems: [],
        };
        var venueData = this.props.ticketCategoryData.venue;
        //var venueData = this.props.ticketCategoryData.venue.filter(function (val) {
        //    return val.id == that.props.eventTimeObject.venueId
        //});
        var ticketCategories = this.props.ticketCategoryData.eventTicketDetail;
        //var ticketCategories = this.props.ticketCategoryData.eventTicketDetail.filter(function (val) {
        //    return val.eventDetailId == that.props.eventTimeObject.id
        //});
        ticketCategories.forEach(function (item) {
            let categoryName = that.props.ticketCategoryData.ticketCategory.filter(function (val) {
                return val.id == item.ticketCategoryId
            });
            let price = that.props.ticketCategoryData.eventTicketAttribute.filter(function (val) {
                return val.eventTicketDetailId == item.id
            });

            let ticketCategoryDescription = that.props.ticketCategoryData.eventTicketAttribute.filter(function (val) {
                return val.eventTicketDetailId == item.id
            });

            let eventTermsAndConditions = that.props.ticketCategoryData.event.termsAndConditions;

            let cartJson: CartItem = {
                altId: that.props.eventAltId,
                eventDetailId: that.props.ticketCategoryData.eventDetail[0].id,
                name: that.props.ticketCategoryData.eventDetail[0].name,
                eventStartDate: that.props.ticketCategoryData.eventDetail[0].startDateTime,
                venue: venueData[0].name,
                city: that.props.ticketCategoryData.city[0].name,
                ticketCategoryId: categoryName[0].id,
                ticketCategoryName: categoryName[0].name,
                eventTicketAttributeId: price[0].id,
                currencyId: that.props.ticketCategoryData.currencyType.id,
                currencyName: that.props.ticketCategoryData.currencyType.code,
                quantity: 0,
                pricePerTicket: price[0].price,
                ticketCategoryDescription: ticketCategoryDescription[0].ticketCategoryDescription,
                eventTermsAndConditions: eventTermsAndConditions,
                guestList: [],
            }
            ticketCategoryDataModel.cartItems.push(cartJson);
        });
        this.setState({ cartData: ticketCategoryDataModel.cartItems, calenderVisible: true, showTicketType: false, enableContinueToItinerary: false, dateSelected: false });
        this.loadCartItems(ticketCategoryDataModel.cartItems);
    }

    //public componentDidMount() {
    //	if (localStorage.getItem('cartItems') != null && localStorage.getItem('cartItems') != '0') {
    //		var data = JSON.parse(localStorage.getItem("cartItems"));
    //		this.loadCartItems(data);
    //		this.setState({ cartData: data });
    //	}
    //}

    public toggleCalender(e) {
        this.setState({ calenderVisible: (!this.state.calenderVisible) });
    }

    public showTime(date) {
        var that = this;
        var ticketCategoryDataModel: CartDataViewModel = {
            cartItems: [],
        };
        var venueData = this.props.ticketCategoryData.venue;
        //var venueData = this.props.ticketCategoryData.venue.filter(function (val) {
        //    return val.id == that.props.eventTimeObject.venueId
        //});
        var ticketCategories = this.props.ticketCategoryData.eventTicketDetail;
        var eventDateData = this.props.ticketCategoryData.eventDetail.filter(function (val) {
            return new Date(val.startDateTime).toDateString() == new Date(date).toDateString()
        });
        var currentDateCategories = this.props.ticketCategoryData.eventTicketDetail.filter(function (val) {
            return val.eventDetailId == eventDateData[0].id
        })
        //var ticketCategories = this.props.ticketCategoryData.eventTicketDetail.filter(function (val) {
        //    return val.eventDetailId == that.props.eventTimeObject.id
        //});
        currentDateCategories.forEach(function (item) {
            let categoryName = that.props.ticketCategoryData.ticketCategory.filter(function (val) {
                return val.id == item.ticketCategoryId
            });
            let price = that.props.ticketCategoryData.eventTicketAttribute.filter(function (val) {
                return val.eventTicketDetailId == item.id
            });

            let ticketCategoryDescription = that.props.ticketCategoryData.eventTicketAttribute.filter(function (val) {
                return val.eventTicketDetailId == item.id
            });
            let eventTermsAndConditions = that.props.ticketCategoryData.event.termsAndConditions;

            let cartJson: CartItem = {
                altId: that.props.eventAltId,
                eventDetailId: eventDateData[0].id,
                name: eventDateData[0].name,
                eventStartDate: eventDateData[0].startDateTime,
                venue: venueData[0].name,
                city: that.props.ticketCategoryData.city[0].name,
                ticketCategoryId: categoryName[0].id,
                ticketCategoryName: categoryName[0].name,
                eventTicketAttributeId: price[0].id,
                currencyId: that.props.ticketCategoryData.currencyType.id,
                currencyName: that.props.ticketCategoryData.currencyType.code,
                quantity: 0,
                pricePerTicket: price[0].price,
                ticketCategoryDescription: ticketCategoryDescription[0].ticketCategoryDescription,
                eventTermsAndConditions: eventTermsAndConditions,
                guestList: [],
            }
            ticketCategoryDataModel.cartItems.push(cartJson);
        });
        this.setState({ cartData: ticketCategoryDataModel.cartItems, calenderVisible: true, "date": date, showTicketType: true, dateSelected: true });
        this.loadCartItems(ticketCategoryDataModel.cartItems);
    }

    public loadCartItems(data) {
        var that = this;
        data.map(function (val) {
            var eventTicketAttributeId = val.eventTicketAttributeId;
            var quantity = val.quantity;
            that.setState({
                [eventTicketAttributeId]: quantity
            })
        });
        //PubSub.publish('UPDATE_CART_COUNT_EVENT', 1);
        
    }

    public addTicket(item, e) {
        e.preventDefault();
        if (this.state[item.eventTicketAttributeId] < 10) {
            var cartData = this.state.cartData;
            for (var i = 0; i < cartData.length; i++) {
                if (cartData[i].eventTicketAttributeId == item.eventTicketAttributeId) {
                    cartData[i].quantity = ++(item.quantity);
                    var newGuest: GuestData = {
                        guestId: cartData[i].guestList.length + 1,
                        firstName: "",
                        lastName: "",
                        nationality: "",
                        documentTypeId: "",
                        documentNumber: "",
                    };
                    cartData[i].guestList.push(newGuest);
                    var cartItems = cartData.filter(function (val) {
                        return val.quantity > 0
                    });
                    localStorage.setItem('currentCartItems', JSON.stringify(cartItems));
                    this.loadCartItems(cartData);
                    break;
                }
            }
        }
    }

    public removeTicket(item, e) {
        e.preventDefault();
        if (this.state[item.eventTicketAttributeId] > 0) {
            var cartData = this.state.cartData;
            for (var i = 0; i < cartData.length; i++) {
                if (cartData[i].eventTicketAttributeId == item.eventTicketAttributeId) {
                    cartData[i].quantity = --(item.quantity);
                    cartData[i].guestList.pop();
                    var cartItems = cartData.filter(function (val) {
                        return val.quantity > 0
                    });
                    localStorage.setItem('currentCartItems', JSON.stringify(cartItems));
                    this.loadCartItems(cartData);
                    break;
                }
            }
        }
    }

    public setGuestInformation(event, guestId, info, e) {
        var cartData = this.state.cartData;
        for (var i = 0; i < cartData.length; i++) {
            if (cartData[i].eventTicketAttributeId == event.eventTicketAttributeId) {

                cartData[i].guestList[guestId][info] = e.target.value;
                var cartItems = cartData.filter(function (val) {
                    return val.quantity > 0
                });
                localStorage.setItem('currentCartItems', JSON.stringify(cartItems));
                break;
            }
        }
    }

    public continueToItinerary() {
        if (!this.state.dateSelected) {
            alert("Please select date");
        } else {
            var categories = [];
            this.state.cartData.forEach(function (val) {
                if (val.guestList.length > 0) {
                    val.guestList.forEach(function (guest) {
                        categories.push(guest);
                    });
                }
            });
            if (categories.length == 0) {
                alert("Please add tickets");
            } else {
                var guestList = [];
                this.state.cartData.forEach(function (val) {
                    if (val.guestList.length > 0) {
                        val.guestList.forEach(function (guest) {
                            if ((guest.firstName != "" && guest.lastName != "" && guest.nationality != "" && guest.documentTypeId != "" && guest.documentNumber != "")) {
                                guestList.push(guest);
                            }
                        })
                    }
                });
                if (guestList.length < categories.length) {
                    alert("Please fill all guests details");
                } else {
                    var allCartItems = (localStorage.getItem('cartItems') != null && localStorage.getItem('cartItems') != '0') ? JSON.parse(localStorage.getItem("cartItems")) : [];
                    var currentCartItems = (localStorage.getItem('currentCartItems') != null && localStorage.getItem('currentCartItems') != '0') ? JSON.parse(localStorage.getItem("currentCartItems")) : [];
                    currentCartItems.forEach(function (val) {
                        allCartItems.push(val);
                    });
                    localStorage.setItem('cartItems', JSON.stringify(allCartItems));
                    PubSub.publish('UPDATE_NAV_CART_DATA_EVENT', 1);
                    this.props.goToItinerary();
                }
            }
        }
    }

    public render() {
        var ticketData = this.state.cartData;
        var data = this.state.cartData;
        var Quantity = 0;
        var that = this;

        var countries = this.props.countries.map((item) => {
            return <option value={item.phonecode}>{item.name}</option>
        });

        var ticketCategory = data.map(function (val) {
            var category = val.eventTicketAttributeId;
            var ticketQuantity = that.state[category];
            Quantity += ticketQuantity;
            var symbol;
            if (val.currencyName == "INR") {
                symbol = "";
            }
            else if (val.currencyName == "GBP") {
                symbol = "";
            }
            else if (val.currencyName == "USD") {
                symbol = "$";
            }
            else if (val.currencyName == "EURO") {
                symbol = "";
            }
            else {
                symbol = "";
            }
            var guestInformation = val.guestList.map(function (guest) {
                return <div className="col-sm-12 pt-3">
                    <form>
                        <div className="form-row mt-2 pl-2 pr-2 mb-2">
                            <div style={{lineHeight:"26px"}}>
                                Guest {guest.guestId}:
                            </div>
                            <div className="form-group col-md-2 mb-2">
                                <input type="text" className="form-control form-control-sm" onChange={that.setGuestInformation.bind(that, val, (guest.guestId - 1), "firstName")} placeholder="First Name" required />
                            </div>
                            <div className="form-group col-md-2 mb-2">
                                <input type="text" className="form-control form-control-sm" onChange={that.setGuestInformation.bind(that, val, (guest.guestId - 1), "lastName")} placeholder="Last Name" required />
                            </div>
                            <div className="form-group col-md-2 mb-2">
                                <select id="inputState" className="form-control form-control-sm" onChange={that.setGuestInformation.bind(that, val, (guest.guestId - 1), "nationality")} required >
                                    <option>Nationality</option>
                                    {countries}
                                </select>
                            </div>
                            <div className="form-group col-md-2 mb-2">
                                <select id="inputState" className="form-control form-control-sm" onChange={that.setGuestInformation.bind(that, val, (guest.guestId - 1), "documentTypeId")} required>
                                    <option>Document Type</option>
                                    <option value="1">Passport</option>
                                    <option value="2">Driving Licence</option>
                                    <option value="3">Aadhar</option>
                                    <option value="4">Voter ID</option>
                                </select>
                            </div>
                            <div className="form-group col-md-2 mb-2">
                                <input type="number" pattern="[0-9]*" className="form-control form-control-sm" onChange={that.setGuestInformation.bind(that, val, (guest.guestId - 1), "documentNumber")} placeholder="ID Number" required />
                            </div>
                        </div>
                    </form>
                </div>
            });
            return <div>
                <hr />
                <div className="row">
                    <div className="col-sm-6 col-lg-7">
                        <span className="h6">{val.ticketCategoryName}</span><i className="fa fa-question-circle ml-2 text-primary" data-toggle="tooltip" data-placement="top" title={val.ticketCategoryDescription}></i>
                    </div>
                    <div className="col-sm-6 col-lg-5 text-lg-right">
                        <span className="pr-2"></span>
                        <span className="pink-color pr-3 h5">{symbol + numeral(val.pricePerTicket).format('00.00')}</span>
                        <div className="add-to-cart-btns">
                            <a href="javascript:void(0)" className="rounded-circle border decrease-iten" onClick={that.removeTicket.bind(that, val)}>-</a>
                            <input className="form-control velue-item mr-1 ml-1" placeholder={ticketQuantity} type="text" />
                            <a href="javascript:void(0)" className="rounded-circle border increase-item" onClick={that.addTicket.bind(that, val)}>+</a>
                        </div>
                    </div>
                    {guestInformation}
                </div>
            </div>;
        });
        var subEventData = this.props.ticketCategoryData.eventDetail.filter(function (val) {
            return val.name == that.props.eventName;
        })
        if (subEventData.length == 0) {
            return <div></div>;
        }
        let maxDate = subEventData.reduce(function (a, b) { return a.startDateTime > b.startDateTime ? a : b; });
        let minDate = subEventData.reduce(function (a, b) { return a.startDateTime < b.startDateTime ? a : b; });
        return <div>
            <section className="ticket-details bg-light pt-5 pb-5">
                <div className="container">
                    <h5 onClick={this.toggleCalender.bind(this)} className="mb-3 pink-color text-center"><i className="fa fa-calendar-alt"></i> <a href="javascript:void(0)" className="pink-color btn-lg btn-link p-0"> Select Date</a></h5>
                    {this.state.calenderVisible && <div className="calendar-picker">
                        <Calendar className="m-auto"
                            onChange={this.showTime.bind(this)}
                            minDate={new Date(new Date(minDate.startDateTime).getFullYear(), new Date(minDate.startDateTime).getMonth(), new Date(minDate.startDateTime).getDate())}
                            maxDate={new Date(new Date(maxDate.startDateTime).getFullYear(), new Date(maxDate.startDateTime).getMonth(), new Date(maxDate.startDateTime).getDate())} /> <br />
                    </div>}
                    {this.state.showTicketType && <div>
                        <h5 className="mb-3 pink-color">Select Ticket Type</h5>
                        <div className="cat-selection pb-3">
                            <div className="custom-control custom-radio mb-2">
                                <input type="radio" id="customRadio1" name="customRadio" className="custom-control-input" checked />
                                <label className="custom-control-label">Regular / Single Monument <i className="fa fa-question-circle ml-2 text-primary" data-toggle="tooltip" data-placement="top" title="Regular / Single Monuments"></i></label>
                            </div>
                            <div className="custom-control custom-radio mb-2 opacity-disabled">
                                <input type="radio" id="customRadio2" name="customRadio" className="custom-control-input" />
                                <label className="custom-control-label">Composite - Multiple Monument <span className="text-success d-none">Save up to x%</span> <i className="fa fa-question-circle ml-2 text-primary" data-toggle="tooltip" data-placement="top" title="Composite - Multiple Monuments"></i></label>
                            </div>
                        </div>
                        {ticketCategory}
                    </div>}
                    <hr />
                    <div className="footer-bottom text-center pt-4">
                        {(Quantity <= 0) && <a href="javascript:void(0)" className="btn-lg btn btn-light opacity-disabled">Continue</a>}
                        {(Quantity > 0) && <a href="javascript:void(0)" onClick={this.continueToItinerary.bind(this)} className="btn-lg site-primery-btn">Continue</a>}
                    </div>
                </div>
            </section>
        </div>;
    }
}


