import { Form, Formik } from "formik";
import * as React from "react";
import { Link, RouteComponentProps } from "react-router-dom";
import { Button } from "react-bootstrap";
import "../../views/Checkout.scss";

interface IUserCheckoutProps {
    onSubmit: (values: any) => void;
    onClick: () => void;
    initialValues: any;
    validationSchema: any; // TODO: figure out where Yup SCHEMA went
    hideSubmit?: boolean;
}

export default class UserCheckout extends React.Component<IUserCheckoutProps, any> {
    public render() {
        return <Formik
            onSubmit={this.props.onSubmit}
            initialValues={this.props.initialValues || {}}
            validationSchema={this.props.validationSchema}
        >
            {(props) => (<Form>
                {this.props.children}
                {!this.props.hideSubmit &&
                    <div>
                        <button type="submit" className="btn btn-block site-primery-btn">Sign in</button>
                        <div className="text-center pt-2">
                            <a onClick={this.props.onClick} href="#"><small>Forgot password?</small></a>
                            <p className="pt-3">Dont have an account? <Link to="/register">Sign up</Link></p>
                        </div>
                    </div>
                }
            </Form>
            )}
        </Formik>
    }
}



