import { autobind } from "core-decorators";
import * as React from "react";
import * as ReactDOM from "react-dom";
import { connect } from "react-redux";
import { CartDataViewModel, CartItem } from "../../models/Cart/CartDataViewModel";
import { Link, RouteComponentProps } from "react-router-dom";
import * as numeral from "numeral";
import * as PubSub from 'pubsub-js';

export default class ItineraryComponent extends React.Component<any, any> {
    public constructor() {
        super();
        this.state = { cartData: "", enableCheckout: false };
    }

    public componentDidMount() {
        if (localStorage.getItem('cartItems') != null && localStorage.getItem('cartItems') != '0') {
            var data = JSON.parse(localStorage.getItem("cartItems"));
            this.loadCartItems(data);
            this.setState({ cartData: data, enableCheckout: true });
        }
        PubSub.subscribe('UPDATE_CART_DATA_EVENT', this.subscriberData.bind(this));
    }

    public subscriberData(msg, data) {
        var that = this;
        if (localStorage.getItem('cartItems') != null && localStorage.getItem('cartItems') != "" && localStorage.getItem('cartItems') != '0') {
            var data = JSON.parse(localStorage.getItem('cartItems'));
            this.setState({ cartData: data });
        }
        else {
            this.setState({ cartData: "" });
        }
        if ((window as any).location.href.indexOf("payment") > -1) {
            this.setState({ isPaymentPage: true });
        }
    }

    public loadCartItems(data) {
        var that = this;
        if (data != "") {
            this.setState({ cartData: data });
        }
        else {
            this.props.disableCheckout();
            this.setState({ cartData: "" });
        }
    }

    public removeItemFromCart(item, e) {
        e.preventDefault();
        var cartData = this.state.cartData;
        var cartItems = cartData.filter(function (val) {
            return val.altId != item;
        });
        localStorage.setItem('cartItems', JSON.stringify(cartItems));
        this.loadCartItems(cartItems);
        PubSub.publish('UPDATE_NAV_CART_DATA_ON_CART_REMOVE_EVENT', 1);
    }

    public getTotalCartAmount() {
        var cartData = this.state.cartData;
        var currencies = cartData.map((val) => val.currencyId);
        var currencyTypes = currencies.filter(function (value, index, self) {
            return self.indexOf(value) === index;
        });

        var totalCartAmount = 0;
        if (currencyTypes.length > 1) {
            return (<p></p>);
        } else {
            for (var i = 0; i < cartData.length; i++) {
                totalCartAmount += (cartData[i].quantity * cartData[i].pricePerTicket);
            }
            var symbol;
            if (cartData[0].currencyName == "INR") {
                symbol = "";
            }
            else if (cartData[0].currencyName == "GBP") {
                symbol = "";
            }
            else if (cartData[0].currencyName == "USD") {
                symbol = "$";
            }
            else if (cartData[0].currencyName == "EURO") {
                symbol = "";
            }
            else {
                symbol = "";
            }
            return (<div><div className="col-xs-10">Cart Subtotal</div> <div className="col-xs-2"> {symbol + numeral(totalCartAmount).format('00.00')}</div></div>);
        }
    }

    public showTotal() {
        var cartData = this.state.cartData;
        var currencies = cartData.map((val) => val.currencyId);
        var currencyTypes = currencies.filter(function (value, index, self) {
            return self.indexOf(value) === index;
        });
        var symbol;
        if (cartData[0].currencyName == "INR") {
            symbol = "";
        }
        else if (cartData[0].currencyName == "GBP") {
            symbol = "";
        }
        else if (cartData[0].currencyName == "USD") {
            symbol = "$";
        }
        else if (cartData[0].currencyName == "EURO") {
            symbol = "";
        }
        else {
            symbol = "";
        }
        var totalCartAmount = 0;
        if (currencyTypes.length > 1) {
            return (<b>Your cart contains multiple currencies. Please remove items accordingly</b>);
        } else {
            for (var i = 0; i < cartData.length; i++) {
                totalCartAmount += (cartData[i].quantity * cartData[i].pricePerTicket);
            }
            return (<h4 className="m-0">Total: {symbol + numeral(totalCartAmount).format('00.00')}</h4>);
        }
    }

    public render() {
        var data = this.state.cartData;
        var that = this;
        var isMultipleCurrencies = false;
        if (data != "") {
            // To sort according to date and time to create itinerary
            data.sort(function (a, b) {
                var keyA = new Date(a.eventStartDate),
                    keyB = new Date(b.eventStartDate);
                if (keyA < keyB) return -1;
                if (keyA > keyB) return 1;
                return 0;
            });
            var currencies = data.map((val) => val.currencyId);
            var currencyTypes = currencies.filter(function (value, index, self) {
                return self.indexOf(value) === index;
            });
            if (currencyTypes.length > 1) {
                isMultipleCurrencies = true;
            }
            var unique = Array.from(new Set(data.map(item => item.altId)));
            var cartItems = unique.map(function (altId) {
                var categories = data.filter(function (cat) {
                    return cat.altId == altId;
                });
                var categoryCost = 0;
                var categoryData = categories.map(function (val) {
                    categoryCost += (val.quantity * val.pricePerTicket);
                    var symbol;
                    if (val.currencyName == "INR") {
                        symbol = "";
                    }
                    else if (val.currencyName == "GBP") {
                        symbol = "";
                    }
                    else if (val.currencyName == "USD") {
                        symbol = "$";
                    }
                    else if (val.currencyName == "EURO") {
                        symbol = "";
                    }
                    else {
                        symbol = "";
                    }
                    return <p className="m-0">
                        <small>{val.ticketCategoryName + " (" + val.quantity + " x " + symbol + numeral(val.pricePerTicket).format('00.00') + ")"}
                            <span className="float-right">{symbol + numeral((val.quantity * val.pricePerTicket)).format('00.00')}</span>
                        </small>
                    </p>;
                });
                var eventInformation = categories[0];
                var symbol;
                if (eventInformation.currencyName == "INR") {
                    symbol = "";
                }
                else if (eventInformation.currencyName == "GBP") {
                    symbol = "";
                }
                else if (eventInformation.currencyName == "USD") {
                    symbol = "$";
                }
                else if (eventInformation.currencyName == "EURO") {
                    symbol = "";
                }
                else {
                    symbol = "";
                }
                return <div>
                    <div className="media">
                        <img className="mr-3 d-none d-sm-block" src={"https://static1.feelaplace.com/images/event/HotTickets/" + eventInformation.altId.toUpperCase() + "-ht-c1.jpg"} alt="Generic placeholder image" width="120" height="110" />
                        <div className="media-body">
                            <div className="mb-2">
                                {eventInformation.name}
                                <span className="pink-color pl-2"><i className="fa fa-calendar"></i> {new Date(eventInformation.eventStartDate).toDateString().split(' ').join(', ').toUpperCase() + " | " + numeral(new Date(eventInformation.eventStartDate).getHours()).format('00') + ":" + numeral(new Date(eventInformation.eventStartDate).getMinutes()).format('00')}</span>
                                <div className="float-md-right">
                                    <a href="#" className="btn btn-sm pink-color" onClick={that.removeItemFromCart.bind(that, altId)}>Remove</a>
                                    {symbol + numeral(categoryCost).format('00.00')}
                                </div>
                            </div>
                            <div className="text-muted">
                                <p className="m-0">
                                    <small>
                                        <i className="fa fa-map-marker pink-color"></i> {eventInformation.venue + ", " + eventInformation.city} -
								<a href="#"><i className="fa fa-map-marker ml-1"></i> <Link to={"https://www.google.com/maps/search/" + eventInformation.venue} target="_blank">View on Map</Link></a>
                                    </small>
                                </p>
                                {categoryData}
                                <p className="m-0"><small><a href="javascript:void(0)"><i className="fa fa-file-text"></i> Terms &amp; Conditions</a></small></p>
                            </div>
                        </div>
                    </div>
                    {altId != unique[unique.length - 1] && < hr />}
                </div>;
            })
            return <div>
                <div className="card-body">
                    {cartItems}
                </div>
                <div className="card-body border-top text-right">
                    {this.showTotal()}
                </div>
                {this.state.enableCheckout && <div className="card-footer gradient-bg text-right">
                    <Link to="/" className="btn btn-outline-primary text-uppercase mr-1">Feel More</Link>
                    {(isMultipleCurrencies === false) && <Link to="/checkout" className="btn site-primery-btn text-uppercase">Place Order</Link>}
                </div>
                }
            </div>;
        } else {
            return <div className="card-body">
                No items in your itinerary
  			</div>;
        }
    }
}


