import * as React from "React";
import MonumentsTickets from "../Home/MonumentsTickets";

export default class SimilarListingEvents extends React.Component<any, any> {
    public render() {
        var similarListings = this.props.similarListings.slice(0, 4);
        return <div>
            <MonumentsTickets isAuthenticated={this.props.isAthenticated} islearnMore={true} push={() => { this.props.goToLoginPage() }} filterKeyword={""} categoryData={similarListings} />            
        </div>;
    }
}



