import * as React from "React";
import MonumentsTickets from "../Home/MonumentsTickets";
import WishList from "../Home/WishList";

export default class BespokeItinerary extends React.Component<any, any> {
    public constructor() {
        super();
        this.state = { bespokeData: "" };
    }

    public componentDidMount() {
        if (localStorage.getItem('bespokeItems') != null && localStorage.getItem('bespokeItems') != '0') {
            var data = JSON.parse(localStorage.getItem("bespokeItems")).filter(function (val) { return val.userAltId == localStorage.getItem('userToken') });
            var wishlistData = [];
            data.forEach(function (val) { wishlistData.push(val.cartItems) });
            this.loadBespokeItems(wishlistData);
            this.setState({ bespokeData: wishlistData });
        }
    }

    public loadBespokeItems(data) {
        var that = this;
        if (data != "") {
            this.setState({ bespokeData: data });
        }
        else {
            this.setState({ bespokeData: "" });
        }
    }

    public render() {
        var data = this.state.bespokeData;
        var that = this;
        if (data != "") {
            return <div className="pt-4">
                <h4>
                    Your feelList
                </h4>
                <WishList islearnMore={true} filterKeyword={""} categoryData={data} />
                <div className="clearfix"></div>
            </div>;
        } else {
            return <div>
            </div>;
        }
    }
}



