import { Field } from "formik";
import * as React from "react";
import { Link, RouteComponentProps } from "react-router-dom";
import RegisterFeelFormSignUp from "./RegisterFeelFormSignUp";
import { RegistrationFormDataViewModel } from "shared/models/RegistrationFormDataViewModel";
import CountryDataViewModel from "../../models/Country/CountryDataViewModel"
import Yup from "yup";

interface IRegisterFormProps {
    onSubmit: (values: RegistrationFormDataViewModel) => void;
    countryData: CountryDataViewModel;
    redirectLink: string;
}

export class RegisterFeelForm extends React.Component<IRegisterFormProps, {}> {
    public render() {
        const schema = this.getSchema();
        var countries = this.props.countryData.countries.map((item) => {
            return <option value={item.phonecode}>{item.name}</option>
        });
        return (
            <RegisterFeelFormSignUp {...this.props} validationSchema={schema} initialValues={{}}>
                <div className="form-group">
                    <Field type="firstName" name="firstName" placeholder="First Name" className="form-control" required />
                </div>
                <div className="form-group">
                    <Field type="lastName" name="lastName" placeholder="Last Name" className="form-control" required />
                </div>
                <div className="form-group">
                    <Field type="email" name="email" placeholder="Email address" className="form-control" required />
                </div>
                <div className="form-group">
                    <Field type="password" name="password" placeholder="Password" className="form-control" required />
                </div>
                <div className="form-group">
                    <Field type="password" name="confirmPassword" placeholder="Confirm Password" className="form-control" required />
                </div>
                <div className="form-group">
                    <div className="custom-control custom-radio custom-control-inline">
                        <input type="radio" id="male" name="gender" className="custom-control-input" />
                        <label htmlFor="male" className="custom-control-label" >Male</label>
                    </div>
                    <div className="custom-control custom-radio custom-control-inline">
                        <input type="radio" id="female" name="gender" className="custom-control-input" />
                        <label htmlFor="female" className="custom-control-label">Female</label>
                    </div>
                </div>
                <div className="form-group">
                    <select className="custom-select">
                        <option selected>Resident of</option>
                        {countries}
                    </select>
                </div>
                <div className="form-group">
                    <select className="custom-select">
                        <option selected>Citizen of</option>
                        {countries}
                    </select>
                </div>
                <div className="custom-control custom-checkbox my-1 mb-3">
                    <input type="checkbox" className="custom-control-input" id="receiveCommunication" />
                    <label htmlFor="receiveCommunication" className="custom-control-label" > <small>Id like to receive marketing and policy communications from feel</small> </label>
                </div>
            </RegisterFeelFormSignUp>
        );
    }

    private getSchema() {
        return Yup.object().shape({
            firstName: Yup.string().required(),
            lastName: Yup.string().required(),
            email: Yup.string().email().required(),
            password: Yup.string().required(),
            confirmPassword: Yup.string().required()
        });
    }
}



