import * as React from "react";
import { Link, RouteComponentProps } from "react-router-dom";
import { ForgotPasswordForm } from "../form/ForgotPasswordForm";

export class ForgotPasswordComponent extends React.Component<any, any> {
    public render() {
        return <div>
            <p>Enter the email address associated with your account, and we will email you a link to reset your password.</p>
            <ForgotPasswordForm onSubmit={this.props.submitForgotPassword} onClick={this.props.switchPage} />
            {this.props.invalidCredentials && <span className="text-danger"><p>Email Id you entered is Invalid. Please try again.</p></span>}
            {this.props.passwordResetMailSent && <span className="validate-txt"><p style={{ color: "#5bac30" }}>Password reset link is successfully sent to your email address. Please check your mail</p></span>}
        </div>;
    }
}



