import { autobind } from "core-decorators";
import * as React from "react";
import { connect } from "react-redux";
import { Link, RouteComponentProps } from "react-router-dom";
import * as GoogleLogin from "react-google-login-component";
import { GoogleSignInResponseViewModel } from "../../../models/SocialSignIn/GoogleSignInResponseViewModel"
import { GoogleSignInFormDataViewModel } from "../../../models/SocialSignIn/GoogleSignInFormDataViewModel";
import { IApplicationState } from "../../../stores";
import * as LoginGoogleStore from "../../../stores/LoginGoogle";
//1085443601509-sr1ndfe7fn35m84ret0cavjgj3n4fnbv.apps.googleusercontent.com - Production
//1085443601509-gs90kgi1ptdh26ddtj7n5fv863c8qvp5.apps.googleusercontent.com - Local
type LoginProps = LoginGoogleStore.ILoginState & typeof LoginGoogleStore.actionCreators & RouteComponentProps<{}>;
class SignInGoogle extends React.Component<any, any> {
    render() {
        return (
            <div>
                <GoogleLogin.GoogleLogin socialId="1085443601509-sr1ndfe7fn35m84ret0cavjgj3n4fnbv.apps.googleusercontent.com"
                    className="btn btn-outline-danger btn-block fa-google-cus mt-2"
                    scope="profile"
                    fetchBasicProfile={true}
                    responseHandler={this.responseGoogle}
                    buttonText="Sign in with Google"
                />
                </div>
        );
    }
    
    @autobind
    responseGoogle(googleUser) {
        var id_token = googleUser.getAuthResponse().id_token;
        var googleId = googleUser.getId();
        var userData = googleUser.getBasicProfile();
        var res = new GoogleSignInFormDataViewModel();
        res.email = userData.U3;
        res.firstName = userData.ofa;
        res.lastName = userData.wea;
        res.SocialLoginId = googleId;
        this.props.googleLogin(res, (response: GoogleSignInResponseViewModel) => {
            if (response.success) {
                localStorage.setItem('userToken', response.session.user.altId);   
                (window as any).location.replace("/");
              }
          });       
    }
}
export default connect(
    (state: IApplicationState) => state.LoginGoogle,
    LoginGoogleStore.actionCreators
)(SignInGoogle);



