import * as React from "react";
import { Field } from "formik";
import * as SearchStore from "../../stores/Search";
import { autobind } from "core-decorators";
import { IApplicationState } from "../../stores/index";
import CategorySearchResult from "../Search/CategorySearchResult";
import CitySearchResult from "../Search/CitySearchResult";
import StateSearchResult from "../Search/StateSearchResult";
import CountrySearchResult from "../Search/CountrySearchResult";
import { connect } from "react-redux";
import * as isEmpty from "lodash/isEmpty";

export default class Search extends React.Component<any, any> {
    public componentWillMount() {
        this.setState({ defaultSearchValue: "" });
    }    

    public getSearchResults(e) {
        if (e.target.value == "") {
            this.props.clearSearchAction();
        } else if (e.target.value.length >= 3) {
            this.props.searchAction(e.target.value, this.props.siteLevel);
        }
    }

    public setCountrySearchKeyword(search, e) {
        this.props.setCountrySearchKeyword(search);
        this.setState({ defaultSearchValue: search });
    }

    public setStateSearchKeyword(search, e) {
        this.props.setStateSearchKeyword(search);
        this.setState({ defaultSearchValue: search });
    }

    public setCitySearchKeyword(search, e) {
        this.props.setCitySearchKeyword(search);
        this.setState({ defaultSearchValue: search });
    }

    public render() {
        let anyResults = !isEmpty(this.props.searchResult.categoryEvents)
            || !isEmpty(this.props.searchResult.cities)
            || !isEmpty(this.props.searchResult.countries)
            || !isEmpty(this.props.searchResult.states);
        let showResults = (this.props.searchSuccess || this.props.clearSearchSuccess) && anyResults;
        return <div className="form-group dropdown">
            <img src="https://static1.feelaplace.com/images/header/search-icon.png" alt="search" title="search icon" className="search-icon" />
            <input type="text" className="form-control border" aria-describedby="FeelJaipur" placeholder="feel a place" defaultValue={this.state.defaultSearchValue} id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false" onChange={this.getSearchResults.bind(this)} />
            <div className="search-dropdown bg-white p-3 border dropdown-menu" aria-labelledby="dropdownMenuButton" x-placement="bottom-start">                    
                {showResults && this.props.siteLevel == 4 && <CountrySearchResult searchSuccess={this.props.searchSuccess} emptySearch={this.props.clearSearchSuccess} searchResult={this.props.searchResult.countries} setCountrySearchKeyword={this.setCountrySearchKeyword.bind(this)} defaultCountries={this.props.defaultCountries} />}
                {showResults && this.props.siteLevel >= 3 && <StateSearchResult searchSuccess={this.props.searchSuccess} emptySearch={this.props.clearSearchSuccess} searchResult={this.props.searchResult.states} setStateSearchKeyword={this.setStateSearchKeyword.bind(this)} defaultStates={this.props.defaultStates} /> }
                {showResults && this.props.siteLevel >= 2 && <CitySearchResult searchSuccess={this.props.searchSuccess} emptySearch={this.props.clearSearchSuccess} searchResult={this.props.searchResult.cities} setCitySearchKeyword={this.setCitySearchKeyword.bind(this)} defaultCities={this.props.defaultCities} /> }                   
                {showResults && <CategorySearchResult searchSuccess={this.props.searchSuccess} emptySearch={this.props.clearSearchSuccess} searchResult={this.props.searchResult.categoryEvents} />}
            </div>
        </div>;
    }

}



