import { fetch, addTask } from "domain-task";
import { Action, ActionCreator, Reducer } from "redux";
import { LoginFormDataViewModel } from "shared/models/LoginFormDataViewModel";
import { LoginResponseViewModel } from "shared/models/LoginResponseViewModel";
import { ForgotPasswordFormDataViewModel } from "shared/models/ForgotPasswordFormDataViewModel";
import { ForgotPasswordResponseViewModel } from "shared/models/ForgotPasswordResponseViewModel";
import { sessionService } from "shared/services/session";
import {
    actionCreators as sessionActionCreators,
    IGetSessionReceivedAction,
    IGetSessionRequestAction
} from "shared/stores/Session";
import { IAppThunkAction } from "./";

export const LOGIN_REQUEST = "LoginRequestAction";
export const LOGIN_SUCCESS = "LoginSuccessAction";
export const LOGIN_INVALID = "LoginInvalidAction";
export const LOGIN_ERROR = "LoginErrorAction";

export const FORGOT_PASSWORD_REQUEST = "ForgotPasswordRequestAction";
export const FORGOT_PASSWORD_SUCCESS = "ForgotPasswordSuccessAction";
export const FORGOT_PASSWORD_INVALID = "ForgotPasswordInvalidAction";
export const FORGOT_PASSWORD_ERROR = "ForgotPasswordErrorAction";

export const LOGOUT_REQUEST = "LogoutRequestAction";
export const LOGOUT_SUCCESS = "LogoutSuccessAction";
export const LOGOUT_ERROR = "LogoutErrorAction";

export interface ILoginState {
    authenticating: boolean;
    authenticated: boolean;
    invalidCredentials: boolean;
    invalidEmail: boolean;
    changePasswordMailSent: boolean;
}

const DefaultLoginState: ILoginState = {
    authenticating: false,
    authenticated: false,
    invalidCredentials: false,
    invalidEmail: false,
    changePasswordMailSent: false,
};

interface ILoginRequestAction {
    type: "LoginRequestAction";
}

interface ILoginErrorAction {
    type: "LoginErrorAction";
}

export interface ILoginSuccessAction {
    type: "LoginSuccessAction";
}

interface ILoginInvalidAction {
    type: "LoginInvalidAction";
}

interface IForgotPasswordRequestAction {
    type: "ForgotPasswordRequestAction";
}

interface IForgotPasswordErrorAction {
    type: "ForgotPasswordErrorAction";
}

export interface IForgotPasswordSuccessAction {
    type: "ForgotPasswordSuccessAction";
}

interface IForgotPasswordInvalidAction {
    type: "ForgotPasswordInvalidAction";
}

interface ILogoutErrorAction {
    type: "LogoutErrorAction";
}

interface IAuthenticatedAction {
    type: "AuthenticatedAction";
    idToken: any;
    authToken: string;
}

interface ILogoutRequestAction {
    type: "LogoutRequestAction";
}

interface ILogoutSuccessAction {
    type: "LogoutSuccessAction";
}

type KnownAction = ILoginRequestAction | ILoginErrorAction | ILoginSuccessAction |
    IForgotPasswordRequestAction | IForgotPasswordErrorAction | IForgotPasswordSuccessAction | IForgotPasswordInvalidAction |
    ILoginInvalidAction | ILogoutRequestAction | ILogoutErrorAction | ILogoutSuccessAction;

export const actionCreators = {
    login: (loginInput: LoginFormDataViewModel, callback: (LoginResponseViewModel) => void)
        : IAppThunkAction<KnownAction> => async (dispatch, getState) => {

            dispatch({ type: LOGIN_REQUEST });

            sessionService.login(loginInput)
                .then((response: LoginResponseViewModel) => {
                    if (response.success == true && response.session.isAuthenticated == true) {
                        dispatch({ type: LOGIN_SUCCESS });
                        dispatch(sessionActionCreators.getSession(response.session) as any);
                    } else if (response.success == false && response.session.isAuthenticated == false) {
                        dispatch({ type: LOGIN_INVALID });
                    }
                    callback(response);
                },
                (error) => {
                    dispatch({ type: LOGIN_ERROR });
                });
        },

    forgotPassword: (forgotPasswordInput: ForgotPasswordFormDataViewModel, callback: (ForgotPasswordResponseViewModel) => void)
        : IAppThunkAction<KnownAction> => async (dispatch, getState) => {
            dispatch({ type: FORGOT_PASSWORD_REQUEST });
            sessionService.forgotPassword(forgotPasswordInput)
                .then((response: ForgotPasswordResponseViewModel) => {
                    if (response.success == true && response.isExisting == true) {
                        dispatch({ type: FORGOT_PASSWORD_SUCCESS });
                    } else if (response.success == true && response.isExisting == false) {
                        dispatch({ type: FORGOT_PASSWORD_INVALID });
                    }
                    callback(response);
                },
                (error) => {
                    dispatch({ type: FORGOT_PASSWORD_ERROR });
                });
        },
};

export const reducer: Reducer<ILoginState> = (state: ILoginState, action: KnownAction) => {
    switch (action.type) {
        case LOGIN_REQUEST:
            return { authenticating: true, authenticated: false, invalidCredentials: false, changePasswordMailSent: false, invalidEmail: false };
        case LOGIN_ERROR:
            return { authenticating: false, authenticated: false, invalidCredentials: false, changePasswordMailSent: false, invalidEmail: false };
        case LOGIN_SUCCESS:
            return { authenticating: false, authenticated: true, invalidCredentials: false, changePasswordMailSent: false, invalidEmail: false };
        case LOGIN_INVALID:
            return { authenticating: false, authenticated: false, invalidCredentials: true, changePasswordMailSent: false, invalidEmail: false };
        case FORGOT_PASSWORD_REQUEST:
            return { authenticating: true, authenticated: false, invalidCredentials: false, changePasswordMailSent: false, invalidEmail: false };
        case FORGOT_PASSWORD_ERROR:
            return { authenticating: false, authenticated: false, invalidCredentials: false, changePasswordMailSent: false, invalidEmail: false };
        case FORGOT_PASSWORD_SUCCESS:
            return { authenticating: false, authenticated: false, invalidCredentials: false, changePasswordMailSent: true, invalidEmail: false };
        case FORGOT_PASSWORD_INVALID:
            return { authenticating: false, authenticated: false, invalidCredentials: false, changePasswordMailSent: false, invalidEmail: true };
        case LOGOUT_REQUEST:
            return { authenticating: true, authenticated: true, invalidCredentials: false, changePasswordMailSent: false, invalidEmail: false };
        case LOGOUT_ERROR:
            return { authenticating: false, authenticated: true, invalidCredentials: false, changePasswordMailSent: false, invalidEmail: false };
        case LOGOUT_SUCCESS:
            return { authenticating: false, authenticated: false, invalidCredentials: false, changePasswordMailSent: false, invalidEmail: false };
        default:
            const exhaustiveCheck: never = action;
    }
    return state || DefaultLoginState;
};



