import { fetch, addTask } from "domain-task";
import { Action, Reducer, ActionCreator } from "redux";
import { IAppThunkAction } from "./";
import { TicketCategoryResponseViewModel } from "../models/TicketCategoryResponseViewModel";
import SubEventTicketCategoryResponseViewModel from "../models/SubEventTicketCategoryResponseViewModel";
import CountryDataViewModel from "../models/Country/CountryDataViewModel";
import { CategoryDataModel } from "../models/CategoryDataModel";
import { CategoryResponseViewModel } from "../models/CategoryResponseViewModel";
import { CategoryViewModel } from "../models/CategoryViewModel";
import { CategoryPageResponseViewModel } from "../models/CategoryPageResponseViewModel";

export const GET_COUNTRY_LIST_REQUEST = "TICKET_CATEGORY_GET_COUNTRY_LIST_REQUEST";
export const GET_COUNTRY_LIST_SUCCESS = "TICKET_CATEGORY_GET_COUNTRY_LIST_SUCCESS";
export const GET_COUNTRY_LIST_FAILURE = "TICKET_CATEGORY_GET_COUNTRY_LIST_FAILURE";

export interface ITicketCategoryPageState {
    enableCheckout?: boolean;
    requesting?: boolean;
	currentPage?: string;
	currentEventName?: string;
	currentEventDate?: string;
	currentEventTime?: any;
	altId?: string;
    isLoading?: boolean;
    isCategoryLoading?: boolean;
    errors?: any;
	fetchEventSuccess: boolean;
	fetchSubEventSuccess?: boolean;
    data?: string;
    ticketCategories?: TicketCategoryResponseViewModel;
    subevents?: SubEventTicketCategoryResponseViewModel;
    fetchCountriesSuccess?: boolean;
    countryList?: CountryDataViewModel;
    countries?: any;
    requestCountry?: boolean;
    fetchsuccess?: boolean;
    categories: CategoryViewModel[];
    category: ICategoryEventData;
    fetchCategorySuccess?: boolean;   
}

export interface ICategoryEventData {
    category: CategoryViewModel
    categoryEvents: CategoryPageResponseViewModel[];
}

const emptyCountries: CountryDataViewModel = {
    countries: [],
};

interface IRequestTicketCategoryData {
    type: "REQUEST_TICKET_CATEGORY_DATA";
}

interface IReceiveTicketCategoryData {
    type: "RECEIVE_TICKET_CATEGORY_DATA";
    ticketCategories: TicketCategoryResponseViewModel;
}

interface IRequestSubEventTicketCategoryData {
    type: "REQUEST_SUBEVENT_TICKET_CATEGORY_DATA";
}

interface IReceiveSubEventTicketCategoryData {
    type: "RECEIVE_SUBEVENT_TICKET_CATEGORY_DATA";
    subevents: SubEventTicketCategoryResponseViewModel;
}

interface IRequestGetCountryListAction {
    type: "TICKET_CATEGORY_GET_COUNTRY_LIST_REQUEST";
}

interface IReceiveGetCountryListAction {
    type: "TICKET_CATEGORY_GET_COUNTRY_LIST_SUCCESS";
    countryList: CountryDataViewModel;
}

interface IGetCountryListFailureAction {
    type: "TICKET_CATEGORY_GET_COUNTRY_LIST_FAILURE";
    errors: any;
}

interface IRequestCategoryData {
    type: "REQUEST_CATEGORY_DATA";
}

interface IReceiveCategoryData {
    type: "RECEIVE_CATEGORY_DATA";
    categories: CategoryViewModel[];
}

interface IRequestCategoryPageData {
    type: "REQUEST_CATEGORY_PAGE_DATA";
}

interface IReceiveCategoryPageData {
    type: "RECEIVE_CATEGORY_PAGE_DATA";
    category: ICategoryEventData;
}

const emptyCategory: CategoryViewModel = {
    categoryId: 0,
    displayName: "",
    slug: "",
    eventCategory: null,
    order: null,
    isHomePage: false,
    isFeel: true,
    subCategories: []
};

type KnownAction = IRequestTicketCategoryData | IReceiveTicketCategoryData | IRequestSubEventTicketCategoryData | IReceiveSubEventTicketCategoryData
    | IRequestGetCountryListAction | IReceiveGetCountryListAction | IGetCountryListFailureAction | IRequestCategoryData | IReceiveCategoryData | IRequestCategoryPageData | IReceiveCategoryPageData ;

export const actionCreators = {
    getTicketCategory: (eventAltId: string): IAppThunkAction<KnownAction> => (dispatch, getState) => {
        const fetchTask = fetch(`api/ticketcategories/${eventAltId}`)
            .then((response) => response.json() as Promise<TicketCategoryResponseViewModel>)
            .then((data) => {
                dispatch({ type: "RECEIVE_TICKET_CATEGORY_DATA", ticketCategories: data });
            });
        addTask(fetchTask);
        dispatch({ type: "REQUEST_TICKET_CATEGORY_DATA" });
    },
    getSubEventTicketCategory: (eventid: number): IAppThunkAction<KnownAction> => (dispatch, getState) => {
        const fetchTask = fetch(`api/subevents/${eventid}`)
            .then((response) => response.json() as Promise<SubEventTicketCategoryResponseViewModel>)
            .then((data) => {
                dispatch({ type: "RECEIVE_SUBEVENT_TICKET_CATEGORY_DATA", subevents: data });
            });
        addTask(fetchTask);
        dispatch({ type: "REQUEST_SUBEVENT_TICKET_CATEGORY_DATA" });
    },
    requestCountryData: (): IAppThunkAction<KnownAction> => (dispatch, getState) => {
        const fetchTask = fetch(`api/country/all`)
            .then((response) => response.json() as Promise<CountryDataViewModel>)
            .then((data) => {
                dispatch({ type: "TICKET_CATEGORY_GET_COUNTRY_LIST_SUCCESS", countryList: data, });
            },
            (error) => {
                dispatch({ type: "TICKET_CATEGORY_GET_COUNTRY_LIST_FAILURE", errors: error });
            },
        );
        addTask(fetchTask);
        dispatch({ type: "TICKET_CATEGORY_GET_COUNTRY_LIST_REQUEST" });
    },
    requestCategoryData: (): IAppThunkAction<KnownAction> => (dispatch, getState) => {
        const fetchTask = fetch("api/category")
            .then((response) => response.json() as Promise<CategoryResponseViewModel>)
            .then((data) => {
                dispatch({ type: "RECEIVE_CATEGORY_DATA", categories: data.categories });
            });
        addTask(fetchTask);
        dispatch({ type: "REQUEST_CATEGORY_DATA" });
    },

    getCategoryEvents: (slug: string): IAppThunkAction<KnownAction> => (dispatch, getState) => {
        const fetchTask = fetch(`api/category/${slug}`)
            .then((response) => response.json() as Promise<ICategoryEventData>)
            .then((data) => {
                dispatch({ type: "RECEIVE_CATEGORY_PAGE_DATA", category: data });
            });
        addTask(fetchTask);
        dispatch({ type: "REQUEST_CATEGORY_PAGE_DATA" });

    },
};

const categoryData: ICategoryEventData = {
    category: emptyCategory,
    categoryEvents: undefined
};

const eventTicketCategoryData: TicketCategoryResponseViewModel = {
    event: undefined,
    eventDetail: undefined,
    city: undefined,
    eventTicketAttribute: undefined,
    venue: undefined,
    currencyType: undefined,
    eventTicketDetail: undefined,
    ticketCategory: undefined,    
	eventCategory: "",
	eventCategoryName: "",
};

const eventSubEventTicketCategoryData: SubEventTicketCategoryResponseViewModel = {
    eventDetail: undefined,
    city: undefined,
    eventTicketAttribute: undefined,
    venue: undefined,
    currencyType: undefined,
    eventTicketDetail: undefined,
    ticketCategory: undefined,
};

const unloadedState: ITicketCategoryPageState = {
    ticketCategories: eventTicketCategoryData, subevents: eventSubEventTicketCategoryData, isLoading: false, errors: null, fetchEventSuccess: false,
    requestCountry: false,
    fetchCountriesSuccess: false,
    countryList: emptyCountries,
    categories: [],        
    fetchsuccess: false,
    category: categoryData,
    fetchCategorySuccess: false,
};

export const reducer: Reducer<ITicketCategoryPageState> = (state: ITicketCategoryPageState, incomingAction: Action) => {
    const action = incomingAction as KnownAction;
    switch (action.type) {
        case "REQUEST_TICKET_CATEGORY_DATA":
            return {
                category: state.category,
                fetchCategorySuccess: state.fetchCategorySuccess,
                fetchsuccess: state.fetchsuccess,
                requesting: state.requesting,
                categories: state.categories,
                isCategoryLoading: state.isCategoryLoading,
				ticketCategories: state.ticketCategories,
				altId: state.altId,
                isLoading: true,
                fetchEventSuccess: false,
                countryList: state.countryList,
                fetchCountriesSuccess: state.fetchCountriesSuccess,
            };
        case "RECEIVE_TICKET_CATEGORY_DATA":
            return {
                category: state.category,
                fetchCategorySuccess: state.fetchCategorySuccess,
                fetchsuccess: state.fetchsuccess,
                requesting: state.requesting,
                categories: state.categories,
                isCategoryLoading: state.isCategoryLoading,
				ticketCategories: action.ticketCategories,
				altId: action.ticketCategories.eventDetail[0].AltId,
                isLoading: false,
                fetchEventSuccess: true,
                countryList: state.countryList,
                fetchCountriesSuccess: state.fetchCountriesSuccess,
            };
        case "REQUEST_SUBEVENT_TICKET_CATEGORY_DATA":
            return {
                category: state.category,
                fetchCategorySuccess: state.fetchCategorySuccess,
                fetchsuccess: state.fetchsuccess,
                requesting: state.requesting,
                categories: state.categories,
                isCategoryLoading: state.isCategoryLoading,
				subevents: state.subevents,
				altId: state.altId,
				isLoading: true,
				fetchEventSuccess: false,
                fetchSubEventSuccess: false,
                countryList: state.countryList,
                fetchCountriesSuccess: state.fetchCountriesSuccess,
            };
        case "RECEIVE_SUBEVENT_TICKET_CATEGORY_DATA":
            return {
                category: state.category,
                fetchCategorySuccess: state.fetchCategorySuccess,
                fetchsuccess: state.fetchsuccess,
                requesting: state.requesting,
                categories: state.categories,
                isCategoryLoading: state.isCategoryLoading,
				subevents: action.subevents,
				altId: state.altId,
				isLoading: false,
				fetchEventSuccess: false,
                fetchSubEventSuccess: true,
                countryList: state.countryList,
                fetchCountriesSuccess: state.fetchCountriesSuccess,
            };
        case "TICKET_CATEGORY_GET_COUNTRY_LIST_REQUEST":
            return {
                category: state.category,
                fetchCategorySuccess: state.fetchCategorySuccess,
                fetchsuccess: state.fetchsuccess,
                requesting: state.requesting,
                categories: state.categories,
                isCategoryLoading: state.isCategoryLoading,
                ticketCategories: state.ticketCategories,
                altId: state.altId,
                isLoading: state.isLoading,
                fetchEventSuccess: state.fetchEventSuccess,
                subevents: state.subevents,
                fetchSubEventSuccess: state.fetchSubEventSuccess,
                countryList: state.countryList,
                requestCountry: true,                
                fetchCountriesSuccess: false,
            };
        case "TICKET_CATEGORY_GET_COUNTRY_LIST_SUCCESS":
            return {
                category: state.category,
                fetchCategorySuccess: state.fetchCategorySuccess,
                fetchsuccess: state.fetchsuccess,
                requesting: state.requesting,
                categories: state.categories,
                isCategoryLoading: state.isCategoryLoading,
                ticketCategories: state.ticketCategories,
                altId: state.altId,
                isLoading: state.isLoading,
                fetchEventSuccess: state.fetchEventSuccess,
                subevents: state.subevents,
                fetchSubEventSuccess: state.fetchSubEventSuccess,
                countryList: action.countryList,
                requestCountry: false,                
                fetchCountriesSuccess: true,
            };
        case "TICKET_CATEGORY_GET_COUNTRY_LIST_FAILURE":
            return {
                category: state.category,
                fetchCategorySuccess: state.fetchCategorySuccess,
                fetchsuccess: state.fetchsuccess,
                requesting: state.requesting,
                categories: state.categories,
                isCategoryLoading: state.isCategoryLoading,
                ticketCategories: state.ticketCategories,
                altId: state.altId,
                isLoading: state.isLoading,
                fetchEventSuccess: state.fetchEventSuccess,
                subevents: state.subevents,
                fetchSubEventSuccess: state.fetchSubEventSuccess,
                requestCountry: false,                
                fetchCountriesSuccess: false,
            };
        case "REQUEST_CATEGORY_DATA":
            return {
                ticketCategories: state.ticketCategories,
                altId: state.altId,
                fetchEventSuccess: state.fetchEventSuccess,
                countryList: state.countryList,
                fetchCountriesSuccess: state.fetchCountriesSuccess,
                subevents: state.subevents,
                fetchSubEventSuccess: state.fetchSubEventSuccess,
                categories: state.categories,
                isCategoryLoading: true,
                fetchsuccess: false,
                requesting: false,                            
                category: state.category,                
            };
        case "RECEIVE_CATEGORY_DATA":
            return {
                ticketCategories: state.ticketCategories,
                altId: state.altId,
                fetchEventSuccess: state.fetchEventSuccess,
                countryList: state.countryList,
                fetchCountriesSuccess: state.fetchCountriesSuccess,
                subevents: state.subevents,
                fetchSubEventSuccess: state.fetchSubEventSuccess,
                categories: action.categories,
                isCategoryLoading: false,
                fetchsuccess: true,
                requesting: false,                
                category: state.category,
            };
        case "REQUEST_CATEGORY_PAGE_DATA":
            return {
                ticketCategories: state.ticketCategories,
                altId: state.altId,
                fetchEventSuccess: state.fetchEventSuccess,
                countryList: state.countryList,
                fetchCountriesSuccess: state.fetchCountriesSuccess,
                subevents: state.subevents,
                fetchSubEventSuccess: state.fetchSubEventSuccess,
                category: state.category,
                isCategoryLoading: true,
                fetchCategorySuccess: false,
                fetchsuccess: false,
                requesting: false,                
                categories: state.categories,
            };
        case "RECEIVE_CATEGORY_PAGE_DATA":
            return {
                ticketCategories: state.ticketCategories,
                altId: state.altId,
                fetchEventSuccess: state.fetchEventSuccess,
                countryList: state.countryList,
                fetchCountriesSuccess: state.fetchCountriesSuccess,
                subevents: state.subevents,
                fetchSubEventSuccess: state.fetchSubEventSuccess,
                category: action.category,
                isCategoryLoading: false,
                fetchCategorySuccess: true,
                fetchsuccess: true,
                requesting: false,
                categories: state.categories,
            };
        default:
            const exhaustiveCheck: never = action;
    }
    return state || unloadedState;
};



