import * as React from "react";
import { Field } from "formik";
import { Link, RouteComponentProps } from "react-router-dom";
import KzLoader from "../Loader/KzLoader";
import { ChangeNameForm } from "../Account/form/ChangeNameForm";
import { ChangePasswordForm } from "../Account/form/ChangePasswordForm";
import { ChangePrimaryPhoneForm } from "../Account/form/ChangePrimaryPhoneForm";
import { GetUserProfileDataViewModel } from "../../models/Account/GetUserProfileDataViewModel";

export class UserProfile extends React.Component<any, any> {
    public componentDidMount() {
        if (localStorage.getItem('userToken') != null && localStorage.getItem('userToken') != '0') {
            var user: GetUserProfileDataViewModel = {
                altId: localStorage.getItem('userToken'),
            };
            this.props.getUserProfile(user);
        }
    }

    public render() {
        if (this.props.fetchSuccess) {
            const userProfile = this.props.userProfile.userProfile;
            var countries = this.props.countryData;
            return <div className="col-sm-9 right-act-pnl">
                <h4 className="mb-3 mt-3">My Account</h4>
                <ul className="list-group mt-2">
                    <div className="acnt-row clearfix">
                        <div className="col-sm-12">
                            <div className="row">
                            </div>
                        </div>
                    </div>
                    <li className="list-group-item">
                        <div className="list-group-deta clearfix">
                            <span>Full Name: <span className="text-muted">
                                {userProfile.firstName + " " + userProfile.lastName}</span></span>
                            <span className="badge badge-primary badge-pill" data-toggle="collapse" data-target="#collapseExample" aria-expanded="false" aria-controls="collapseExample"><i className="fa fa-edit"></i> Edit</span>
                        </div>
                        <div className="collapse mt-2" id="collapseExample" >
                            <ChangeNameForm firstName={userProfile.firstName} lastName={userProfile.lastName} onSubmit={this.props.submitChangeName} onCancel={this.props.onCancel} />
                        </div>
                    </li>
                    <li className="list-group-item">
                        <div className="list-group-deta clearfix">
                            <span>Email: <span className="text-muted"> {userProfile.email}</span></span>
                        </div>
                    </li>
                    <li className="list-group-item">
                        <div className="list-group-deta clearfix">
                            <div className="list-group-deta clearfix">
                                <span>Primary Phone: <span className="text-muted">
                                    
                                    {(userProfile.phoneCode == null ? "" : "+" + userProfile.phoneCode) + (userProfile.phoneNumber == null ? "" : userProfile.phoneNumber)}
                                </span></span>
                                <span className="badge badge-primary badge-pill" data-toggle="collapse" data-target="#collapseExample2" aria-expanded="false" aria-controls="collapseExample2"><i className="fa fa-edit"></i> Edit</span>
                            </div>
                            <div className="collapse mt-2" id="collapseExample2">
                                <ChangePrimaryPhoneForm countryData={countries} phoneCode={userProfile.phoneCode} phoneNumber={userProfile.phoneNumber} onSubmit={this.props.submitChangePhone} onCancel={this.props.onCancel} />
                            </div>
                        </div>
                    </li>
                    <li className="list-group-item">
                        <div className="list-group-deta clearfix">
                            <span className="mute">Password: <span className="text-muted">
                                xxxxxxxxxx
                                    </span></span>
                            <span className="badge badge-primary badge-pill" data-toggle="collapse" data-target="#collapseExample3" aria-expanded="false" aria-controls="collapseExample3"><i className="fa fa-edit"></i> Edit</span>
                        </div>
                        <div className="collapse mt-2" id="collapseExample3">
                            <ChangePasswordForm onSubmit={this.props.submitChangePassword} onCancel={this.props.onCancel} />
                            {this.props.passwordMismatch && <span className="validate-txt"><br /><p>Passwords do not match</p></span>}
                        </div>
                        {this.props.invalidPassword && <span style={{ marginBottom: "15px" }} className="validate-txt"><small>Invalid user details</small></span>}
                    </li>
                </ul>
            </div>;
        } else if (this.props.invalidPassword) {
            return <div>
            </div>
        } else if (this.props.updateSuccess) {
            return <div>
            </div>
        } else {
            return <div><KzLoader /></div>;
        }
    }
}



