import { autobind } from "core-decorators";
import * as React from "react";
import { connect } from "react-redux";
import * as ReactDOM from "react-dom";
import { Link, RouteComponentProps } from "react-router-dom";
import { bindActionCreators } from "redux";
import { IApplicationState } from "../stores";
import * as TicketCategoryPageStore from "../stores/TicketCategory";
import KzLoader from "../components/Loader/KzLoader";
import Metatag from "../components/Metatags/Metatag";
import TicketCategorySelection from "../components/TicketCategory/TicketCategorySelection";
import "../scss/_ticket-details.scss";
import "../scss/_custom.scss";
import { StickyContainer, Sticky } from 'react-sticky';

type TicketCategoryComponentProps =
    TicketCategoryPageStore.ITicketCategoryPageState
    & typeof TicketCategoryPageStore.actionCreators
    & RouteComponentProps<{ eventId: string; }>;

class TicketPurchaseSelection extends React.Component<TicketCategoryComponentProps, any> {
    constructor(props) {
        super(props);
        this.state = {
            currentValue: 0,
            categoryState: 0,
            defaultPlace: true,
            catClass: 0,
            sabCatClass: 0,
            className: ""
        }
        this.hideBar = this.hideBar.bind(this);
    }

    public componentWillMount() {
        this.setState({ enableCheckout: false });
        this.props.requestCountryData();
    }

    public componentDidMount() {
        if (window) {
            window.scrollTo(0, 0)
        }

        this.props.getTicketCategory(this.props.match.params.eventId);
        var slug = localStorage.getItem("currentSlug");
        this.props.getCategoryEvents(slug);
        if (!this.props.isCategoryLoading) {
            this.props.requestCategoryData();
        }

        this.setState({ className: "" });
        window.addEventListener('scroll', this.hideBar);
    }

    hideBar() {
        if (window.pageYOffset >= 450 && window.pageXOffset === 0) {
            this.setState({ className: "learn-head-stiky" });
        }
        else {
            this.setState({ className: "" });
        }
    }

    public componentWillReceiveProps(nextProps) {
        if (this.props.fetchEventSuccess && this.props.fetchCountriesSuccess && this.props.fetchsuccess && nextProps != this.props) {
            var that = this;
            var category = 0;
            var subCategory = 0;
            var slug = "monuments-tickets";
            this.props.categories.forEach(function (val) {
                val.subCategories.forEach(function (subcat) {
                    if (subcat.eventCategory.toString() == that.props.ticketCategories.event.eventCategoryId.toString()) {
                        category = val.eventCategory;
                        subCategory = subcat.eventCategory;
                        slug = subcat.slug;
                    }
                })
            })
            this.setState({ catClass: category, sabCatClass: subCategory });
        }
    }

    public disableCheckout() {
        this.setState({ enableCheckout: false });
    }

    public goToItinerary() {
        this.props.history.push({ pathname: "/itinerary" });
    }

    public getSubcategories(index, e) {
        var i = e;
        // XXX: TODO: remove if(e == 0) {} in later releases
        if (e >= 0 && e < 3) {
            this.setState({
                currentValue: e,
                catClass: e,
                sabCatClass: 0
            })
            this.props.getCategoryEvents(this.props.categories[i].subCategories[0].slug);
        }
    }

    public getSubCategoryData(index, e) {
        this.props.getCategoryEvents(index)
        this.setState({ categoryState: 1, sabCatClass: e });
    }

    public render() {
        if (this.props.fetchEventSuccess && this.props.fetchCountriesSuccess && this.props.fetchsuccess) {
            var i = -1;
            var j = -1;
            var that = this;
            const cats = this.props.categories.map((item) => {
                i = i + 1;
                var img;
                var classname;
                if (item.eventCategory == this.state.catClass) {
                    classname = "nav-item mb-2 active";
                    img = "https://static1.feelaplace.com/images/category-tab-icon/" + item.slug + "-active.png"
                } else {
                    classname = "mb-2 opacity-disabled";
                    img = "https://static1.feelaplace.com/images/category-tab-icon/" + item.slug + ".png"
                }
                return (
                    <li className={classname}>
                        <span className="tabs-icon mb-0"><img src={img} alt="" /></span>
                        <p className="tabs-title m-0" >{item.displayName}</p>
                    </li>)
            });
            var currentCat = this.props.categories.filter(function (val) {
                return val.eventCategory == that.state.catClass;
            });
            var subcats: any;
            if (currentCat[0] != undefined) {
                subcats = currentCat[0].subCategories.map((item) => {
                    j = j + 1;
                    var classname;
                    if (item.eventCategory == this.state.sabCatClass) {
                        classname = "btn btn-sm btn-outline-secondary mr-1 ml-1 active"
                    }
                    else {
                        classname = "btn btn-sm btn-outline-secondary mr-1 ml-1"
                    }
                    return <li className={classname}>{item.displayName}</li>
                });
            } else {
                subcats = <li></li>
            }
            const ticketCategoryData = this.props.ticketCategories;
            return <div>
                <Metatag url={this.props.location.pathname} metaContent={ticketCategoryData} title={ticketCategoryData.event.name} />
                <div className="inner-banner">
                    <img src={"https://static1.feelaplace.com/images/event/InnerBanner/" + this.props.match.params.eventId.toUpperCase() + ".jpg"} alt="" className="card-img" />
                    <div className="card-img-overlay">
                        <nav aria-label="breadcrumb">
                            <ol className="breadcrumb">
                                <li className="breadcrumb-item"><a href="#">Home</a></li>
                                <li className="breadcrumb-item"><a href="#">{ticketCategoryData.eventCategoryName}</a></li>
                                <li className="breadcrumb-item"><a href="#">{ticketCategoryData.eventCategory}</a></li>
                                <li className="breadcrumb-item active" aria-current="page"><a href="#">{ticketCategoryData.eventDetail[0].name}</a></li>
                            </ol>
                        </nav>
                    </div>
                </div>
                <div className="content-area tickating-tabs">
                    <div id="parentHorizontalTab" className="page-navigation">
                        <div className="page-navigation text-center">
                            <ul className="nav nav-pills resp-tabs-list mt-1">
                                {cats}
                            </ul>
                            <div className="tab-content">
                                <ul className="bg-light tab-pane fade show active p-3 m-0">
                                    {subcats}
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
                <StickyContainer>
                    <div className="container tickating-sub-tabs text-center pb-3">
                        <Sticky topOffset={400}>
                            {({
                                style,
                                isSticky
                            }) => {
                                return (
                                    <header className={this.state.className}>
                                        <ul className="nav justify-content-center pb-4 ticket-flow">
                                            <li className="nav-item">
                                                <a className="nav-link active">{ticketCategoryData.eventDetail[0].name}</a>
                                            </li>
                                            <li className="nav-item">
                                                <a className="nav-link disabled">Exhibits</a>
                                            </li>
                                            <li className="nav-item">
                                                <a className="nav-link disabled">Rides</a>
                                            </li>
                                            <li className="nav-item">
                                                <a className="nav-link disabled">Add-ons</a>
                                            </li>
                                        </ul>
                                    </header>
                                )
                            }
                            }
                        </Sticky>
                        <h2>Choose Your Ticket</h2>
                    </div>
                    <TicketCategorySelection ticketCategoryData={ticketCategoryData} eventName={ticketCategoryData.eventDetail[0].name} eventAltId={this.props.match.params.eventId} goToItinerary={this.goToItinerary.bind(this)} countries={this.props.countryList.countries} />
                </StickyContainer>
            </div>
        }
        else {
            return <div><KzLoader /></div>
        }
    }
}
export default connect(
    (state: IApplicationState) => state.ticketCategory,
    TicketCategoryPageStore.actionCreators
)(TicketPurchaseSelection);




