import * as React from "react";

export default class StateSearchResult extends React.Component<any, any> {
    
    public render() {
        var that = this;
           if (this.props.searchSuccess) {
               var searchResult = this.props.searchResult.map(function (val) {
                   return <button type="button" onClick={() => { that.props.setStateSearchKeyword(val.name) }} className="btn btn-sm btn-outline-secondary mr-1 ml-1 mb-2">{val.name}</button>;
               });
               if (searchResult != "") {
                   return <div>
                       <h6 className="text-secondary">feel a state</h6>
                       {searchResult}
                       <hr />
                   </div>
               }
               else {
                   return <div>                       
                       </div>
               }

           } else if (this.props.emptySearch) {
               //var defaultStates = this.props.defaultStates.length > 5 ? this.props.defaultStates.slice(0, 5) : this.props.defaultStates;
               var defaultStates = this.props.defaultStates;
               var defaultSearch = defaultStates.map(function (val) {
                   if (val.name != "") {
                       return <button type="button" onClick={() => { that.props.setStateSearchKeyword(val.name) }} className="btn btn-sm btn-outline-secondary mr-1 ml-1 mb-2">{val.name}</button>;
                   }
               })
               return <div>
                <h6 className="text-secondary">feel a state</h6>
                {defaultSearch}
                <hr />
            </div>;
        }
    }
}



