import { autobind } from "core-decorators";
import * as React from "react";
import { connect } from "react-redux";
import { Link, RouteComponentProps } from "react-router-dom";
import { UserSignInComponent } from "../components/Account/UserSignInComponent";
import { ForgotPasswordComponent } from "../components/Account/ForgotPasswordComponent";
import { LoginFormDataViewModel } from "shared/models/LoginFormDataViewModel";
import { ForgotPasswordFormDataViewModel } from "shared/models/ForgotPasswordFormDataViewModel";
import { IApplicationState } from "../stores";
import * as LoginStore from "../stores/Login";
import FeelLoader from "../components/Loader/FeelLoader";
import SignInGoogle from "../components/Account/SocialLogins/SignInGoogle";
import SignInFacebook from "../components/Account/SocialLogins/SignInFacebook";
import "./Checkout.scss";
import Metatag from "../components/Metatags/Metatag";

type LoginProps = LoginStore.ILoginState & typeof LoginStore.actionCreators & RouteComponentProps<{}>;

class SignIn extends React.Component<LoginProps, any> {
    public constructor() {
        super();
        this.state = { currentPage: "login", rememberMe: false };
    }

    public componentDidMount() {
        if (window) {
            window.scrollTo(0, 0)
        }
    }

    public showPage(page, e) {
        e.preventDefault();
        this.setState({
            currentPage: page
        });
    }

    public toggleRememberMe(e) {
        this.setState({ rememberMe: (!this.state.rememberMe) });
    }

    public render() {
        return <div> <Metatag url={this.props.location.pathname} title="Login on feelaplace" />
            <div className="container sign-form pt-3 pb-5 inner-banner">
                <div className="card">
                    <div className="card-header h5 bg-white">
                        Please Sign In <small className="float-right text-muted"><i className="fa fa-lock text-success"></i> Secure</small>
                    </div>

                    <div className="card-body">
                        <div className="row">
                            <div className="col-lg-6 col-sm-12 sign-form-left">
                                {
                                    this.state.currentPage === "login"
                                    &&
                                    <UserSignInComponent invalidCredentials={this.props.invalidCredentials} onSubmit={this.onSubmitLogin} switchPage={this.showPage.bind(this, "forgotPassword")} toggleRememberMe={this.toggleRememberMe.bind(this)} rememberMe={this.state.rememberMe} email={this.state.email} password={this.state.password} />
                                }

                                {
                                    this.state.currentPage === "forgotPassword"
                                    &&
                                    <ForgotPasswordComponent passwordResetMailSent={this.props.changePasswordMailSent} invalidCredentials={this.props.invalidEmail} switchPage={this.showPage.bind(this, "login")} submitForgotPassword={this.onSubmitForgotPassword.bind(this)} />
                                }
                            </div>
                            <div className="col-lg-6 col-sm-12 sign-form-right">
                                <SignInFacebook />
                                <SignInGoogle />
                            </div>
                        </div>                        
                    </div>
                    <div className="card-footer">
                        <a href="#" className="btn btn-outline-dark">Cancel</a>
                        <small className="float-right">Questions? Write to us at <a href="mailto:support@feelaplace.com" className="btn btn-link"><img alt="Feel Mail Icon" src="https://s3-us-west-2.amazonaws.com/feel-cdn/images/feel-mail-icon.png"/></a></small>
                    </div>
                </div>
            </div>
        </div>
    }

    @autobind
    private onSubmitLogin(values: LoginFormDataViewModel) {
        this.props.login(values, (response) => {
            if (response.success) {
                localStorage.setItem('userToken', response.session.user.altId);
                if (localStorage.getItem("isRedirectHomePage") !== null) {
                    localStorage.removeItem("isRedirectHomePage");
                    this.props.history.replace("/");
                }
                else if (localStorage.getItem("redirectPlaceAltId") !== null) {                  
                    var AltId = localStorage.getItem("redirectPlaceAltId");
                    localStorage.removeItem("redirectPlaceAltId");
                    this.props.history.push("/place/" + AltId);
                } else {
                    this.props.history.replace("/account");
                }
            }
        });
    }

    @autobind
    private onSubmitForgotPassword(values: ForgotPasswordFormDataViewModel) {
        this.props.forgotPassword(values, (response) => {
            if (response.success) {
            }
        });
    }
}

export default connect(
    (state: IApplicationState) => state.login,
    LoginStore.actionCreators
)(SignIn);



