import { autobind } from "core-decorators";
import * as React from "react";
import { connect } from "react-redux";
import { Link, RouteComponentProps } from "react-router-dom";
import { RegistrationFormDataViewModel } from "shared/models/RegistrationFormDataViewModel";
import { RegistrationResponseViewModel } from "shared/models/RegistrationResponseViewModel";
import { RegisterComponent } from "../components/Account/RegisterComponent";
import Metatag from "../components/Metatags/Metatag";
import { IApplicationState } from "../stores";
import * as RegisterStore from "../stores/Register";
import FeelLoader from "../components/Loader/FeelLoader";
import "./Checkout.scss";

type RegistrationProps = RegisterStore.IRegisterState & typeof RegisterStore.actionCreators & RouteComponentProps<{}>;
interface IRegistrationState { email: string; }

class Register extends React.Component<RegistrationProps, IRegistrationState> {
    public componentDidMount() {
        if (window) {
            window.scrollTo(0, 0)
        }
        this.props.requestCountryData();
    }

    public render() {
        if (this.props.fetchCountriesSuccess) {
            return <div>
                <Metatag url={this.props.location.pathname} title="Register on feelaplace" />
                <div className="container sign-form sign-up-form pt-3 pb-5">
                    <div className="card">
                        <div className="card-header h5 bg-white">
                            Please Sign Up <small className="float-right text-muted"><i className="fa fa-lock text-success"></i> Secure</small>
                        </div>

                        <div className="card-body">
                            <div className="row">
                                <div className="col-lg-6 col-sm-12 sign-form-left">
                                    <RegisterComponent success={this.props.registered} error={this.props.userExists || this.props.error} registrationMessage={this.props.alertMessage.body} submitRegister={this.onSubmitRegister.bind(this)} countryList={this.props.countryList} redirectLink={"checkout"} />
                                </div>
                                <div className="col-lg-6 col-sm-12 sign-form-right">
                                    <button type="button" className="btn btn-primary btn-block"><i className="fa fa-facebook-f mr-2"></i> Sign in with Facebook</button>
                                    <button type="button" className="btn btn-outline-danger btn-block"><i className="fa fa-google-plus-g mr-2"></i> Sign in with Google</button>
                                </div>
                            </div>
                        </div>
                        <div className="card-footer">
                            <Link to="/checkout" className="btn btn-outline-dark">Cancel</Link>
                            <small className="float-right">Questions? Write to us at <a href="mailto:support@feelaplace.com" className="btn btn-link"><img alt="Feel Mail Icon" src="https://s3-us-west-2.amazonaws.com/feel-cdn/images/feel-mail-icon.png" /></a></small>
                        </div>
                    </div>
                </div>
            </div>
        } else {
            return <div>
                <FeelLoader />
            </div>
        }
    }

    @autobind
    private onSubmitRegister(values: RegistrationFormDataViewModel) {
        values.userName = values.email;
        this.props.register(values, (response: RegistrationResponseViewModel) => {
            if (response.success) {
            } else if (response.isExisting) {
            }
        });
    }
}

export default connect(
    (state: IApplicationState) => state.register,
    RegisterStore.actionCreators
)(Register);



