import * as React from "react";
import ReactStars from 'react-stars';
import { Link } from "react-router-dom";

export default class EventRatings extends React.Component<any, any> {
    public render() {
        var rating = this.props.rating;
        var reviewAvg = 0;
        var ratingLength = 0;
        if (rating !== undefined) {
            var reviewLength = 1;
            if (rating.length !== 0) {
                reviewLength = rating.filter(function (item) { return item.points > 0 }).length;
            }
            reviewAvg = rating.length == 0 ? 0 : rating.map((item) => { return item.points }).reduce((a, b) => { return a + b }) / reviewLength;
            ratingLength = rating.length;
        } else {
            ratingLength = 0;
        }

        return <div className="rating">
                <span>Ratings:
                    <Link to={"/place/" + this.props.eventAltId.toUpperCase()}>
                        <ReactStars className="rating d-inline-block" color2={"#572483"} size={"14px"} edit={true} value={reviewAvg} count={5} />
                    </Link>
                </span>
                <span className="review-text">{ratingLength}+ Reviews</span>
        </div>;
    }
}



