import { Form, Formik } from "formik";
import * as React from "react";
import { Button } from "react-bootstrap";
import { Link } from "react-router-dom";

interface IKzFormProps {
    onSubmit: (values: any) => void;
    initialValues: any;
    validationSchema: any; // TODO: Figure out where yup schema went
    hideSubmit?: boolean;
    redirectLink: string;
}

export default class RegisterFeelFormSignUp extends React.Component<IKzFormProps, {}> {
    public render() {
        return (
            <Formik
                onSubmit={this.props.onSubmit}
                initialValues={this.props.initialValues || {}}
                validationSchema={this.props.validationSchema}
            >
                {(props) => (<Form>
                    {this.props.children}
                    {!this.props.hideSubmit && <div>
                        <Button type="submit" className="btn btn-block site-primery-btn">Sign Up</Button>
                        <div className="text-center">
                            <p className="pt-3">Already have a feel account? <Link to={"/" + this.props.redirectLink}>Sign in</Link></p>
                        </div>
                    </div>
                    }
                </Form>)}
            </Formik>
        );
    }
}




