import * as React from "react";
import { Link, RouteComponentProps } from "react-router-dom";
import { UserCheckoutLoginForm } from "../CheckOut/UserCheckoutLoginForm";
import { UserSignInForm } from "../Account/UserSignInForm";

export class UserSignInComponent extends React.Component<any, any> {
    public render() {
        return <div>
            <h6>
                Returning Customer
            </h6>
            <UserSignInForm onSubmit={this.props.onSubmit} onClick={this.props.switchPage} toggleRememberMe={this.props.toggleRememberMe} rememberMe={this.props.rememberMe} email={this.props.email} password={this.props.password} />
            {this.props.invalidCredentials && <span className="validate-txt"><br /><p className="text-danger">Invalid email address or password</p></span>}
        </div>;
    }
}



