import { Field } from "formik";
import * as React from "react";
import ForgotPasswordFeelForm from "../form/ForgotPasswordFeelForm";
import { ForgotPasswordFormDataViewModel } from "shared/models/ForgotPasswordFormDataViewModel";
import Yup from "yup";

interface IForgotPasswordFormProps {
    onSubmit: (values: ForgotPasswordFormDataViewModel) => void;
    onClick: () => void;
}

export class ForgotPasswordForm extends React.Component<IForgotPasswordFormProps, {}> {
    public render() {
        const schema = this.getSchema();
        return (
            <ForgotPasswordFeelForm {...this.props} validationSchema={schema} initialValues={{}}>
                <div className="form-group">
                    <Field type="email" name="email" placeholder="Enter the email address" className="form-control" required />
                </div>                
            </ForgotPasswordFeelForm>
        );
    }

    private getSchema() {
        return Yup.object().shape({
            email: Yup.string().email().required()            
        });
    }
}



