import { Field } from "formik";
import * as React from "react";
import AccountForm from "./AccountForm";
import { ChangeNameFormDataViewModel } from "../../../models/Account/ChangeNameFormDataViewModel";
import Yup from "yup";

interface IChangeNameFormProps {
    onSubmit: (values: ChangeNameFormDataViewModel) => void;
    onCancel: () => void;
    firstName: string;
    lastName: string;
}

export class ChangeNameForm extends React.Component<IChangeNameFormProps, {}> {
    public render() {
        const schema = this.getSchema();
        return (
            <AccountForm {...this.props} validationSchema={schema} initialValues={{}}>
                <div className="col-auto">
                    <label className="sr-only" ></label>
                    <Field type="firstName" name="firstName" placeholder="First Name" className="form-control mb-2" required />
                </div>
                <div className="col-auto">
                    <label className="sr-only" >Last Name</label>
                    <div className="input-group mb-2">
                        <Field type="lastName" name="lastName" placeholder="Last Name" className="form-control" required />
                    </div>
                </div>
            </AccountForm>
        );
    }

    private getSchema() {
        return Yup.object().shape({
            firstName: Yup.string().required(),
            lastName: Yup.string().required(),
        });
    }
}



