import { autobind } from "core-decorators";
import * as map from "lodash/map";
import * as chunk from "lodash/chunk";
import * as debounce from 'lodash/debounce';
import * as React from "React";
import {
    Grid, Row, ListGroup,
    ListGroupItem, Collapse, Glyphicon,
    Button, InputGroup, FormControl
} from "react-bootstrap";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { bindActionCreators } from "redux";
import { IApplicationState } from "../../stores";
import * as CategoryStore from "../../stores/Category";
import MonumentsTickets from "./MonumentsTickets";
import FeelLoader from "../Loader/FeelLoader";

export default class ProjectFeelExploreSelection extends React.Component<any, any> {
    constructor() {
        super()
        this.state = {
            currentValue: 0,
            categoryState: 0,
            defaultPlace: true,
            catClass: 0,
            sabCatClass: 0
        }
    }

    public componentDidMount() {
        this.props.getCategoryEvents("top-tickets");
        localStorage.setItem('currentSlug', "top-tickets");
        if (!this.props.category.isLoading) {
            this.props.requestCategoryData();
        }
    }

    public getSubcategories(index, e) {
        var i = e;
        localStorage.setItem('currentSlug', this.props.category.categories[i].subCategories[0].slug);
        this.setState({
            currentValue: e,
            catClass: e,
            sabCatClass: 0
        })
        this.props.getCategoryEvents(this.props.category.categories[i].subCategories[0].slug);
    }

    public getSubCategoryData(index, e) {
        this.props.getCategoryEvents(index);
        localStorage.setItem('currentSlug', index);
        this.setState({ categoryState: 1, sabCatClass: e });
    }

    public render() {
        var that = this;
        var defaultplace = this.state.defaultPlace;
        if (this.props.category.fetchsuccess == true) {
            var i = -1;
            var j = -1;
            const cats = this.props.category.categories.map((item) => {
                i = i + 1;
                var img;
                var classname;
                if (i == this.state.catClass) {
                    classname = "nav-item mb-2 active";
                    img = "https://static1.feelaplace.com/images/category-tab-icon/" + item.slug + "-active.png"
                } else {
                    classname = "mb-2";
                    img = "https://static1.feelaplace.com/images/category-tab-icon/" + item.slug + ".png"
                }
                return (
                    <li className={classname} onClick={this.getSubcategories.bind(that, item, i)}>
                        <span className="tabs-icon mb-0"><img src={img} alt="" /></span>
                        <p className="tabs-title m-0" >{item.displayName}</p>
                    </li>)
            });
            const subcats = this.props.category.categories[this.state.currentValue].subCategories.map((item) => {
                j = j + 1;
                var classname;
                if (j == this.state.sabCatClass) {
                    classname = "btn btn-sm btn-outline-secondary mr-1 ml-1 active"
                }
                else {
                    classname = "btn btn-sm btn-outline-secondary mr-1 ml-1"
                }
                return <li className={classname} onClick={this.getSubCategoryData.bind(that, item.slug, j)}>{item.displayName}</li>
            });
            return <div className="content-area">
                <div className="page-navigation text-center">
                    <ul className="nav nav-pills resp-tabs-list mt-1">
                        {cats}
                    </ul>
                    <div className="tab-content">
                        <ul className="bg-light tab-pane fade show active p-3 m-0">
                            {subcats}
                        </ul>
                    </div>
                </div>
                <div className="nav-tab-content container">
                    <div className="row">
                        {(this.props.category.fetchCategorySuccess == true) && <MonumentsTickets push={this.props.push()} isAuthenticated={this.props.isAuthenticated} display={defaultplace} categoryData={this.props.category.category.categoryEvents} cityFilterKeyword={this.props.cityFilterKeyword} stateFilterKeyword={this.props.stateFilterKeyword} countryFilterKeyword={this.props.countryFilterKeyword} clearSearchSuccess={this.props.clearSearchSuccess} />}
                    </div>
                    <div className="pt-2 pb-3 text-center">
                        <Link className="btn btn-link" to="#" onClick={() => this.setState({ defaultPlace: !this.state.defaultPlace })}>{this.state.defaultPlace ? "Show All" : "Show Less"}</Link>
                    </div>
                </div>
            </div>;
        }
        else {
            return <div>
                <FeelLoader />
            </div>;
        }
    }
}



