import * as React from "react";
import { Link, RouteComponentProps } from "react-router-dom";
import KzLoader from "../Loader/KzLoader";
import { DeleteCardDataViewModel } from "../../models/Account/DeleteCardDataViewModel";
import { SaveCardForm } from "../Account/form/SaveCardForm";
import { GetCardListDataViewModel } from "../../models/Account/GetCardListDataViewModel";

export class UserSavedCards extends React.Component<any, any> {
    public componentDidMount() {
        if (localStorage.getItem('userToken') != null && localStorage.getItem('userToken') != '0') {
            var userCard: GetCardListDataViewModel = {
                altId: localStorage.getItem('userToken'),
            };
            this.props.getCardList(userCard);
        }           
    }

    public deleteCard(cardAltID, e) {
        e.preventDefault();
        const card: DeleteCardDataViewModel = {
            altId: cardAltID,
        };
        this.props.submitDeleteCard(card);
    }

    public render() {
        var that = this;
        if (this.props.fetchSuccess) {
            var cardList = this.props.cardList.userCards.map(function (val) {
                return <div>
                <ul key={val.altId} className="list-group">
                        <li className="list-group-item list-group-item-secondary">
                            <img src="" className="img-full" />{val.cardNumber}
                        <div className="float-right">
                                <Link to="#" onClick={that.deleteCard.bind(that, val.altId)}>
                                    <i className="fa fa-trash" aria-hidden="true"></i> Remove </Link>
                        </div>
                            </li>
                </ul>
                    </div>;
            });
            return <div className="col-sm-9 right-act-pnl">
                <h4 className="mb-3 mt-3">Saved Cards</h4>
                <div className="card">
                    <div className="card-body">
                        <p><button type="button" className="btn btn-outline-secondary" data-toggle="collapse" data-target="#collapseExample" aria-expanded="false" aria-controls="collapseExample">+ Add New Card</button></p>
                            <div id="collapseExample" className="new-address-fields collapse">
                                  <SaveCardForm onSubmit={this.props.submitSaveCard} onCancel={this.props.onCancel} />
                            </div>
                    <div className="user-address">
                        <div>
                            {cardList}
                        </div>
                    </div>
                </div>
             </div>
            </div>;
        } else if (this.props.updateSuccess) {
			return <div>        				
            </div>
        } else {
            return <div><KzLoader /></div>;
        }
    }
}



