import { autobind } from "core-decorators";
import * as React from "react";
import { Link, RouteComponentProps } from "react-router-dom";
import { UserAddresses } from "../components/Account/UserAddresses";
import { UserOrders } from "../components/Account/UserOrders";
import { UserProfile } from "../components/Account/UserProfile";
import { UserSavedCards } from "../components/Account/UserSavedCards";
import KzAlert from "../components/Alert/KzAlert";
import AddressResponseViewModel from "../models/Account/AddressResponseViewModel";
import { AlertDataViewModel } from "../models/Alert/AlertDataViewModel";
import CardResponseViewModel from "../models/Account/CardResponseViewModel";
import UserProfileResponseViewModel from "../models/Account/UserProfileResponseViewModel";
import { ChangeNameFormDataViewModel } from "../models/Account/ChangeNameFormDataViewModel";
import { ChangeNameResponseViewModel } from "../models/Account/ChangeNameResponseViewModel";
import { ChangePasswordFormDataViewModel } from "../models/Account/ChangePasswordFormDataViewModel";
import { ChangePasswordResponseViewModel } from "../models/Account/ChangePasswordResponseViewModel";
import { ChangePrimaryPhoneFormDataViewModel } from "../models/Account/ChangePrimaryPhoneFormDataViewModel";
import { ChangePrimaryPhoneResponseViewModel } from "../models/Account/ChangePrimaryPhoneResponseViewModel";
import { DeleteAddressDataViewModel } from "../models/Account/DeleteAddressDataViewModel";
import { DeleteAddressResponseViewModel } from "../models/Account/DeleteAddressResponseViewModel";
import { DeleteCardDataViewModel } from "../models/Account/DeleteCardDataViewModel";
import { DeleteCardResponseViewModel } from "../models/Account/DeleteCardResponseViewModel";
import { GetAddressesDataViewModel } from "../models/Account/GetAddressesDataViewModel";
import { GetAddressListDataViewModel } from "../models/Account/GetAddressListDataViewModel";
import { GetCardListDataViewModel } from "../models/Account/GetCardListDataViewModel";
import { GetUserProfileDataViewModel } from "../models/Account/GetUserProfileDataViewModel";
import { SaveAddressFormDataViewModel } from "../models/Account/SaveAddressFormDataViewModel";
import { SaveAddressResponseViewModel } from "../models/Account/SaveAddressResponseViewModel";
import { SaveCardFormDataViewModel } from "../models/Account/SaveCardFormDataViewModel";
import { SaveCardResponseViewModel } from "../models/Account/SaveCardResponseViewModel";
import { SetDefaultAddressFormDataViewModel } from "../models/Account/SetDefaultAddressFormDataViewModel";
import { SetDefaultAddressResponseViewModel } from "../models/Account/SetDefaultAddressResponseViewModel";
import { IApplicationState } from "../stores";
import * as AccountStore from "../stores/Account";
import "../components/Account/styles/_myaccount.scss";
import Loader from 'react-loader-advanced';
import { connect } from "react-redux";


type AccountProps = AccountStore.IUserAccountState & typeof AccountStore.actionCreators & RouteComponentProps<{}>;

export class Account extends React.Component<AccountProps, any> {
    public constructor() {
        super();
        this.state = {
            currentView: "userProfile",
            enableMyaccount: false,
            userToken: "",
        };
    }

    public componentDidMount() {
        if (window) {
            window.scrollTo(0, 0)
        }

        if (localStorage.getItem('userToken') != null && localStorage.getItem('userToken') != '0') {
            this.setState({ userToken: localStorage.getItem('userToken') });
        }
        this.props.requestCountryData();
    }

    public setView(view, e) {
        e.preventDefault();
        this.setState({
            currentView: view
        });
    }


    public render() {
        return <div className="container my-acnt-content pt-2 pb-3">
            <KzAlert visible={this.props.updateSuccess} >{this.props.alertMessage.body}</KzAlert>
            <div className="my-acnt-header text-center">
                <h3>My Account</h3>
                <p className="text-gray text-muted">
                    Modify an order, track a shipment, and update your account info.<br />
                    All in one place. All with a few simple clicks.
			       </p>
            </div>

            <hr />
            <div className="row">
                <div className="col-sm-3 left-act-pnl text-left">
                    <h4 className="mb-3 mt-3 text-muted"><i className="fa fa-gift" aria-hidden="true"></i> Orders</h4>
                    <Link className="btn btn-block btn-outline-secondary" to="#" onClick={this.setView.bind(this, "userOrders")} rel="nofollow">My Orders</Link>
                    <Link className="btn btn-block btn-outline-secondary" to="http://www.bluedart.com/maintracking.html" rel="nofollow" target="_blank">Track Shipment</Link>
                    <h4 className="mb-3 mt-4 text-muted"><i className="fa fa-cog" aria-hidden="true"></i> Settings</h4>
                    <Link className="btn btn-block btn-outline-secondary active" to="#" onClick={this.setView.bind(this, "userProfile")} rel="nofollow">Account</Link>
                    <Link className="btn btn-block btn-outline-secondary" to="#" onClick={this.setView.bind(this, "userAddresses")} rel="nofollow">Addresses</Link>
                    <Link className="btn btn-block btn-outline-secondary" to="#" onClick={this.setView.bind(this, "userSavedCards")} rel="nofollow">Saved Cards</Link>
                </div>
                {this.state.currentView === "userAddresses" && <UserAddresses countryData={this.props.countryList} requesting={this.props.requesting} fetchSuccess={this.props.fetchSuccess} updateSuccess={this.props.updateSuccess} error={this.props.errors} addressList={this.props.userAddresses} getAddressList={this.props.getAddressListAction.bind(this)} submitDeleteAddress={this.onSubmitDeleteAddress.bind(this)} submitSaveAddress={this.onSubmitSaveAddress.bind(this)} submitSetDefaultAddress={this.onSubmitSetDefaultAddress.bind(this)} onCancel={this.props.getAddressListAction.bind(this)} />}
                {this.state.currentView === "userOrders" && <UserOrders  requesting={this.props.requesting} fetchSuccess={this.props.fetchOrderSuccess} updateSuccess={this.props.updateSuccess} error={this.props.errors} userOrders={this.props.getUserOrdertData} orderList={this.props.userOrders}/>}
                {this.state.currentView === "userProfile" && <UserProfile countryData={this.props.countryList} requesting={this.props.requesting} fetchSuccess={this.props.fetchSuccess} updateSuccess={this.props.updateSuccess} error={this.props.errors} userProfile={this.props.userProfile} getUserProfile={this.props.getUserProfileAction.bind(this)} submitChangeName={this.onSubmitChangeName.bind(this)} submitChangePassword={this.onSubmitChangePassword.bind(this)} submitChangePhone={this.onSubmitChangePhone.bind(this)} onCancel={this.props.getUserProfileAction.bind(this)} invalidPassword={this.props.invalidPassword} userToken={this.state.userToken} />}
                {this.props.changeNameSuccess === true && <div> Name updated successfully</div>}
                {this.props.changePhoneSuccess === true && <div> Phone number updated successfully</div>}
                {this.props.changePasswordSuccess === true && <div> Password updated successfully</div>}
                {this.state.currentView === "userSavedCards" && <UserSavedCards requesting={this.props.requesting} fetchSuccess={this.props.fetchSuccess} updateSuccess={this.props.updateSuccess} error={this.props.errors} cardList={this.props.userCards} getCardList={this.props.getCardListAction.bind(this)} submitDeleteCard={this.onSubmitDeleteCard.bind(this)} submitSaveCard={this.onSubmitSaveCard.bind(this)} onCancel={this.props.getCardListAction.bind(this)} />}
            </div>
        </div>;
    }

    @autobind
    private onSubmitChangeName(values: ChangeNameFormDataViewModel) {
        values.altId = this.state.userToken;
        this.props.changeNameAction(values, (response: ChangeNameResponseViewModel) => {
            if (response.success) {
                var user: GetUserProfileDataViewModel = {
                    altId: this.state.userToken,
                };
                setTimeout(() => {
                    this.props.getUserProfileAction(user, (response: UserProfileResponseViewModel) => {
                    });
                }, 3000);
            } else {
                alert("Some problem encountered while changing name");
            }
        });
    }

    @autobind
    private onSubmitChangePassword(values: ChangePasswordFormDataViewModel) {
        if (values.newPassword != values.confPassword) {
            alert("Password mismatch");
        }
        values.altId = this.state.userToken;
        this.props.changePasswordAction(values, (response: ChangePasswordResponseViewModel) => {
            if (response.success) {
                var user: GetUserProfileDataViewModel = {
                    altId: this.state.userToken,
                };
                setTimeout(() => {
                    this.props.getUserProfileAction(user, (response: UserProfileResponseViewModel) => {
                    });
                }, 3000);
            }
        });
    }

    @autobind
    private onSubmitChangePhone(values: ChangePrimaryPhoneFormDataViewModel) {
        values.altId = this.state.userToken;
        this.props.changePhoneAction(values, (response: ChangePrimaryPhoneResponseViewModel) => {
            if (response.success) {
                var user: GetUserProfileDataViewModel = {
                    altId: this.state.userToken,
                };
                setTimeout(() => {
                    this.props.getUserProfileAction(user, (response: UserProfileResponseViewModel) => {
                    });
                }, 3000);
            } else {
                alert("Some problem encountered while changing phone");
            }
        });
    }

    @autobind
    private onSubmitDeleteAddress(values: DeleteAddressDataViewModel) {
        this.props.deleteAddressAction(values, (response: DeleteAddressResponseViewModel) => {
            if (response.success) {
                var address: GetAddressesDataViewModel = {
                    altId: this.state.userToken,
                    addressType: 1,
                };
                setTimeout(() => {
                    this.props.getAddressListAction(address, (response: AddressResponseViewModel) => {
                    });
                }, 3000);
            } else {
                alert("Some problem encountered");
            }
        });
    }

    @autobind
    private onSubmitDeleteCard(values: DeleteCardDataViewModel) {
        this.props.deleteCardAction(values, (response: DeleteCardResponseViewModel) => {
            if (response.success) {
                var userCard: GetCardListDataViewModel = {
                    altId: this.state.userToken,
                };
                setTimeout(() => {
                    this.props.getCardListAction(userCard, (response: CardResponseViewModel) => {
                    });
                }, 3000);
            } else {
                alert("Some problem encountered");
            }
        });
    }

    @autobind
    private onSubmitSaveAddress(values: SaveAddressFormDataViewModel) {
        values.altId = this.state.userToken;
        this.props.saveAddressAction(values, (response: SaveAddressResponseViewModel) => {
            if (response.success) {
                var address: GetAddressesDataViewModel = {
                    altId: this.state.userToken,
                    addressType: values.addressTypeId,
                };
                setTimeout(() => {
                    this.props.getAddressListAction(address, (response: AddressResponseViewModel) => {
                    });
                }, 3000);
            } else {
                alert("Some problem encountered");
            }
        });
    }

    @autobind
    private onSubmitSaveCard(values: SaveCardFormDataViewModel) {
        values.altId = this.state.userToken;
        this.props.saveCardAction(values, (response: SaveCardResponseViewModel) => {
            if (response.success) {
                var userCard: GetCardListDataViewModel = {
                    altId: this.state.userToken,
                };
                setTimeout(() => {
                    this.props.getCardListAction(userCard, (response: CardResponseViewModel) => {
                    });
                }, 3000);
            } else {
                alert("Some problem encountered");
            }
        });
    }

    @autobind
    private onSubmitSetDefaultAddress(values: SetDefaultAddressFormDataViewModel) {
        this.props.setDefaultAddressAction(values, (response: SetDefaultAddressResponseViewModel) => {
            if (response.success) {
                var address: GetAddressesDataViewModel = {
                    altId: this.state.userToken,
                    addressType: 1,
                };
                setTimeout(() => {
                    this.props.getAddressListAction(address, (response: AddressResponseViewModel) => {
                    });
                }, 3000);
            } else {
                alert("Some problem encountered");
            }
        });
    }
}

export default connect(
    (state: IApplicationState) => state.account,
    AccountStore.actionCreators
)(Account);



