import { Form, Formik } from "formik";
import * as React from "react";
import { Button } from "react-bootstrap";
import "./AccountForm.scss";

interface IAccountFormProps {
    onSubmit: (values: any) => void;
    onCancel: () => void;
    initialValues: any;
    validationSchema: any; // TODO: figure out where Yup SCHEMA went
}

export default class AccountAddressForm extends React.Component<IAccountFormProps, {}> {
    public render() {
        return (
            <Formik
                onSubmit={this.props.onSubmit}
                initialValues={this.props.initialValues || {}}
                validationSchema={this.props.validationSchema}
            >
                {(props) => (<Form>
                        {this.props.children}
                        <div className="form-group text-center">
                        <Button type="submit" className="btn site-primery-btn mr-2" disabled={props.isSubmitting}>Submit</Button>
                            <button type="button" onClick={this.props.onCancel} className="btn btn-link">Cancel</button>
                        </div>
                </Form>)}
            </Formik>
        );
    }
}




