import { Form, Formik } from "formik";
import * as React from "react";
import { Button } from "react-bootstrap";

interface IKzFormProps {
    onSubmit: (values: any) => void;
    onClick: (values: any) => void;
    initialValues: any;
    validationSchema: any; // TODO: Figure out where yup schema went
    hideSubmit?: boolean;
}

export default class ForgotPasswordFeelForm extends React.Component<IKzFormProps, {}> {
    public render() {
        return (
            <Formik
                onSubmit={this.props.onSubmit}
                onClick={this.props.onClick}
                initialValues={this.props.initialValues || {}}
                validationSchema={this.props.validationSchema}
            >
                {(props) => (<Form>
                    {this.props.children}
                    {!this.props.hideSubmit &&
                        <div>
                            <a onClick={this.props.onClick} href="#" className="text-info mb-2 d-block"><small>Already a member? Sign In</small></a>
                            <div className="forgot-btn clearfix">
                                <Button type="submit" className="btn site-primery-btn">Send Reset Link</Button>
                            </div>
                        </div>
                    }
                </Form>)}
            </Formik>
        );
    }
}




