import { Field } from "formik";
import * as React from "react";
import { Link, RouteComponentProps } from "react-router-dom";
import ResetPasswordKzForm from "shared/components/form/ResetPasswordKzForm";
import { ResetPasswordFormDataViewModel } from "shared/models/ResetPasswordFormDataViewModel";
import Yup from "yup";

interface IResetPasswordFormProps {
    onSubmit: (values: ResetPasswordFormDataViewModel) => void;
}

export class ResetPasswordForm extends React.Component<IResetPasswordFormProps, any> {
    public render() {
        const schema = this.getSchema();
        return (
            <div>
                <ResetPasswordKzForm {...this.props} validationSchema={schema} initialValues={{}}>
                    <div className="form-group">
                        <Field type="password" name="password" placeholder="Enter password" className="form-control" required />
                    </div>
                    <div className="form-group">
                        <Field type="password" name="confirmPassword" placeholder="Confirm password" className="form-control" required />
                    </div>
                </ResetPasswordKzForm>
            </div>
        );
    }

    private getSchema() {
        return Yup.object().shape({
            password: Yup.string().required(),
            confirmPassword: Yup.string().required(),
        });
    }
}



