import * as React from "React";

export default class EventAmenities extends React.Component<any,any> {
    public render() {
            var Amenities = this.props.amenities.map((item) => {
            return <li className="list-inline-item"><img src={"https://static1.feelaplace.com/images/amenities/" + item + ".png"} alt="" />{item}</li>
        })
        return <div className="amenities-sec pt-3">
            <h5 className="mb-4">Amenities</h5>
            <ul className="list-inline">
                {Amenities}
            </ul>
        </div>
    }
}


