import { autobind } from "core-decorators";
import Countdown from "react-countdown-now";
import * as React from "react";
import * as ReactDOM from "react-dom";
import { connect } from "react-redux";
import { Field } from "formik";
import Yup from "yup";
import { Link, RouteComponentProps } from "react-router-dom";
import { Grid, Row, Col, Button, Alert } from "react-bootstrap";
import { PaymentFormDataViewModel, StripePaymentModel } from "../models/Payment/PaymentFormDataViewModel";
import { PaymentFormResponseViewModel } from "../models/Payment/PaymentFormResponseViewModel";
import CardDetailForm from "../components/Payment/CardDetailForm";
import KzLoader from "../components/Loader/KzLoader";
import { IApplicationState } from "../stores";
import * as numeral from "numeral";
import Loader from 'react-loader-advanced';
import * as PaymentStore from "../stores/Payment";
import "../scss/_custom.scss";
import "../scss/_payment.scss";
import Metatag from "../components/Metatags/Metatag";

type PaymentComponentProps = PaymentStore.IPaymentState & typeof PaymentStore.actionCreators & RouteComponentProps<{}>;

export class Payment extends React.Component<PaymentComponentProps, PaymentStore.IPaymentState> {
    public componentWillMount() {
        this.props.requestCountryData();
        this.props.requestPaymentOptionData();
        this.setState({ transactionDetail: "", paymentOption: "1", cardTypeId: 0, cardNumber: "" });
    }

    public renderer = ({ minutes, seconds, completed }) => {
        if (completed) {
            return this.props.history.push({ pathname: "/pgerror", search: "SessionExpired" });
        } else {
            return <span>{minutes}:{seconds}</span>;
        }
    }

    public constructor() {
        super();
        this.state = {
            transactionDetail: "",
            timer: new Date().getTime() + 600000,
        };
    }

    public componentDidMount() {
        if (window) {
            window.scrollTo(0, 0)
        }

        this.props.requestPaymentOptionData();
        if (localStorage.getItem('transactionState') != null && localStorage.getItem('transactionState') != '0') {
            var data = JSON.parse(localStorage.getItem("transactionState"));
            var cart = JSON.parse(localStorage.getItem("cartItems"));
            data.currencyName = cart[0].currencyName;
            this.setState({ transactionDetail: data });
            this.setState({ timer: new Date().getTime() + 600000 });
        }
    }

    public setPaymentOption(option, e) {
        this.setState({ paymentOption: option });
    }

    public setCardTypeId(type, number) {
        this.setState({ cardTypeId: type, cardNumber: number });
    }

    public checkCardType(e) {
        var cardNumber = e.target.value;
        if (cardNumber == "") {
            this.setCardTypeId("0", cardNumber);
        }
        else if (/^5[1-5]/.test(cardNumber)) {
            this.setCardTypeId("1", cardNumber);
        }

        else if (/^4/.test(cardNumber)) {
            this.setCardTypeId("2", cardNumber);
        }

        else if (/^3[47]/.test(cardNumber)) {
            this.setCardTypeId("3", cardNumber);
        }

        else if (/^(5018|5044|5020|5038|6220|6304|6759|676[1-3])/.test(cardNumber)) {
            this.setCardTypeId("4", cardNumber);
        }
        else {
            this.setCardTypeId("0", cardNumber);
        }
    }

    public render() {
        const schema = this.getSchema();

        if (this.props.fetchPaymentOptionsSuccess) {

            var countries = this.props.countryList.countries.map((item) => {
                return <option value={item.name}>{item.name}</option>
            });

            var bankDetails = this.props.paymentOptionList.bankDetails.map((item) => {

                return <option value={item.altId}>{item.bankName}</option>
            });

            var cashCardDetails = this.props.paymentOptionList.cashCardDetails.map((item) => {
                return <option value={item.altId}>{item.cardName}</option>

            });

            var data = this.state.transactionDetail;
            var paymentData;
            if (data != "") {
                var symbol;
                if (data.currencyName == "INR") {
                    symbol = "";
                }
                else if (data.currencyName == "GBP") {
                    symbol = "";
                }
                else if (data.currencyName == "USD") {
                    symbol = "$";
                }
                else if (data.currencyName == "EURO") {
                    symbol = "";
                }
                else {
                    symbol = "";
                }
                paymentData = <div className="card-body">
                    <div className="row">
                        <div className="col-lg-12 text-right">
                            <p>
                                <small>Bag Subtotal {symbol + numeral(data.grossTicketAmount).format('00.00')}</small><br />
                                <small><i className="fa fa-info-circle text-primary mr-2" data-toggle="tooltip" data-placement="top" title="" data-original-title="Bank, service and handling charges">
                                </i> Bank, service and handling charges {symbol + numeral(data.convenienceCharges + data.serviceCharge).format('00.00')} </small></p>
                            <div className="total-price">
                                <span className="border-top pl-3 pt-2 mt-2">Total	{symbol + numeral(data.netTicketAmount).format('00.00')}</span>
                            </div>
                        </div>
                    </div>
                </div>;
            } else {
                paymentData = <div className="col-sm-8">
                </div>;
            }
            return (<div>
                <Metatag url={this.props.location.pathname} title="Payment Options" />
                    <div className="container checkout-pages pt-3 pb-5">
                        <div className="card checkout-card">
                            <div>
                                <h5>Payment <small className="float-sm-right text-muted">Time left to complete transaction <span className="text-danger"><Countdown
                                    date={Date.now() + (this.state.timer - new Date().getTime())}
                                    renderer={this.renderer}
                                /></span></small></h5>
                                <section>
                                    <div className="payment-opp p-3">
                                        <div className="container">
                                            <ul className="nav nav-tabs gradient-bg border rounded">
                                                <li className="nav-item border-right" onClick={this.setPaymentOption.bind(this, "1")}><a className="nav-link active" data-toggle="tab" href="#cc-dc">Credit / Debit Card</a></li>
                                            </ul>
                                        </div>
                                        <CardDetailForm onSubmit={this.onSubmitPaymentDetails} validationSchema={schema} initialValues={{}}>
                                            <div className="container">
                                                <div className="tab-content pt-4 pb-2">
                                                    <div id="cc-dc" className="tab-pane fade active show">
                                                        <div className="row">
                                                            <div className="col-sm-6">
                                                                <h6 className="text-muted mb-4">Billing Address</h6>
                                                                <div className="form-group">
                                                                    <Field type="text" name="address" className="form-control" id="" placeholder="Apt, Floor, Suite, Street Address, etc." required={this.state.paymentOption == "1"} />
                                                                </div>
                                                                <div className="form-group">
                                                                    <Field type="text" name="city" className="form-control" id="" placeholder="Suburb" required={this.state.paymentOption == "1"} />
                                                                </div>
                                                                <div className="form-group">
                                                                    <Field type="text" name="state" className="form-control" id="" placeholder="State/Province/Region" required={this.state.paymentOption == "1"} />
                                                                </div>
                                                                <div className="form-row">
                                                                    <div className="col-sm-6 form-group">
                                                                        <div className="form-group">
                                                                        <Field type="number" pattern="[0-9]*" name="zipcode" className="form-control" id="" placeholder="Pin/Zip Code" required={this.state.paymentOption == "1"} />
                                                                        </div>
                                                                    </div>
                                                                    <div className="col-sm-6 form-group">
                                                                        <div className="form-group">
                                                                            <Field name="country" component="select" className="form-control">
                                                                                <option>Select Country</option>
                                                                                {countries}
                                                                            </Field>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                            <div className="col-md-6">
                                                                <h6 className="text-muted mb-4">Payment Method</h6>
                                                                <form>
                                                                    <p><img src={"https://static1.feelaplace.com/images/payment-card/cardTypeId" + this.state.cardTypeId + ".png"} alt="" className="img-responsive" style={{ height: "30px" }} />
                                                                    </p>
                                                                    <div className="form-group">
                                                                    <Field type="number" pattern="[0-9]*" name="cardNumber" className="form-control" id="" onChange={this.checkCardType.bind(this)} placeholder="Credit/Debit Card Number" required={this.state.paymentOption == "1"} />
                                                                    </div>
                                                                    <div className="form-group">
                                                                        <Field type="text" name="nameOnCard" className="form-control" id="" placeholder="Name on Card" required={this.state.paymentOption == "1"} />
                                                                    </div>
                                                                    <div className="form-row">
                                                                        <div className="col-sm-4 form-group">
                                                                        <Field type="number" pattern="[0-9]*" name="cvv" className="form-control cvv-disc" id="" placeholder="CVV/Security Code" />
                                                                        </div>
                                                                        <div className="col-sm-4 form-group">
                                                                            <Field component="select" name="expiryMonth" className="form-control">
                                                                                <option value="0">Expiry Month</option>
                                                                                <option value="01">01</option>
                                                                                <option value="02">02</option>
                                                                                <option value="03">03</option>
                                                                                <option value="04">04</option>
                                                                                <option value="05">05</option>
                                                                                <option value="06">06</option>
                                                                                <option value="07">07</option>
                                                                                <option value="08">08</option>
                                                                                <option value="09">09</option>
                                                                                <option value="10">10</option>
                                                                                <option value="11">11</option>
                                                                                <option value="12">12</option>
                                                                            </Field>
                                                                        </div>
                                                                        <div className="col-sm-4 form-group">
                                                                            <Field component="select" name="expiryYear" className="form-control">
                                                                                <option value="0">Expiry Year</option>
                                                                                <option value="2018">2018</option>
                                                                                <option value="2019">2019</option>
                                                                                <option value="2020">2020</option>
                                                                                <option value="2021">2021</option>
                                                                                <option value="2022">2022</option>
                                                                                <option value="2023">2023</option>
                                                                                <option value="2024">2024</option>
                                                                                <option value="2025">2025</option>
                                                                                <option value="2026">2026</option>
                                                                                <option value="2027">2027</option>
                                                                                <option value="2028">2028</option>
                                                                                <option value="2029">2029</option>
                                                                                <option value="2030">2030</option>
                                                                                <option value="2031">2031</option>
                                                                                <option value="2032">2032</option>
                                                                                <option value="2033">2033</option>
                                                                                <option value="2034">2034</option>
                                                                                <option value="2035">2035</option>
                                                                                <option value="2036">2036</option>
                                                                                <option value="2037">2037</option>
                                                                                <option value="2038">2038</option>
                                                                            </Field>
                                                                        </div>
                                                                    </div>
                                                                </form>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </CardDetailForm>
                                    </div>
                                </section>
                            </div>
                            {paymentData}
                            <div className="card-footer gradient-bg">
                                <small>By clicking continue, you accept and agree to all terms of feel india policy. A receipt will be provided to you electronically that contains these terms and an itemized list of your purchase including taxes and shipping charges, if any.</small>
                            </div>
                        </div>
                    </div >
                </div >
            );
        } else {
            return <div><KzLoader /></div>;
        }
    }

    @autobind
    private onSubmitPaymentDetails(values: PaymentFormDataViewModel) {
        var that = this;
        if (localStorage.getItem('transactionId') != null && localStorage.getItem('transactionId') != '0') {
            var transactionId = localStorage.getItem("transactionId");
            values.transactionId = +transactionId;
            values.cardTypeId = this.state.cardTypeId;
            values.cardNumber = this.state.cardNumber;
            values.paymentOption = this.state.paymentOption;
            switch (+values.paymentOption) {
                case 1:
                    values.paymentGateway = "1";
                    break;
                case 4:
                    values.paymentGateway = "4";
                    break;
                case 16:
                    values.paymentGateway = "4";
                    break;
            }

            if (values.paymentGateway == "1") {
                (window as any).Stripe.setPublishableKey('pk_live_3lHeU2G6UkNDiQJRrVgAWQ4N');
                var card: StripePaymentModel = {
                    number: that.state.cardNumber,
                    cvc: values.cvv,
                    exp_month: values.expiryMonth.toString(),
                    exp_year: values.expiryYear.toString(),
                };
                (window as any).Stripe.createToken(card, function (status, response) {
                    if (response.error) {
                        alert(response.error.message);
                        that.props.requestPaymentOptionData();
                    }
                    else {
                        values.token = response.id;
                        that.props.proceedToPayment(values, (response: PaymentFormResponseViewModel) => {
                            if (response.success && response.action != "") {
                                that.props.history.replace({ pathname: "/paymentform", state: { paymentInfo: response } });
                            }
                            else if (response.success && response.action == "" && response.errorMessage == "") {
                                that.props.history.push({ pathname: "/order-confirmation" });
                            }
                            else {
                                that.props.history.push({ pathname: "/pgerror", search: response.errorMessage });
                            }
                        });
                    }
                });
            } else {
                that.props.proceedToPayment(values, (response: PaymentFormResponseViewModel) => {
                    if (response.success && response.action != "") {
                        that.props.history.replace({ pathname: "/paymentform", state: { paymentInfo: response } });
                    }
                    else if (response.success && response.action == "" && response.errorMessage == "") {
                        that.props.history.push({ pathname: "/order-confirmation" });
                    }
                    else {
                        that.props.history.push({ pathname: "/pgerror", search: response.errorMessage });
                    }
                });
            }
        } else {
            that.props.history.replace("/");
        }
    }

    private getSchema() {
        return Yup.object().shape({
        });
    }
}

export default connect(
    (state: IApplicationState) => state.Payment,
    PaymentStore.actionCreators
)(Payment);



