import { Field } from "formik";
import * as React from "react";
import AccountAddressForm from "./AccountAddressForm";
import { SaveAddressFormDataViewModel } from "../../../models/Account/SaveAddressFormDataViewModel";
import CountryDataViewModel from "../../../models/Country/CountryDataViewModel"
import Yup from "yup";

interface ISaveAddressFormProps {
    onSubmit: (values: SaveAddressFormDataViewModel) => void;
    onCancel: () => void;
    addressType: number;
    countryData: CountryDataViewModel;
}

export class SaveAddressForm extends React.Component<ISaveAddressFormProps, {}> {
    public render() {
        const schema = this.getSchema();
        var phoneCode = this.props.countryData.countries.map((item) => {
            return <option value={item.phonecode}>{item.name + " (" + item.phonecode + ")"}</option>
        });
        var countries = this.props.countryData.countries.map((item) => {
            return <option value={item.phonecode}>{item.name}</option>
        });
        return (
            <AccountAddressForm {...this.props} validationSchema={schema} initialValues={{ addressTypeId: this.props.addressType }}>
                <div>
                      <h5 className="mt-3">New Address</h5>
                        <div className="form-group">
                        <div className="row">
                            <div className="col-sm-6">
                                <Field type="firstName" name="firstName" placeholder="First Name" className="form-control " required />                                
                            </div>
                            <div className="col-sm-6">
                                <Field type="lastName" name="lastName" placeholder="Last Name" className="form-control " required />
                            </div>
                            </div>
                        </div>
                    <div className="form-group">
                        <div className="row">
                            <div className="col-sm-5">
                                <Field type="phoneCode" name="phoneCode" component="select" className="form-control" required>
                                    <option>Country Code</option>
                                    {phoneCode}
                                </Field>                                
                            </div>
                            <div className="col-sm-7">
                                <Field type="phoneNumber" name="phoneNumber" placeholder="Phone" className="form-control" required />
                            </div>
                        </div>
                        </div>
                    <div className="form-group">
                        <Field type="country" name="country" id="country" component="select" className="form-control" required>
                            <option>Select Country</option>
                            {countries}   
                        </Field>
                        </div>
                    <div className="form-group">
                        <Field type="addressLine1" name="addressLine1" placeholder="Apt, Floor, Suite, Street Address, etc." className="form-control" required />    
                        </div>
                    <div className="form-group">
                        <Field type="zipcode" name="zipcode" placeholder="Zip Code" className="form-control " required />                                                                                                                     
                    </div>    
                    </div>
            </AccountAddressForm>
        );
    }

    private getSchema() {
        return Yup.object().shape({
            firstName: Yup.string().required(),
            lastName: Yup.string().required(),   
            phoneCode: Yup.string().required(),   
            addressLine1: Yup.string().required(),            
            zipcode: Yup.number().required(),
        });
    }
}



