import { autobind } from "core-decorators";
import * as React from "react";
import * as ReactDOM from "react-dom";
import { actionCreators as sessionActionCreators, ISessionProps } from "shared/stores/Session";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { Link, RouteComponentProps } from "react-router-dom";
import { IApplicationState } from "../stores";
import * as Checkoutstore from "../stores/Checkout";
import { LoginFormDataViewModel } from "shared/models/LoginFormDataViewModel";
import { GuestCheckoutFormDataViewModel } from "../models/Checkout/GuestCheckoutFormDataViewModel";
import { UserCheckoutResponseViewModel } from "../models/UserCheckoutResponseViewModel";
import { UserCheckoutLoginForm } from "../components/Checkout/UserCheckoutLoginForm";
import GuestCheckoutForm from "../components/Checkout/GuestCheckoutForm";
import { GuestCheckoutResponseViewModel } from "../models/GuestCheckoutResponseViewModel";
import { EventTicketDetailDataViewModel, EventTicketAttribute } from "../models/Checkout/EventTicketDetailDataViewModel";
import { CheckoutSignInDataViewModel } from "../models/Checkout/CheckoutSignInDataViewModel";
import { LoginTransactionFormDataViewModel, GuestTransactionFormDataViewModel } from "../models/Checkout/TransactionFormDataViewModel"
import FeelLoader from "../components/Loader/FeelLoader";
import { UserCheckoutLoginComponent } from "../components/Account/UserCheckoutLoginComponent";
import { ForgotPasswordComponent } from "../components/Account/ForgotPasswordComponent";
import { ForgotPasswordFormDataViewModel } from "shared/models/ForgotPasswordFormDataViewModel";
import SignInGoogle from "../components/Account/SocialLogins/SignInGoogle";
import SignInFacebook from "../components/Account/SocialLogins/SignInFacebook";
import "../scss/_checkout.scss";
import Metatag from "../components/Metatags/Metatag";

type CheckoutProps =
    Checkoutstore.ICheckoutLoginProps
    & typeof Checkoutstore.actionCreators
    & ISessionProps
    & typeof sessionActionCreators
    & RouteComponentProps<{}>;

class Checkout extends React.Component<CheckoutProps, any> {
    public componentDidMount() {
        if (window) {
            window.scrollTo(0, 0)
        }
        if (this.props.session.isAuthenticated) {
            if (localStorage.getItem('cartItems') != null && localStorage.getItem('cartItems') != '0') {
                var data = JSON.parse(localStorage.getItem("cartItems"));
                var ticketData = [];
                data.forEach(function (val) {
                    let eventDetail: EventTicketAttribute = {
                        id: val.eventTicketAttributeId,
                        ticketType: 1,
                        totalTickets: val.quantity,
                        guestList: val.guestList,
                    };
                    ticketData.push(eventDetail);
                });
                var userCheckoutData: CheckoutSignInDataViewModel = {
                    userAltId: this.props.session.user.altId,
                    eventTicketAttributeList: ticketData,
                }
                this.props.checkoutLoginToDeliveryOption(userCheckoutData, (response: UserCheckoutResponseViewModel) => {
                    if (response.success) {
                        localStorage.setItem('transactionId', response.transactionId.toString());
                        this.props.history.push("/delivery-options");
                    } else {
                        alert("Invalid credentials");
                        this.props.requestCountryData();
                    }
                });
            } else {
                this.props.history.replace("/");
            }
        } else {
            this.props.requestCountryData();
        }
    }
    public constructor() {
        super();
        this.state = {
            currentPage: "checkout",
            default: 0
        };
    }
    public showPage(page, e) {
        e.preventDefault();
        this.setState({
            currentPage: page
        });
    }
    public render() {
        if (this.props.checkout.fetchCountriesSuccess) {
            return <div>
                <Metatag url={this.props.location.pathname} title="Checkout Page" />
                <div className="container sign-form pt-3 pb-5 inner-banner">
                    <div className="card">
                        <div className="card-header h5 bg-white">
                            Please Sign In <small className="float-right text-muted"><i className="fa fa-lock text-success"></i> Secure</small>
                        </div>

                        <div className="card-body">
                            <div className="row">

                                <div className="col-lg-6 col-sm-12 sign-form-left">
                                    <h4>Guest Checkout</h4>
                                    <p className="text-gray">Proceed to checkout as a guest, an account will be automatically created for you and login credentials emailed once you click Continue.</p>
                                    <GuestCheckoutForm onSubmit={this.onSubmitGuestCheckOut} countryData={this.props.checkout.countryList} />
                                </div>
                                <div className="col-lg-6 col-sm-12 sign-form-left">
                                    {
                                        this.state.currentPage === "checkout"
                                        && <UserCheckoutLoginComponent invalidCredentials={this.props.checkout.invalidCredentials} onSubmit={this.onSubmitSignIn} switchPage={this.showPage.bind(this, "forgotPassword")} />
                                    }
                                    {
                                        this.state.currentPage === "forgotPassword"
                                        && <ForgotPasswordComponent passwordResetMailSent={this.props.checkout.changePasswordMailSent} invalidCredentials={this.props.checkout.invalidEmail} switchPage={this.showPage.bind(this, "checkout")} submitForgotPassword={this.onSubmitForgotPassword.bind(this)} />
                                    }

                                    <SignInFacebook />
                                    <SignInGoogle />
                                </div>

                            </div>
                        </div>
                        <div className="card-footer">
                            <a href="#"><button type="button" className="btn btn-outline-dark">Cancel</button></a>
                            <small className="float-right">Questions? Write to us at <a href="mailto:support@feelaplace.com" className="btn btn-link"><img alt="Feel Mail Icon" src="https://s3-us-west-2.amazonaws.com/feel-cdn/images/feel-mail-icon.png"/></a></small>
                        </div>
                    </div>
                </div>
            </div>
        } else {
            return <div>
                <FeelLoader />
            </div>;
        }
    }

    @autobind
    private onSubmitSignIn(values: LoginFormDataViewModel) {
        if (localStorage.getItem('cartItems') != null && localStorage.getItem('cartItems') != '0') {
            var data = JSON.parse(localStorage.getItem("cartItems"));
            var ticketData = [];
            data.forEach(function (val) {
                let eventDetail: EventTicketAttribute = {
                    id: val.eventTicketAttributeId,
                    ticketType: 1,
                    totalTickets: val.quantity,
                    guestList: val.guestList,
                };
                ticketData.push(eventDetail);
            });
            var userCheckoutData: LoginTransactionFormDataViewModel = {
                userDetail: values,
                eventTicketAttributeList: ticketData,
            }
            this.props.checkoutLogin(userCheckoutData, (response: UserCheckoutResponseViewModel) => {
                if (response.success) {
                    localStorage.setItem('transactionId', response.transactionId.toString());
                    this.props.history.push("/delivery-options");
                } else {
                    alert("Invalid credentials");
                    this.props.requestCountryData();
                }
            });
        } else {
            this.props.history.replace("/");
        }
    }
    @autobind
    private onSubmitGuestCheckOut(values: GuestCheckoutFormDataViewModel) {
        if (localStorage.getItem('cartItems') != null && localStorage.getItem('cartItems') != '0') {
            var data = JSON.parse(localStorage.getItem("cartItems"));
            var ticketData = [];
            data.forEach(function (val) {
                let eventDetail: EventTicketAttribute = {
                    id: val.eventTicketAttributeId,
                    ticketType: 1,
                    totalTickets: val.quantity,
                    guestList: val.guestList,
                };
                ticketData.push(eventDetail);
            });
            var guestCheckoutData: GuestTransactionFormDataViewModel = {
                userDetail: values,
                eventTicketAttributeList: ticketData,
            }
            this.props.guestRegister(guestCheckoutData, (response: GuestCheckoutResponseViewModel) => {
                if (response.success) {
                    localStorage.setItem('transactionId', response.transactionId.toString());
                    this.props.history.push("/delivery-options");
                }
            });
        } else {
            this.props.history.replace("/");
        }
    }
    @autobind
    private onSubmitForgotPassword(values: ForgotPasswordFormDataViewModel) {
        this.props.forgotPassword(values, (response) => {
            if (response.success) {
                this.props.requestCountryData();
            }
        });
    }
}

export default connect(
    (state: IApplicationState) => ({ session: state.session, checkout: state.checkout }),
    (dispatch) => bindActionCreators({ ...sessionActionCreators, ...Checkoutstore.actionCreators }, dispatch)
)(Checkout);



