import * as React from "react";
import * as map from "lodash/map";
import {
    Nav,
    Navbar,
    NavbarBrand,
    NavItem, Carousel
} from "react-bootstrap";
import { Link } from "react-router-dom";
import { LinkContainer } from "react-router-bootstrap";
import { CategoryViewModel } from "../models/CategoryViewModel";
import "../scss/_header.scss";
import "../scss/_custom.scss";
import "../scss/_tiles.scss";
import "../scss/_custome_Carousel.scss";
import ProjectFeelSearchExplore from "../components/Home/ProjectFeelSearchExplore";
import CustomerUpdate from "../components/CustomerUpdate/CustomerUpdate";
import NavCart from "../components/NavCart/NavCart";
import FeelList from "../components/NavCart/FeelList";
import * as PubSub from 'pubsub-js';
import axios from "axios";
import { autobind } from "core-decorators";
import { ChangeProfilePicDataViewModel } from "../models/Account/ChangeProfilePicDataViewModel";
import { ChangeProfilePicResponseViewModel } from "../models/Account/ChangeProfilePicResponseViewModel";
import * as SessionState from "shared/stores/Session";
import { ISessionState } from "shared/stores/Session";

interface INavProps {
    isOverlay: boolean;
    categories: CategoryViewModel[];
    onLogout: () => void;
    session: ISessionState;

}

export class AuthedNavMenu extends React.Component<INavProps, any> {
    // TODO: XXX: need to make logoName dynamic
    inputElement: any;
    constructor(props) {
        super(props);
        this.state = {
            updateProfileSuccess: 0,
        };
    }
    public componentWillMount() {
        this.setState({ logoName: "aplace", cartCount: 0 });
        PubSub.subscribe('UPDATE_CART_COUNT_EVENT', this.subscriber.bind(this));
    }

    public subscriber(msg, data) {
        var that = this;
        if (localStorage.getItem('currentCartItems') != null && localStorage.getItem('currentCartItems') != '0') {
            var data = JSON.parse(localStorage.getItem('currentCartItems'));
            this.setState({ cartCount: data.length });
        }
    }


    public componentDidMount() {
        var logoName = "aplace";
        var origin = (window as any).location.origin;

        var siteName = origin.split(".")[1];
        if (origin.indexOf("feel") > -1) {
            if (siteName == "feelaplace") {
                logoName = "aplace";
            } else {
                logoName = siteName.slice(4).toLowerCase();
            }
        }
        if (localStorage.getItem('currentCartItems') != null && localStorage.getItem('currentCartItems') != '0' && localStorage.getItem('currentCartItems') !== "undefined") {
            var data = JSON.parse(localStorage.getItem('currentCartItems'));
            this.setState({ cartCount: data.length });
        }
        if ((window as any).location.href.indexOf("order-confirmation") > -1) {
            this.setState({ cartCount: 0 });
        }
        this.setState({ logoName: logoName });
    }


    @autobind
    handleUploadFile(event) {
        var formData = new FormData();
        formData.append('file', event.target.files[0]);
        this.setState({ isLoading: true });
        axios.post("/api/upload/profilepicture", formData, {
            headers: {
                "Content-Type": "multipart/form-data"
            }
        }).then((response) => {
            if (response.data == true) {
                this.uploadprofileShow();
            }
        });
    }

    @autobind
    handleUploddProfilePicture() {
        this.inputElement.click();
    }

    @autobind
    profileShow() {
        this.setState({ updateProfileSuccess: this.state.updateProfileSuccess + 1 });
    }

    @autobind
    uploadprofileShow() {
        this.setState({ updateProfileSuccess: this.state.updateProfileSuccess - this.state.updateProfileSuccess });
        this.setState({ isLoading: false });
    }
    public render() {
        var cartItemCount;
        var that = this;
        if (this.props.session.user.profilePic == true) {
            axios.get("/api/account/profilepicture")
                .then(function (response) {
                    that.setState({ profileImage: response.data, updateProfileSuccess: 1 });
                    that.profileShow();
                })
        }
        else if (this.state.updateProfileSuccess === 0) {
            this.setState({ profileImage: "https://s3-us-west-2.amazonaws.com/kz-cdn/Images/ProfilePictures/default.jpg", updateProfileSuccess: 1 });
        }
        // TODO: XXX: Cart, My Account and Notifications
        return <header className="site-header">
            <nav className="navbar navbar-expand-lg navbar-light fixed-top">
                <div className="container-fluid">
                    <a className="navbar-brand p-0" href="#"><img src={"https://static1.feelaplace.com/images/logos/feel-" + this.state.logoName + ".png"} alt={"Feel " + this.state.logoName + " Logo"} title={"Feel " + this.state.logoName + " Logo"} /></a>
                    <ul className="navbar-nav">
                        <li className="nav-item active">
                            <a className="nav-link" href="coming-soon"> <span className="btn btn-outline-primary btn-sm">Host a feel</span></a>
                        </li>
                        <li className="nav-item dropdown">
                            <Link to="/itinerary" data-toggle="dropdown" className="nav-link" role="button" aria-expanded="false">
                                <img src="https://static1.feelaplace.com/images/header/cart-icon-fill.png" alt="Feel Cart Icon" />
                            </Link>
                            <ul className="dropdown-menu dropdown-cart p-2">
                                <NavCart isAuth={true} />
                            </ul>
                        </li>
                        <li className="nav-item dropdown">
                            <Link to="/itinerary" data-toggle="dropdown" className="nav-link" role="button" aria-expanded="false">
                                <img src="https://s3-us-west-2.amazonaws.com/feel-cdn/images/header/itinerary-icon.png" alt="Feel Itinerary Icon" />
                            </Link>
                            <ul className="dropdown-menu dropdown-cart p-2">
                                <FeelList />
                            </ul>
                        </li>
                        <li className="nav-item dropdown">
                            <CustomerUpdate />
                        </li>
                        <li className="nav-item dropdown">
                            <a className="nav-link" href="#" id="navbarDropdown2" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                <img alt="Feel User Icon" src="https://s3-us-west-2.amazonaws.com/feel-cdn/images/header/user-icon.png" />
                            </a>
                            <div className="dropdown-menu" aria-labelledby="navbarDropdown2">
                                <li>
                                    <a href="javascript:void(0);">
                                        <input type="file" onChange={this.handleUploadFile} ref={input => this.inputElement = input} style={{ display: "none" }} />
                                        <img src={this.state.profileImage} className="user-profile" onClick={this.handleUploddProfilePicture.bind(this)} />
                                        {this.props.session.user.firstName}</a>
                                </li>
                                <Link className="dropdown-item border-bottom" to="/account">My Account</Link>
                                <a className="dropdown-item" href="#" onClick={this.props.onLogout}>Sign Out</a>
                            </div>
                        </li>
                    </ul>
                </div>
            </nav>
        </header>;
    }
}



