import { autobind } from "core-decorators";
import * as React from "react";
import { connect } from "react-redux";
import { RouteComponentProps } from "react-router-dom";
import { Grid, Row } from "react-bootstrap";
import { actionCreators as sessionActionCreators, ISessionProps } from "shared/stores/Session";
import { IApplicationState } from "../stores";
import GoogleTagManager from "./analytics/GoogleTagManager";
import Metatag from "../components/Metatags/Metatag";
import { AuthedNavMenu } from "./AuthedNavMenu";
import Footer from "./footer/FooterBar";
import { NavMenu } from "./NavMenu";
import { CategoryViewModel } from "../models/CategoryViewModel";
import { Helmet } from "react-helmet";
import * as CookieConsent from "react-cookie-consent";

interface ILayoutProps {
    gtmId: string;
    categories: CategoryViewModel[];
    url: string;
}

type LayoutProps =
    ILayoutProps
    & ISessionProps
    & typeof sessionActionCreators;

class Layout extends React.Component<LayoutProps, {}> {
    private getMenu() {
        let isHome = this.props.url == "/";
        if (this.props.session.isAuthenticated) {
            return <AuthedNavMenu
                isOverlay={isHome}
                categories={this.props.categories}
                onLogout={this.props.logout}
                session={this.props.session}
            />;

        }
        return <NavMenu
            isOverlay={isHome}
            categories={this.props.categories} />
    }

    public render() {
        return <div className="d-inline">
            <Metatag url={null} />
            <GoogleTagManager gtmId={this.props.gtmId} />
            {this.getMenu()}
            {this.props.children}
            <Footer />
            {/*<CookieConsent
                location="bottom"
                buttonText="Got it!"
                cookieName="feelCookieConsent"
                style={{ background: "#2B373B" }}
                buttonStyle={{ color: "#4e503b", fontSize: "13px" }}
            >
                This website uses cookies to enhance the user experience. <a href="https://cookies.insites.com/">Learn More</a>
            </CookieConsent>*/}
        </div>;
    }
}

export default connect(
    (state: IApplicationState, ownProps) => ({ session: state.session, ...ownProps }),
    sessionActionCreators
)(Layout);



