import * as numeral from "numeral";
import * as React from "react";
import { Link } from "react-router-dom";
import BespokeWishListViewModel from "../../models/BespokeWishList/BespokeWishListViewModel";
import * as PubSub from 'pubsub-js';

export default class FeelList extends React.Component<any, any> {
    public constructor() {
        super();
        this.state = { cartData: "", isRedirect: true, isSort: 0, isPaymentPage: false, isOrderConfirmation: false };
    }

    public componentWillMount() {
        this.setState({ cartData: "" });
        PubSub.subscribe('UPDATE_NAV_WISHLIST_DATA_EVENT', this.subscriberData.bind(this));        
    }

    //public componentWillUnmount() {
    //    PubSub.unsubscribe('UPDATE_NAV_CART_DATA_ON_CART_REMOVE_EVENT');
    //}

    public subscriberData(msg, data) {
        var that = this;
        if (localStorage.getItem('bespokeItems') != null && localStorage.getItem('bespokeItems') != '0') {
            var data = JSON.parse(localStorage.getItem('bespokeItems'));

            this.setState({ cartData: data });
        }
        this.setState({ cartError: false });
        if ((window as any).location.href.indexOf("payment") > -1) {
            this.setState({ isPaymentPage: true });
        }
    }

    public subscribeNavCartData(msg, data) {
        var that = this;
        if (localStorage.getItem('bespokeItems') != null && localStorage.getItem('bespokeItems') != '0') {
            var data = JSON.parse(localStorage.getItem('bespokeItems'));
            this.setState({ cartData: data });
        } else {
            localStorage.removeItem('bespokeItems');
            this.setState({ cartData: null });
        }
        this.setState({ cartError: false });
        if ((window as any).location.href.indexOf("payment") > -1) {
            this.setState({ isPaymentPage: true });
        }
    }

    public componentDidMount() {
        if ((window as any).location.href.indexOf("payment") > -1) {
            this.setState({ isPaymentPage: true });
        }
        if ((window as any).location.href.indexOf("order-confirmation") > -1) {
            this.setState({ isOrderConfirmation: true });
        }
        if (localStorage.getItem('bespokeItems') != null && localStorage.getItem('bespokeItems') != '0' && localStorage.getItem('bespokeItems') !== "undefined") {
            var data = JSON.parse(localStorage.getItem('bespokeItems'));
            this.setState({ cartData: data, cartError: false });
        }
    }

    public loadCartItems(data) {
        var that = this;
        if (data != "" && (this.state.isOrderConfirmation == false)) {
            var cartData;
            if (localStorage.getItem('cartItems') === "") {
                cartData = null;
                localStorage.removeItem('cartItems');
            } else {
                cartData = JSON.parse(localStorage.getItem('cartItems'));
            }
            data.map(function(val) {
                var eventTicketAttributeId = val.eventTicketAttributeId;
                var quantity = val.quantity;
                that.setState({
                    [eventTicketAttributeId]: quantity,
                    isSort: that.state.isSort + 1,
                    cartData: cartData
                });
            });
        } else {
            this.setState({ cartData: "" });
        }
    }
    

    public getUnique(data) {
        return data.filter(function (x, i) {
            return data.indexOf(x) === i
        })
    }
    
    public addToBespokeItinerary(item, e) {
        
            var that = this;
            var bespokeData = (localStorage.getItem('bespokeItems') != null && localStorage.getItem('bespokeItems') != '0') ? JSON.parse(localStorage.getItem("bespokeItems")) : [];

            if (bespokeData.length > 0) {
                bespokeData = bespokeData.filter(function (val) { return val.userAltId == localStorage.getItem('userToken') });
            }
            var isPlaceExists = bespokeData.filter(function (val) { return val.cartItems.categoryEvent.altId == item.categoryEvent.altId });

            if (isPlaceExists.length == 0) {
                var userCheckoutData: BespokeWishListViewModel = {
                    cartItems: item,
                    userAltId: localStorage.getItem('userToken')
                }
                bespokeData.push(userCheckoutData);
                var allWishlistexceptCurrent;
                if (localStorage.getItem("bespokeItems") !== null && localStorage.getItem('bespokeItems') != '0') {
                    allWishlistexceptCurrent = JSON.parse(localStorage.getItem("bespokeItems")).filter(function (val) { return val.userAltId !== localStorage.getItem('userToken') });
                    allWishlistexceptCurrent.forEach(function (val) {
                        bespokeData.push(val);
                    });
                }
                localStorage.setItem('bespokeItems', JSON.stringify(bespokeData));
                that.setState({
                    [item.categoryEvent.altId]: true
                })
            } else {
                var i = -1;
                localStorage.setItem("currentAltId", item.categoryEvent.altId);
                if (bespokeData.length == 1) {
                    var allWishlistexceptCurrent;
                    if (localStorage.getItem("bespokeItems") !== null && localStorage.getItem('bespokeItems') != '0') {
                        allWishlistexceptCurrent = JSON.parse(localStorage.getItem("bespokeItems")).filter(function (val) { return val.userAltId !== localStorage.getItem('userToken') });
                    }
                    var data = []
                    if (allWishlistexceptCurrent.length > 0) {
                        allWishlistexceptCurrent.forEach(function (val) {
                            data.push(val);
                        });
                        localStorage.setItem('bespokeItems', JSON.stringify(data));
                    } else {
                        localStorage.removeItem('bespokeItems');
                    }
                    that.setState({
                        [item.categoryEvent.altId]: false
                    });
                } else {
                    var bespokeItems = bespokeData.forEach(function (val) {
                        i = i + 1;
                        if (val.cartItems.categoryEvent.altId == item.categoryEvent.altId) {
                            bespokeData.splice(i, 1);
                        }
                    });
                    if (localStorage.getItem("bespokeItems") !== null && localStorage.getItem('bespokeItems') != '0') {
                        allWishlistexceptCurrent = JSON.parse(localStorage.getItem("bespokeItems")).filter(function (val) { return val.userAltId !== localStorage.getItem('userToken') });
                        allWishlistexceptCurrent.forEach(function (val) {
                            bespokeData.push(val);
                        });
                    }
                    localStorage.setItem('bespokeItems', JSON.stringify(bespokeData));
                    that.setState({
                        [item.categoryEvent.altId]: false
                    });
                }
        }
        
        PubSub.publish('UPDATE_WISHLIST_DATA_EVENT', 1);
        PubSub.publish('UPDATE_EVENTLEARNPAGE_WISHLIST_DATA_EVENT', 1);
        PubSub.publish('UPDATE_WISHLIST_PAGE_DATA_EVENT', 1);
    }

    public render() {
        if (this.state.cartData !== "" && this.state.cartData !== null && (this.state.isOrderConfirmation === false)) {
            var bespokeData = (localStorage.getItem('bespokeItems') != null && localStorage.getItem('bespokeItems') != '0') ? JSON.parse(localStorage.getItem("bespokeItems")) : [];
            if (bespokeData.length > 0) {
                bespokeData = bespokeData.filter(function (val) { return val.userAltId == localStorage.getItem('userToken') });
            }
            var data = bespokeData.map(function (item) { return item.cartItems });
           
            var that = this;
            
            var cartItems = data.map(function(val) {
                var imageSrc = "https://static1.feelaplace.com/images/event/HotTickets/" + val.categoryEvent.altId.toUpperCase() + "-ht-c1.jpg";
                return <li className="clearfix">
                    {(!that.state.isPaymentPage) && <a href="javascript:void(0)" onClick={that.addToBespokeItinerary.bind(that, val)} className="close">x</a>}
                   <div className="float-left">
                        <img className="rounded" src={imageSrc} />
                    </div>
                    <div className="float-right">
                        <span>
                            {val.categoryEvent.name}<br />
                        </span>
                    </div>
                </li>
            })
            return <div>
                {cartItems}
                <li>                    
                    {(data.length == 0) && <div>You have not added any place in your wishlist.</div>}
                </li>
            </div>
        } else {
            return <div className="card-body">
               You have not added any place in your wishlist.
            </div>;
        }
    }
}



