import * as React from "React";

export default class InnerBanner extends React.Component<any, any> {
    public constructor(props) {
        super(props);

        this.state = {
            photoIndex: 0,
            isOpen: false,
        };
    }

    public render() {
        var imageList = this.props.eventImageList;
        var images = [];
        for (var i = 1; i <= imageList.length; i++) {
            images.push('https://static1.feelaplace.com/images/event/AboutEvent/photo-gallery/' + this.props.eventImageId.toUpperCase() + '-glr-' + i + '.jpg');
        }        
        const { photoIndex, isOpen } = this.state;
        return <div className="inner-banner photogallery">
            <img src={"https://static1.feelaplace.com/images/event/AboutEvent/" + this.props.eventImageId.toUpperCase() + "-about.jpg"} alt="" className="card-img" />
            <div className="card-img-overlay">
                <nav aria-label="breadcrumb">
                    <ol className="breadcrumb">
                        <li className="breadcrumb-item"><a href="#">Home</a></li>
                        <li className="breadcrumb-item"><a href="#">{this.props.eventCategoryName}</a></li>
                        <li className="breadcrumb-item"><a href="#">{this.props.eventCat}</a></li>
                        <li className="breadcrumb-item active" aria-current="page">{this.props.eventName}</li>
                    </ol>
                </nav>
            </div>
            <button type="button" onClick={() => this.setState({ isOpen: true })} className="btn btn-light text-uppercase" ><i className="fa fa-file-image-o"></i> View Photos</button>
            {this.state.isOpen && <div id="myModal" className="modal d-block">
                <span onClick={() => this.setState({ isOpen: false })} className="close cursor">&times;</span>
                <div className="modal-content">
                    <div className="mySlides d-block">                        
                        <img src={images[photoIndex]} className="img-width w-100" />
                    </div>
                    <a className="prev" onClick={() =>
                        this.setState({
                            photoIndex: (photoIndex + images.length - 1) % images.length,
                        })
                    } >&#10094;</a>
                    <a className="next" onClick={() =>
                        this.setState({
                            photoIndex: (photoIndex + 1) % images.length,
                        })
                    } >&#10095;</a>
                </div>
            </div>}
        </div>
    }
}


