import { Field } from "formik";
import * as React from "react";
import UserCheckout from "./UserCheckout";
import { LoginFormDataViewModel } from "shared/models/LoginFormDataViewModel";
import Yup from "yup";
import "../../views/Checkout.scss";

interface IUserCheckoutLoginFormProps {
    onSubmit: (values: LoginFormDataViewModel) => void;
    onClick: () => void;

}

export class UserCheckoutLoginForm extends React.Component<IUserCheckoutLoginFormProps, any> {
    public componentWillMount() {
        this.setState({ showPassword: false });
    }

    public toggleShowPassword(e) {
        this.setState({ showPassword: (!this.state.showPassword) });
    }

    public render() {
        const schema = this.getSchema();
        var inputType = this.state.showPassword ? "text" : "password";
        return (
            <UserCheckout {...this.props} validationSchema={schema} initialValues={{}}>
                <div className="form-group">
                    <Field type="email" name="email" placeholder="Enter the email address" className="form-control" required />
                </div>
                <div className="form-group">
                    <Field type={inputType} name="password" placeholder="Enter password" className="form-control" required />
                </div>
                <div className="custom-control custom-checkbox my-1 mb-3">
                    <input type="checkbox" className="custom-control-input" id="customControlInline" name="customControlInline" />
                    <label className="custom-control-label" htmlFor="customControlInline"> <small>Remember me</small> </label>
                    <a href="javascript:void(0)" onClick={this.toggleShowPassword.bind(this)} className="float-right"> <small>Show password</small> </a>
                </div>
            </UserCheckout>
        );
    }

    private getSchema() {
        return Yup.object().shape({
            email: Yup.string().email().required(),
            password: Yup.string().required()
        });
    }
}



