import { autobind } from "core-decorators";
import * as React from "react";
import { RouteComponentProps, Link } from "react-router-dom";
import { Grid, Row, Col, Button, Alert, Carousel } from "react-bootstrap";
import { actionCreators as sessionActionCreators, ISessionProps } from "shared/stores/Session";
import { IApplicationState } from "../stores";
import * as CategoryStore from "../stores/Category";
import { CategoryDataModel } from "../models/CategoryDataModel";
import NewsLetterSignupFooter from "../components/Footer/NewsLetterSignupFooter";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import MonumentsTickets from "../components/Home/MonumentsTickets";
import Search from "../components/Home/Search";
import "../scss/_home.scss";
import ProjectFeelExploreSelection from "../components/Home/ProjectFeelExploreSelection";


type ProjectFeelExploreSelectionComponentProps = CategoryStore.ICategoryProps & typeof CategoryStore.actionCreators
    & ISessionProps & typeof sessionActionCreators & RouteComponentProps<{}>;


class Home extends React.Component<ProjectFeelExploreSelectionComponentProps, any> {
    public componentWillMount() {
        this.setState({ citySearchKeyword: "", stateSearchKeyword: "", countrySearchKeyword: "", defaultSearchValue: "", siteName: "feelaplace" });
    }

    public componentDidMount() {
        if (window) {
            window.scrollTo(0, 0)
        }
        if (localStorage.getItem("clear20180721") === null) {
            localStorage.removeItem("bespokeItems");
            localStorage.setItem("clear20180721", "true");
        }
        localStorage.removeItem("isRedirectHomePage");
        var siteName = "feelaplace";
        var origin = (window as any).location.origin;
        var site = origin.split(".")[1];
        if (origin.indexOf("feel") > -1) {
            siteName = site.toLowerCase();
        }
        this.setState({ siteName: siteName });
        if (this.props.category.clearSearchSuccess) {
            this.setState({ citySearchKeyword: "", stateSearchKeyword: "", countrySearchKeyword: "", defaultSearchValue: "" });
        }
        this.props.requestSiteContent();
    }

    public clearSearchAction(e) {
        this.props.clearSearchAction();
        this.setState({ citySearchKeyword: "", stateSearchKeyword: "", countrySearchKeyword: "", defaultSearchValue: "" });
    }

    public setCitySearchKeyword(search) {
        this.setState({ citySearchKeyword: search, stateSearchKeyword: "", countrySearchKeyword: "", defaultSearchValue: search });
    }

    public setStateSearchKeyword(search) {
        this.setState({ citySearchKeyword: "", stateSearchKeyword: search, countrySearchKeyword: "", defaultSearchValue: search });
    }

    public setCountrySearchKeyword(search) {
        this.setState({ citySearchKeyword: "", stateSearchKeyword: "", countrySearchKeyword: search, defaultSearchValue: search });
    }

    public goToLogin() {
        localStorage.setItem("isRedirectHomePage", "true");
        this.props.history.push("/login")
    }

    public render() {
        var that = this;
        var banners = this.props.category.siteBanners.map(function (val, index) {
            var bannerClass = index == 0 ? "carousel-item active" : "carousel-item";
            return <div className={bannerClass}>
                <img src={"https://static1.feelaplace.com/images/header/slider/" + that.state.siteName + "/feel-home-slider-" + (index + 1) + ".jpg"} alt="feel india" className="img-fluid" />
            </div>
        });

        return <div className="d-inline">
            <div className="home-slider">
                <div id="carouselExampleIndicators" className="carousel slide carousel-fade" data-ride="carousel">
                    <div className="carousel-inner" role="listbox">
                        {banners}
                    </div>
                </div>
                <div className="header-search container text-white">
                    <div className="row">
                        <h1>{this.props.category.fetchsuccess ? this.props.category.content.siteTitle : ""}<br />
                            <small>{this.props.category.fetchsuccess ? this.props.category.content.bannerText : ""}</small>
                        </h1>
                        <Search clearSearchAction={this.clearSearchAction.bind(this)} searchAction={this.props.searchAction} searchSuccess={this.props.category.searchSuccess} clearSearchSuccess={this.props.category.clearSearchSuccess} searchResult={this.props.category.searchResult} setCitySearchKeyword={this.setCitySearchKeyword.bind(this)} setStateSearchKeyword={this.setStateSearchKeyword.bind(this)} setCountrySearchKeyword={this.setCountrySearchKeyword.bind(this)} defaultSearchValue={this.state.defaultSearchValue}
                            defaultCities={this.props.category.defaultSearchCities} defaultStates={this.props.category.defaultSearchStates} defaultCountries={this.props.category.defaultSearchCountries} siteLevel={this.props.category.content.siteLevel} />
                    </div>
                </div>
            </div>
            <ProjectFeelExploreSelection push={() => this.goToLogin.bind(this)} isAuthenticated={this.props.session.isAuthenticated} getCategoryEvents={this.props.getCategoryEvents} requestCategoryData={this.props.requestCategoryData} category={this.props.category} cityFilterKeyword={this.state.citySearchKeyword} stateFilterKeyword={this.state.stateSearchKeyword} countryFilterKeyword={this.state.countrySearchKeyword} clearSearchSuccess={this.props.category.clearSearchSuccess} />
            < NewsLetterSignupFooter />
        </div>;
    }
}

export default connect(
    (state: IApplicationState) => ({ session: state.session, category: state.category }),
    (dispatch) => bindActionCreators({ ...sessionActionCreators, ...CategoryStore.actionCreators }, dispatch)
)(Home);



