import { Field } from "formik";
import * as React from "react";
import GuestCheckout from "./GuestCheckout";
import CountryDataViewModel from "../../models/Country/CountryDataViewModel"
import { GuestCheckoutFormDataViewModel } from "../../models/Checkout/GuestCheckoutFormDataViewModel";
import { Link, RouteComponentProps } from "react-router-dom";
import Yup from "yup";
import "../../views/Checkout.scss";

interface IGuestCheckoutFormProps {
    onSubmit: (values: GuestCheckoutFormDataViewModel) => void;
    countryData: CountryDataViewModel;
}

export default class GuestCheckoutForm extends React.Component<IGuestCheckoutFormProps, any> {
    public render() {
        const schema = this.getSchema();
        var countries = this.props.countryData.countries.map((item) => {
            return <option value={item.phonecode}>{item.name + " (" + item.phonecode + ")"}</option>
        });
        return (
            <GuestCheckout {...this.props} validationSchema={schema} initialValues={{}}>
                <div className="form-grp-sml">
                    <div className="form-group">
                        <Field type="firstName" name="firstName" className="form-control" placeholder="First Name" required />
                    </div>
                </div>
                <div className="form-grp-big">
                    <div className="form-group">
                        <Field type="lastName" name="lastName" className="form-control" placeholder="Last Name" required />
                    </div>
                </div>
                <div className="form-group">
                    <Field type="email" className="form-control" name="email" placeholder="Enter the email address" required />
                </div>
                <div className="form-grp-sml">
                    <div className="form-group">
                        <Field component="select" type="phoneCode" name="phoneCode" className="form-control">
                            <option>Select Country</option>
                            {countries}
                        </Field>
                    </div>
                </div>
                <div className="form-grp-big">
                    <div className="form-group">
                        <Field type="number" pattern="[0-9]*" className="form-control" name="phoneNumber" placeholder="Mobile Number" required />
                    </div>
                </div>
                <div className="clearfix"></div>
                <p className="font-12 mb-15">
                    <small>By signing up, I agree to Feel <Link to="#" target="_blank" className="text-info">terms of service</Link>, <Link to={"/privacy-policy"} target="_blank" className="text-info">privacy policy.</Link></small>
                </p>
            </GuestCheckout>
        );
    }

    private getSchema() {
        return Yup.object().shape({
            firstName: Yup.string().required(),
            lastName: Yup.string().required(),
            email: Yup.string().email().required(),
            phoneCode: Yup.string().required(),
            phoneNumber: Yup.string().required(),
        });
    }
}



