import { Field } from "formik";
import * as React from "react";
import UserSignIn from "./UserSignIn";
import { LoginFormDataViewModel } from "shared/models/LoginFormDataViewModel";
import Yup from "yup";
import "../../views/Checkout.scss";

interface IUserSignInFormProps {
    onSubmit: (values: LoginFormDataViewModel) => void;
    onClick: () => void;
    toggleRememberMe: () => void;
    rememberMe: boolean;
    email: string;
    password: string;
}

export class UserSignInForm extends React.Component<IUserSignInFormProps, any> {
    public componentWillMount() {       
        this.setState({ showPassword: false });
    }

    public toggleShowPassword(e) {
        this.setState({ showPassword: (!this.state.showPassword) });
    }

    public render() {
        const schema = this.getSchema();
        var inputType = this.state.showPassword ? "text" : "password";
        return (
            <UserSignIn {...this.props} validationSchema={schema} initialValues={{ rememberLogin: this.props.rememberMe }}>
                <div className="form-group">
                    <Field type="email" name="email" placeholder="Enter the email address" defaultValue={this.state.remember ? this.state.email : ""} className="form-control" required />
                </div>
                <div className="form-group">
                    <Field type={inputType} name="password" placeholder="Enter password" defaultValue={this.state.remember ? this.state.password : ""} className="form-control" required />
                </div>
                <div className="custom-control custom-checkbox my-1 mb-3">
                    <input type="checkbox" checked={this.props.rememberMe} onClick={this.props.toggleRememberMe} className="custom-control-input" id="customControlInline" name="customControlInline" />
                    <label className="custom-control-label" htmlFor="customControlInline"> <small>Remember me</small> </label>
                    <a href="javascript:void(0)" onClick={this.toggleShowPassword.bind(this)} className="float-right"> <small>Show password</small> </a>
                </div>
            </UserSignIn>
        );
    }

    private getSchema() {
        return Yup.object().shape({            
        });
    }
}



