import * as React from "react";
import { Link, RouteComponentProps } from "react-router-dom";
import * as isEmpty from "lodash/isEmpty";

export default class CategorySearchResult extends React.Component<any, any> {

    public render() {
        if (this.props.searchSuccess) {
            var seeAndDoResult = this.props.searchResult.filter(e => e.parentCategory == 29).map(function (val) {
                return <Link to={"/place/" + val.altId.toUpperCase()} className="btn btn-sm btn-outline-secondary btn-block border-0 text-left"><i className="fa fa-map-marker mr-2" aria-hidden="true"></i> {val.name + ", " + val.cityName + ", " + val.countryName} </Link>
            });
            var eatAndDrinksResult = this.props.searchResult.filter(e => e.parentCategory == 30).map(function (val) {
                return <Link to={"/place/" + val.altId.toUpperCase()} className="btn btn-sm btn-outline-secondary btn-block border-0 text-left"><i className="fa fa-map-marker mr-2" aria-hidden="true"></i> {val.name + ", " + val.cityName + ", " + val.countryName} </Link>
            });
            var shopLocalResult = this.props.searchResult.filter(e => e.parentCategory == 31).map(function (val) {
                return <Link to={"/place/" + val.altId.toUpperCase()} className="btn btn-sm btn-outline-secondary btn-block border-0 text-left"><i className="fa fa-map-marker mr-2" aria-hidden="true"></i> {val.name + ", " + val.cityName + ", " + val.countryName} </Link>
            });
            var experienceResult = this.props.searchResult.filter(e => e.parentCategory == 32).map(function (val) {
                return <Link to={"/place/" + val.altId.toUpperCase()} className="btn btn-sm btn-outline-secondary btn-block border-0 text-left"><i className="fa fa-map-marker mr-2" aria-hidden="true"></i> {val.name + ", " + val.cityName + ", " + val.countryName} </Link>
            });
            return <div>
                {!isEmpty(seeAndDoResult) && <div>
                    <h6 className="text-secondary">
                        <img src="https://static1.feelaplace.com/images/category-tab-icon/see-do-tickets.png" alt="" width="16" className="mr-2" /> See &amp; do
                </h6>
                    {seeAndDoResult}
                </div>}
                {!isEmpty(eatAndDrinksResult) && <div>
                    <h6 className="text-secondary">
                        <img src="https://static1.feelaplace.com/images/category-tab-icon/eat-drink-tickets.png" alt="" width="16" className="mr-2" /> Eat &amp; drink
                </h6>
                    {eatAndDrinksResult}
                </div>}
                {!isEmpty(shopLocalResult) && <div>
                    <h6 className="text-secondary">
                        <img src="https://static1.feelaplace.com/images/category-tab-icon/shop-local.png" alt="" width="16" className="mr-2" /> Shop local
                </h6>
                    {shopLocalResult}
                </div>}
                {!isEmpty(experienceResult) && <div>
                    <h6 className="text-secondary">
                        <img src="https://static1.feelaplace.com/images/category-tab-icon/experience-explore.png" alt="" width="16" className="mr-2" /> Experience &amp; explore
                </h6>
                    {experienceResult}
                </div>}
            </div>;
        } else if (this.props.emptySearch) {
            return <div>
            </div>;
        }
    }
}



