import * as React from "react";

export default class CitySearchResult extends React.Component<any, any> {
    
    public render() {
        var that = this;
           if (this.props.searchSuccess) {
               var searchResult = this.props.searchResult.map(function (val) {
                   return <button type="button" onClick={() => { that.props.setCitySearchKeyword(val.name) }} className="btn btn-sm btn-outline-secondary mr-1 ml-1 mb-2">{val.name}</button>;
               });
               if (searchResult != "") {
                   return <div>
                       <h6 className="text-secondary">feel a city</h6>
                       {searchResult}
                       <hr />
                   </div>
               }
               else {
                   return <div>                       
                       </div>
               }

           } else if (this.props.emptySearch) {
               //var defaultCities = this.props.defaultCities.length > 5 ? this.props.defaultCities.slice(0, 5) : this.props.defaultCities;
               var defaultCities = this.props.defaultCities;
               var defaultSearch = defaultCities.map(function (val, index) {
                   return <button type="button" onClick={() => { that.props.setCitySearchKeyword(val.name) }} className="btn btn-sm btn-outline-secondary mr-1 ml-1 mb-2">{val.name}</button>;
               })
               return <div>
                <h6 className="text-secondary">feel a city</h6>
                {defaultSearch}
                <hr />
            </div>;
        }
    }
}



