import { Field } from "formik";
import * as React from "react";
import AccountForm from "./AccountForm";
import { ChangePrimaryPhoneFormDataViewModel } from "../../../models/Account/ChangePrimaryPhoneFormDataViewModel";
import CountryDataViewModel from "../../../models/Country/CountryDataViewModel"
import Yup from "yup";

interface IChangePrimaryPhoneFormProps {
    onSubmit: (values: ChangePrimaryPhoneFormDataViewModel) => void;
    onCancel: () => void;
    phoneCode: number;
    phoneNumber: number;
    countryData: CountryDataViewModel;
}

export class ChangePrimaryPhoneForm extends React.Component<IChangePrimaryPhoneFormProps, {}> {
    public render() {
        const schema = this.getSchema();
        var phoneCode = this.props.countryData.countries.map((item) => {
            return <option value={item.phonecode}>{item.name + " (" + item.phonecode + ")"}</option>
        });
        return (
            <AccountForm {...this.props} validationSchema={schema} initialValues={{}}>
                <div className="col-auto">
                    <label className="sr-only" ></label>
                    <Field component="select" name="phoneCode" className="form-control mb-2" required>
                        <option>Country Code</option>
                        {phoneCode}
                    </Field>
                </div>
                <div className="col-auto">
                    <label className="sr-only" >Last Name</label>
                    <div className="input-group mb-2">
                        <Field type="phoneNumber" name="phoneNumber" placeholder="Phone Number" className="form-control m-b-10" required />
                    </div>
                </div>

            </AccountForm>
        );
    }

    private getSchema() {
        return Yup.object().shape({
            phoneCode: Yup.string().required(),
            phoneNumber: Yup.number().required(),
        });
    }
}



