import { autobind } from "core-decorators";
import * as React from "react";
import { RouteComponentProps, Link } from "react-router-dom";
import { actionCreators as sessionActionCreators, ISessionProps } from "shared/stores/Session";
import { bindActionCreators } from "redux";
import { Grid, Row, Col, Button, Alert, Carousel } from "react-bootstrap";
import { IApplicationState } from "../stores";
import InnerBanner from "../../ClientApp/components/EventLearnPage/InnerBanner";
import EventTitle from "../../ClientApp/components/EventLearnPage/EventTitle";
import EventInfoAndMap from "../../ClientApp/components/EventLearnPage/EventInfoAndMap";
import EventDetails from "../../ClientApp/components/EventLearnPage/EventDetails";
import EventAmenities from "../../ClientApp/components/EventLearnPage/EventAmenities";
import NearByEvent from "../../ClientApp/components/EventLearnPage/NearByEvent";
import EventReviews from "../../ClientApp/components/EventLearnPage/EventReviews";
import SimilarListingEvents from "../../ClientApp/components/EventLearnPage/SimilarListingEvents";
import Checklist from "../../ClientApp/components/EventLearnPage/Checklist";
import InDepth from "../../ClientApp/components/EventLearnPage/InDepth";
import ReviewsRatingDataViewModel from "../models/ReviewsRating/ReviewsRatingViewModel";
import ReviewsRatingResponseViewModel from "../models/ReviewsRating/ReviewsRatingResponseViewModel";
import * as EventLearnPageStore from "../stores/EventLearnPage";
import { connect } from "react-redux";
import Metatag from "../components/Metatags/Metatag";
import KzLoader from "../components/Loader/KzLoader";
import "./EventLearnPage.scss";
import * as numeral from "numeral";
import { StickyContainer, Sticky } from 'react-sticky';

type EventLearnPageComponentProps =
    EventLearnPageStore.IEventLearnPageComponentProps
    & typeof EventLearnPageStore.actionCreators
    & ISessionProps
    & typeof sessionActionCreators
    & RouteComponentProps<{ eventId: string; }>;

class EventLearnPage extends React.Component<EventLearnPageComponentProps, any> {
    containerLine: HTMLDivElement;
    constructor(props) {
        super(props);
        this.state = {
            review: "",
            rating: -1,
            isCount: 1,
            isRedirectlogin: false,
            className: ""
        }
        this.hideBar = this.hideBar.bind(this);
    }

    private getEventId(props) {
        return props.eventId || props.match.params.eventId;
    }

    public componentDidMount() {
        if (window) {
            window.scrollTo(0, 0)
        }
        if (localStorage.getItem('isRedirectLogin') !== null) {
            this.setState({ isRedirectlogin: true });
            localStorage.removeItem('isRedirectLogin');
        }
        if (localStorage.getItem('rating') !== null && localStorage.getItem('review') !== null) {
            this.setState({ review: localStorage.getItem('review') });
            this.setState({ rating: localStorage.getItem('rating') });
            localStorage.removeItem("review");
            localStorage.removeItem("rating");
        }
        this.props.requestEventLearnPageData(this.getEventId(this.props));
        localStorage.removeItem("redirectPlaceAltId");
        if (localStorage.getItem('userToken') !== null && localStorage.getItem('userToken') !== "") {
            this.props.getUserOrdertData(localStorage.getItem('userToken'));
        }
        this.setState({ className: "" });
        window.addEventListener('scroll', this.hideBar);
    }

    hideBar() {
        if (window.pageYOffset >= 450 && window.pageXOffset === 0) {
            this.setState({ className: "learn-head-stiky" });
        }
        else {
            this.setState({ className: "" });
        }
    }

    public componentWillReceiveProps(newProps) {
        let newId = this.getEventId(newProps);
        let currId = this.getEventId(this.props);
        if (newId !== currId) {
            this.props.requestEventLearnPageData(newId);
        }
    }

    public getSocialVenue(Data) {
        var venues = Data.venue.name
        return venues + ", " + Data.city.name + ", " + Data.country.name;
    }

    public fbURL(Data) {
        return "https://www.facebook.com/dialog/feed?app_id=170375807148222&link=https://www.feelaplace.com/place/" + Data.event.altId.toUpperCase() + "&name=" + Data.event.name + "&caption=" + Data.event.name + "&description=" + new Date(Data.eventDetail.startDateTime).toDateString().split(' ').join(', ').toUpperCase() + " " + numeral(new Date(Data.eventDetail.startDateTime).getHours()).format('00') + ":" + numeral(new Date(Data.eventDetail.startDateTime).getMinutes()).format('00') + "<br />" + this.getSocialVenue(Data) + "&redirect_uri=https://www.feelaplace.com";
    }
    public fbMessengerURL(Data) {
        return "https://www.facebook.com/dialog/send?app_id=170375807148222&link=https://www.feelaplace.com/place/" + Data.event.altId.toUpperCase() + "&caption=" + Data.event.name + "&description=" + new Date(Data.eventDetail.startDateTime).toDateString().split(' ').join(', ').toUpperCase() + " " + numeral(new Date(Data.eventDetail.startDateTime).getHours()).format('00') + ":" + numeral(new Date(Data.eventDetail.startDateTime).getMinutes()).format('00') + "<br />" + this.getSocialVenue(Data) + "&redirect_uri=https://www.feelaplace.com";
    }

    public twitterURL(Data) {
        return "http://twitter.com/home?status=" + Data.event.name + ", " + new Date(Data.eventDetail.startDateTime).toDateString().split(' ').join(', ').toUpperCase() + " " + numeral(new Date(Data.eventDetail.startDateTime).getHours()).format('00') + ":" + numeral(new Date(Data.eventDetail.startDateTime).getMinutes()).format('00') + " (" + this.getSocialVenue(Data) + ") https://www.feelaplace.com/place/" + Data.event.altId.toUpperCase() + "";
    }

    public linkedinURL(Data) {
        return "https://www.linkedin.com/shareArticle?mini=true&summary=" + Data.event.name + " " + new Date(Data.eventDetail.startDateTime).toDateString().split(' ').join(', ').toUpperCase() + " " + numeral(new Date(Data.eventDetail.startDateTime).getHours()).format('00') + ":" + numeral(new Date(Data.eventDetail.startDateTime).getMinutes()).format('00') + " " + this.getSocialVenue(Data) + "&url=https://www.feelaplace.com/place/" + Data.event.altId.toUpperCase() + "&source=feelaplace&title=" + Data.event.name + "";
    }

    public goToLogin() {
        this.props.history.push("/login");
    }

    public goToBookNow() {
        this.props.history.push("/ticket-purchase-selection/" + this.props.eventLearnPage.eventDetails.event.altId);
    }

    public render() {
        if (this.props.eventLearnPage.fetchSuccess) {
            var isPurase = false, data;
            if (this.props.eventLearnPage.userOrders != undefined) {
                data = this.props.eventLearnPage.userOrders.event.filter(function (item) { return item.altId === this.props.match.params.eventId });
                if (data.length > 0) {
                    isPurase = true;
                    if (this.state.review !== "" && this.state.rating !== -1 && this.state.isCount === 1 && this.state.isRedirectlogin === true) {
                        this.onSubmitSignIn(this.state.review, parseInt(this.state.rating));
                    }
                }
            }
            var Data = this.props.eventLearnPage.eventDetails;
            var minPrice = Data.eventTicketAttribute.length == 1 ? Data.eventTicketAttribute[0].price : Math.min.apply(null, Data.eventTicketAttribute.map((item) => { return item.price }).filter(Boolean));
            var maxPrice = Math.max(...Data.eventTicketAttribute.map((item) => { return item.price }));
            var historyData = Data.eventLearnMoreAttributes.filter(function (val) {
                return val.learnMoreFeatureId == "1";
            });

            var highlightsData = Data.eventLearnMoreAttributes.filter(function (val) {
                return val.learnMoreFeatureId == "2";
            });
            var timelineData = Data.eventLearnMoreAttributes.filter(function (val) {
                return val.learnMoreFeatureId == "3";
            });
            var immersiveData = Data.eventLearnMoreAttributes.filter(function (val) {
                return val.learnMoreFeatureId == "4";
            });
            var architectureData = Data.eventLearnMoreAttributes.filter(function (val) {
                return val.learnMoreFeatureId == "5";
            });
            var mapPlanData = Data.eventLearnMoreAttributes.filter(function (val) {
                return val.learnMoreFeatureId == "6";
            });

            return <div>
                <StickyContainer>
                    <Metatag url={this.props.location.pathname} metaContent={Data} title={Data.event.name} />
                    <InnerBanner eventCategoryName={Data.eventCategoryName} eventCat={Data.eventCategory} eventName={Data.eventDetail.name} eventImageId={Data.event.altId} eventImageList={Data.eventGalleryImage} />
                    <div className="pt-2 pb-2 container page-content learn-more-page">
                        <Sticky topOffset={400}>
                            {({
                                style,
                                isSticky
                            }) => {
                                return (
                                    <header className={this.state.className}>
                                        <EventTitle goToLoginPage={this.goToLogin.bind(this)} isAthenticated={this.props.session.isAuthenticated} currencyCode={Data.currencyType.code} minPrice={minPrice} maxPrice={maxPrice} event={Data.eventDetail} reviews={Data.rating} eventId={this.props.match.params.eventId} fbLink={this.fbURL(Data)} fbMessengerLink={this.fbMessengerURL(Data)} twitterLink={this.twitterURL(Data)} linkedinLink={this.linkedinURL(Data)} />
                                    </header>
                                )
                            }
                            }
                        </Sticky>
                        <hr />
                        <EventInfoAndMap eventInfo={Data.event} city={Data.city.name} country={Data.country.name} />
                        <hr />
                        <EventDetails phoneCode={Data.country.phonecode} clientPointOfContact={Data.clientPointOfContact} ticketCategory={Data.ticketCategory} eventTicketAttribute={Data.eventTicketAttribute} eventDetails={Data.eventDetail} cityName={Data.city.name} stateName={Data.state.name} countryName={Data.country.name} venueName={Data.venue.name} eventTicketDetail={Data.eventTicketDetail} />
                        <div className="learn-contect-detail row pt-3">
                            {Data.eventLearnMoreAttributes.length > 0 && < InDepth historyData={historyData} highlightsData={highlightsData} timelineData={timelineData} architectureData={architectureData} mapPlanData={mapPlanData} immersiveData={immersiveData} />}
                            <Checklist cityName={Data.city.name} stateName={Data.state.name} countryName={Data.country.name} venueName={Data.venue.name} eventDetails={Data.eventDetail} />
                        </div>
                        <hr />
                        <EventAmenities amenities={Data.eventAmenitiesList} />
                        <hr />
                        <NearByEvent eventInfo={Data.event} city={Data.city.name} country={Data.country.name} />
                        <hr />
                        <EventReviews UserImageMap={Data.userImageMap} minReview={0} maxReview={5} reviews={Data.rating} eventInfo={Data.event} goToBookNowPage={this.goToBookNow.bind(this)} reDirectUrl={this.props.location.pathname.split('/')[2].toUpperCase()} goToLoginPage={this.goToLogin.bind(this)} eventname={Data.event.name} user={Data.user} isPurase={isPurase} isAthenticated={this.props.session.isAuthenticated} onSubmit={this.onSubmitSignIn.bind(this)} />
                        <div className="nav-tab-content row pb-4">
                            <div className="col-sm-12">
                                <h5>Similar listings</h5>
                                <SimilarListingEvents isAthenticated={this.props.session.isAuthenticated} goToLoginPage={this.goToLogin.bind(this)} similarListings={Data.categories} />
                            </div>
                        </div>
                    </div>
                </StickyContainer>
            </div>
        } else {
            return (
                <div>
                    <KzLoader />
                </div>
            )
        }
    }

    private onSubmitSignIn(review, rating) {
        if (this.props.session.isAuthenticated) {
            this.setState({ isCount: 2 });
            var eventAltId = this.props.location.pathname.split('/')[2].toUpperCase();
            var reviewRating = new ReviewsRatingDataViewModel();
            reviewRating.UserAltId = localStorage.getItem('userToken');
            reviewRating.EventAltId = eventAltId;
            reviewRating.Points = rating;
            reviewRating.Comment = review;
            reviewRating.isEnabled = true;
            this.props.userRatingAndReviews(reviewRating, (response: ReviewsRatingResponseViewModel) => {
                if (response.success) {
                    this.props.requestEventLearnPageData(this.getEventId(this.props));
                }
            });
        } else {
            localStorage.setItem("isRedirectLogin", "true");
            localStorage.setItem("review", review);
            localStorage.setItem("rating", rating);
            localStorage.setItem("redirectPlaceAltId", this.props.location.pathname.split('/')[2].toUpperCase());
            this.props.history.push("/login");
        }
    }
}
export default connect(
    (state: IApplicationState) => ({ session: state.session, eventLearnPage: state.eventLearnPage }),
    (dispatch) => bindActionCreators({ ...sessionActionCreators, ...EventLearnPageStore.actionCreators }, dispatch)
)(EventLearnPage);



