import * as React from "react";
import { Link, RouteComponentProps } from "react-router-dom";
import { Grid, Row, Col, Button, Alert } from "react-bootstrap";
import * as numeral from "numeral";

export default class DeliveryPickupOptions extends React.Component<any, any> {
    public constructor() {
        super();
        this.state = { cartData: "" };
    }

    public componentDidMount() {
        if (localStorage.getItem('cartItems') != null && localStorage.getItem('cartItems') != '0') {
            var data = JSON.parse(localStorage.getItem("cartItems"));
            let that = this;
            var unique = Array.from(new Set(data.map(item => item.altId)));        
            unique.forEach(function (val) {                
                that.setState({ [val.toString()]: "0" });
            });
            this.setState({ cartData: data });
        }
    }

    public continueToPickupDetails() {
        let data = this.state.cartData;
        let that = this;
        var unique = Array.from(new Set(data.map(item => item.altId)));        
        let choosed = unique.filter(function (val) {
            return that.state[val.toString()] == "2";
        });
        if (choosed.length == unique.length) {
            this.props.switchPage("pickupDetails");
        } else {
            alert("Please select delivery option for each item in your shopping bag");
        }
    }

    public setPickupOption(item, value, e) {
        this.setState({
            [item]: value
        });
    }

    public render() {
        var data = this.state.cartData;
        var that = this;
        if (data != "") {
            let i = 1;
            data.sort(function (a, b) {
                var keyA = new Date(a.eventStartDate),
                    keyB = new Date(b.eventStartDate);
                if (keyA < keyB) return -1;
                if (keyA > keyB) return 1;
                return 0;
            });
            var unique = Array.from(new Set(data.map(item => item.altId)));
            var deliveryPickupData = unique.map(function (altId) {
                var categories = data.filter(function (cat) {
                    return cat.altId == altId;
                });
                var categoryCost = 0;
                var categoryData = categories.map(function (val) {
                    categoryCost += (val.quantity * val.pricePerTicket);
                    var symbol;
                    if (val.currencyName == "INR") {
                        symbol = "";
                    }
                    else if (val.currencyName == "GBP") {
                        symbol = "";
                    }
                    else if (val.currencyName == "USD") {
                        symbol = "$";
                    }
                    else if (val.currencyName == "EURO") {
                        symbol = "";
                    }
                    else {
                        symbol = "";
                    }
                    return <p className="m-0"><small>{val.ticketCategoryName + " (" + val.quantity + " x " + symbol + numeral(val.pricePerTicket).format('00.00') + ")"} <span className="float-sm-right">{symbol + numeral((val.quantity * val.pricePerTicket)).format('00.00')}</span></small></p>;
                });
                var eventInformation = categories[0];
                var symbol;
                if (eventInformation.currencyName == "INR") {
                    symbol = "";
                }
                else if (eventInformation.currencyName == "GBP") {
                    symbol = "";
                }
                else if (eventInformation.currencyNamee == "USD") {
                    symbol = "$";
                }
                else if (eventInformation.currencyName == "EURO") {
                    symbol = "";
                }
                else {
                    symbol = "";
                }
                return <div>
                    <div className="media">
                        <img className="mr-3 d-none d-sm-block" src={`https://static1.feelaplace.com/images/event/HotTickets/` + eventInformation.altId.toUpperCase() + "-ht-c1.jpg"} alt="Generic placeholder image" width="140" />
                        <div className="media-body">
                            <p className="mb-2">{eventInformation.name}
                                <span className="pink-color pl-2 mr-2"><i className="fa fa-calendar mr-1"></i>{new Date(eventInformation.eventStartDate).toDateString().split(' ').join(', ').toUpperCase() + " | " + numeral(new Date(eventInformation.eventStartDate).getHours()).format('00') + ":" + numeral(new Date(eventInformation.eventStartDate).getMinutes()).format('00')}</span>
                                <a role="button" data-toggle="collapse" href={"#collapseExample" + eventInformation.altId.toUpperCase()} aria-expanded="false" aria-controls="collapseExample"><small className="blue-txt">Show Details</small></a> <span className="float-sm-right">{symbol + numeral(categoryCost).format('00.00')}</span></p>
                                <div className="gradient-bg rounded p-2 border">
                                <div className="custom-control custom-radio">
                                    <input type="radio" id={"optradio" + eventInformation.altId + "1"} name="customRadio" className="custom-control-input" disabled />
                                    <label className="custom-control-label" htmlFor={"optradio" + eventInformation.altId + "1"}> <small>M -Ticket</small> </label>
                                    </div>
                                <div className="custom-control custom-radio">
                                    <input type="radio" id={"optradio" + eventInformation.altId + "2"} onClick={that.setPickupOption.bind(that, eventInformation.altId, "2")} name={"optradio" + eventInformation.altId} className="custom-control-input"/>
                                    <label className="custom-control-label" htmlFor={"optradio" + eventInformation.altId + "2"}> <small>Ticket Counter</small> </label>
                                    </div>
                            </div>
                                <div id={"collapseExample" + eventInformation.altId.toUpperCase()} className="collapse delivery-more-detail">
                                <div id={"show-details" + eventInformation.eventTicketAttributeId} className="text-muted pt-3 pb-3">
                                    <p className="m-0"><small><i className="fa fa-map-marker pink-color"></i> {eventInformation.venue} -  <a href="#"><i className="fa fa-map-marker mr-1"></i><Link to={"https://www.google.com/maps/search/" + eventInformation.venue} target="_blank">View on Map</Link></a> </small></p>
                                    {categoryData}
                                </div>
                            </div>
                        </div>
                    </div>
                    {altId != unique[unique.length - 1] && < hr />}
                </div>
            });
            return <div>
                <div className="card-body">
                    {deliveryPickupData}
                </div>
                {this.props.showButtons && <div className="gradient-bg border p-2 text-right">
                    <Link to="/itinerary" className="btn btn-primary float-left text-uppercase">Back to Bag</Link>
                    <a href="javascript:void(0);" onClick={this.continueToPickupDetails.bind(this)} className="btn site-primery-btn text-uppercase">Continue</a>
                </div>}
            </div>;
        } else {
            return <div>
            </div>;
        }
    }
}



