import * as React from "react";
import { Link, RouteComponentProps } from "react-router-dom";
import { UserCheckoutLoginForm } from "../CheckOut/UserCheckoutLoginForm";

export class UserCheckoutLoginComponent extends React.Component<any, any> {
    public render() {
        return <div>
            <h6>
                Returning Customer
            </h6>
            <UserCheckoutLoginForm onSubmit={this.props.onSubmit} onClick={this.props.switchPage} />
            {this.props.invalidCredentials && <span className="validate-txt"><br /><small>Invalid email address or password</small></span>}
        </div>;
    }
}


