import * as React from "react";
import { Link, RouteComponentProps } from "react-router-dom";
import { actionCreators as sessionActionCreators, ISessionProps } from "shared/stores/Session";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { IApplicationState } from "../stores";
import Metatag from "../components/Metatags/Metatag";
import ItineraryComponent from "../components/ReviewItinerary/ItineraryComponent";
import BespokeItinerary from "../components/ReviewItinerary/BespokeItinerary";
import KzLoader from "../components/Loader/KzLoader";
import "../scss/_buttons.scss";
import "../scss/_custom.scss";

type sessionProps =  ISessionProps & typeof sessionActionCreators & RouteComponentProps<{}>;

export class Itinerary extends React.Component<sessionProps, any> {
    public constructor() {
        super();
        this.state = { enableCheckout: false, loaded: false };
    }

    public componentWillMount() {
        this.setState({ enableCheckout: false, loaded: false });
    }

    public componentDidMount() {
        if (window) {
            window.scrollTo(0, 0)
        }

        if (localStorage.getItem('cartItems') != null && localStorage.getItem('cartItems') != '0' && localStorage.getItem('cartItems') != "[]") {
            this.setState({ enableCheckout: true, loaded: true });
        } else {
            this.setState({ loaded: true });
        }
    }

    public disableCheckout() {
        this.setState({ enableCheckout: false });
    }

    public render() {
        if (this.state.loaded) {
            return <div>
                <Metatag url={this.props.location.pathname} title="Review your itineraries" />
                <div className="container forgot-password pt-3 pb-5 inner-banner">
                    <div className="card">
                        <div className="card-header h5 bg-white">
                            Your Bespoke Itinerary
				        <small className="float-right text-muted">
                                <i className="fa fa-lock text-success"></i> Secure</small>
                        </div>
                        <ItineraryComponent disableCheckout={this.disableCheckout.bind(this)} />

                       
                        {!this.state.enableCheckout && <div className="card-footer gradient-bg text-right">
                            <Link to="/" className="btn btn-outline-primary text-uppercase">Go to Home</Link>
                        </div>
                        }
                    </div>
                    <div className="nav-tab-content">
                        {(this.props.session.isAuthenticated) &&  <BespokeItinerary />}
                    </div>
                </div>
            </div>;
        } else {
            return (
                <div>
                    <KzLoader />
                </div>
            )
        }

    }
}
export default connect(
    (state: IApplicationState) => ({ session: state.session}),
    (dispatch) => bindActionCreators({ ...sessionActionCreators }, dispatch)
)(Itinerary);



