import { Field } from "formik";
import * as React from "react";
import AccountAddressForm from "./AccountAddressForm";
import { SaveCardFormDataViewModel } from "../../../models/Account/SaveCardFormDataViewModel";
import Yup from "yup";

interface ISaveCardFormProps {
    onSubmit: (values: SaveCardFormDataViewModel) => void;
    onCancel: () => void;
}

export class SaveCardForm extends React.Component<ISaveCardFormProps, {}> {
    public render() {
        const schema = this.getSchema();
        return (
            <AccountAddressForm {...this.props} validationSchema={schema} initialValues={{}}>
                  <h5 className="mt-3">New Card</h5>
                    <div className="form-group">
                    <Field type="cardNumber" name="cardNumber" placeholder="Card Number" className="form-control" required />
                    </div>
                   <div className="form-group">
                    <Field type="nameOnCard" name="nameOnCard" placeholder="Name on Card" className="form-control" required />
                     </div>
                    <div className="form-group">
                        <div className="row">
                        <div className="col-sm-6">
                            <Field type="expiryMonth" name="expiryMonth" component="select" className="form-control">
                                <option>Month</option>
                                <option value="01">01</option>
                                <option value="02">02</option>
                                <option value="03">03</option>
                                <option value="04">04</option>
                                <option value="05">05</option>
                                <option value="06">06</option>
                                <option value="07">07</option>
                                <option value="08">08</option>
                                <option value="09">09</option>
                                <option value="10">10</option>
                                <option value="11">11</option>
                                <option value="12">12</option>
                            </Field>
                        </div>

                        <div className="col-sm-6">
                            <Field type="expiryYear" name="expiryYear" component="select" className="form-control " id="ddlexpiryYear">
                                <option value="2018">2018</option>
                                <option value="2019">2019</option>
                                <option value="2020">2020</option>
                            </Field>
                        </div>
                        </div>
                    </div>
                    <div className="form-group">
                    <Field type="description" name="description" placeholder="Card label (optional) - Eg: Brother's Card" className="form-control " />      
                        </div>
            </AccountAddressForm>
        );
    }

    private getSchema() {
        return Yup.object().shape({
            cardNumber: Yup.number().required(),
            nameOnCard: Yup.string().required(),
        });
    }
}



